  upsertOnDialog: true,
  showUserFilter: false,
  showDeleteButton: true,
  searchEndpoint: DashEndpoint.ORGANIZATIONS.ORGANIZATION_ENDPOINT,
  delEndpoint: DashEndpoint.ORGANIZATIONS.ORGANIZATION_ENDPOINT
