import {Component, Input, OnChanges, SimpleChanges, ViewChild} from '@angular/core';
import {
  ChartComponent,
  ApexAxisChartSeries,
  ApexChart,
  ApexXAxis,
  ApexDataLabels,
  ApexStroke,
  ApexMarkers,
  ApexYAxis,
  ApexGrid,
  ApexTitleSubtitle,
  ApexLegend
} from "ng-apexcharts";
import {ILineChartData} from "./line-chart.model";
import {left} from "@popperjs/core";

export type ChartOptions = {
  series: ApexAxisChartSeries;
  chart: ApexChart;
  xaxis: ApexXAxis;
  stroke: ApexStroke;
  dataLabels: ApexDataLabels;
  markers: ApexMarkers;
  colors: string[];
  yaxis: ApexYAxis;
  grid: ApexGrid;
  legend: ApexLegend;
  title: ApexTitleSubtitle;
};

@Component({
  selector: 'app-linechart',
  templateUrl: './linechart.component.html',
  styleUrls: ['./linechart.component.scss']
})
export class LineChartComponent implements OnChanges {
  @ViewChild("chart") chart: ChartComponent;
  public chartOptions: Partial<ChartOptions>;

  @Input() chartData: ILineChartData = {
    titles: {top: '', xAxes: '', yAxes: ''},
    series: []
  }

  constructor() {
    // this.chartOptions = convert(this.chartData);
  }

  ngOnChanges(changes: SimpleChanges): void {
    this.loadLineChart()
  }

  private loadLineChart() {
    this.chartOptions = convert(this.chartData);
  }

}


function convert(data: ILineChartData): Partial<ChartOptions> {
  let series: ApexAxisChartSeries = data.series.map(series => {
    return {
      name: series.seriesName,
      data: series.data.map(item => item.value)
    }
  })

  let min = Math.min(...series.map(item => Math.min(...item.data as number[])))
  let max = Math.max(...series.map(item => Math.max(...item.data as number[])))

  console.log(series)

  let attributes: string[] = data.series.length > 0 ? data.series[0].data.map(item => item.attribute) : []

  return {
    series: series,
    chart: {
      height: 350,
      type: "line",
      dropShadow: {
        enabled: true,
        color: "#000",
        top: 18,
        left: 7,
        blur: 10,
        opacity: 0.2
      },
      toolbar: {
        show: false
      }
    },
    colors: data.series.map(item => item.color),
    dataLabels: {
      enabled: true
    },
    stroke: {
      curve: "smooth"
    },
    title: {
      text: data.titles.top,
      align: "left"
    },
    grid: {
      borderColor: "#e7e7e7",
      row: {
        colors: ["#f3f3f3", "transparent"], // takes an array which will be repeated on columns
        opacity: 0.5
      }
    },
    markers: {
      size: 1
    },
    xaxis: {
      categories: attributes,
      title: {
        text: data.titles.xAxes
      }
    },
    yaxis: {
      title: {
        text: data.titles.yAxes
      },
      min: min,
      max: max
    },
    legend: {
      position: "top",
      horizontalAlign: "right",
      floating: true,
      offsetY: -25,
      offsetX: -5
    }
  }
}
