import {Component, Inject} from "@angular/core";
import {BaseUpsertComponentV2} from "../../../../../../../base/components/base-upsert-v2.component";
import {AddressConfig, defaultAddressConfig, selfRoutes} from "../../models/config.model";
import {FormControl, FormGroup, Validators} from "@angular/forms";
import {EventBusService} from "../../../../../../../services/eventbus/event-bus.service";
import {ActivatedRoute, Router} from "@angular/router";
import {SubscriptionContainerService} from "../../../../../../../base/services/subscription-container.service";
import {AddressService} from "../../services/address.service";
import {NotifierService} from "../../../../../../../services/notifier/notifier.service";
import {BreadcrumbItem} from "../../../../../../common/modules/generic/breadcrumb/breadcrumb.model";
import {EventKey} from "../../../../../../../services/eventbus/event.key";
import {HttpErrorResponse} from "@angular/common/http";
import {IImageSlice} from "../../../../../../common/modules/cms/files/models/file.model";
import {BaseConfig} from "../../../../../../../base/models/baseConfig";
import {MAT_DIALOG_DATA} from "@angular/material/dialog";
import {IAddress} from "../../models/address.model";
import {ILocationType} from "../../../locationtypes/models/locationtype.model";
import {ILocation} from "../../../locations/models/location.model";
import {IParams} from "../../../../../../../base/endpoints/iparams";
import {MetaData} from "ng-event-bus/lib/meta-data";
import {LocationService} from "../../../locations/services/location.service";
import {LocationTypeService} from "../../../locationtypes/services/locationtype.service";


@Component({
  selector: 'app-address-upsert',
  templateUrl: './address-upsert.component.html',
  styleUrls: ['./address-upsert.component.scss']
})
export class AddressUpsertComponent extends BaseUpsertComponentV2<IAddress> {

  config: AddressConfig = defaultAddressConfig

  addressForm = new FormGroup({
    title: new FormControl<string | null>(null, [Validators.required, Validators.minLength(3)]),
    address_line_one: new FormControl<string | null>(null, [Validators.required]),
    address_line_two: new FormControl<string | null>(null, []),
    altitude: new FormControl<number>(0, [Validators.required]),
    latitude: new FormControl<number>(0, [Validators.required]),
    longitude: new FormControl<number>(0, [Validators.required]),
    zip_code: new FormControl<string | null>(null, []),
    location_id: new FormControl<number | null>(null, []),
  })

  locations: {
    type: ILocationType,
    locations: ILocation[],
    selectedLocation: ILocation | null
  }[] = []

  constructor(eventBus: EventBusService,
              router: Router,
              subContainer: SubscriptionContainerService,
              route: ActivatedRoute,
              private addressService: AddressService,
              private locationService: LocationService,
              private locationTypeService: LocationTypeService,
              private notifier: NotifierService,
              @Inject(MAT_DIALOG_DATA) dialogData: any) {
    super(eventBus, router, subContainer, route, dialogData)

    if (dialogData.title != null) {
      this.addressForm.controls.title.setValue(dialogData.title)
    }

  }

  protected override componentName(): string {
    return "AddressUpsertComponent"
  }

  protected override getBreadcrumbItems(): BreadcrumbItem[] {
    return [
      {
         text: this.translations["Dashboard"],
        link: null,
        active: false
      },
      {
        text: "Addresss",
        link: "/dash/addresss",
        active: false
      },
      {
        text: "Upsert",
        link: null,
        active: true
      }
    ]
  }

  override ngOnInit() {
    super.ngOnInit();

    this.fetchLocationTypes({page: 0, size: 100, q: null})
  }

  override initialize(exId: number): void {
    this.loading = true
    this.exId = exId
  }

  fetchExAddress(exId: number) {
    this.loading = true
    let key = this.componentKey(EventKey.ADDRESSS.GET(exId))
    this.addressService.fetchSingle(key, exId)
    this.subscribe(key, null, (value, meta) => {
      this.loading = false;
      this.populateForm(meta.data)
    })
    this.subscribe(this.getErrorKey(key), null, (value, meta) => {
      let error: HttpErrorResponse = meta.data
      console.log(error)
      this.loading = false;
    })
  }

  onSubmit() {
    if (!this.addressForm.valid) {
      return
    }
    this.upsert(this.exId)
  }

  getConfig(): BaseConfig {
    return this.config;
  }

  getListPageRoute(): string {
    return selfRoutes.list;
  }

  upsert(exId: number | null) {
    this.loading = true
    let key = this.componentKey(EventKey.ADDRESSS.UPSERT(exId))
    // @ts-ignore
    this.addressService.upsert(key, this.addressForm.value, exId)
    this.subscribe(key, null, (value, meta) => {
      this.loading = false;
      let message = "Address has been " + (exId ? "updated" : "created")
      this.notifier.success("Success", message)
      this.addressForm.reset();

      if (this.config.upsertOnDialog) {
        this.onCompleted.emit(meta.data)
      } else {
        this.navigateToListPage()
      }
    })
    this.subscribe(this.getErrorKey(key), null, (value, meta) => {
      let error: HttpErrorResponse = meta.data
      this.loading = false;
      this.notifier.warning('Failed!', error.error.message)
    })
  }

  private populateForm(n: IAddress) {
    this.addressForm.controls.address_line_one.setValue(n.address_line_one)
    this.addressForm.controls.address_line_two.setValue(n.address_line_two)
    this.addressForm.controls.altitude.setValue(n.altitude)
    this.addressForm.controls.latitude.setValue(n.latitude)
    this.addressForm.controls.longitude.setValue(n.longitude)
    this.addressForm.controls.title.setValue(n.title)
    this.addressForm.controls.zip_code.setValue(n.zip_code)
    this.addressForm.controls.location_id.setValue(n.location_id)

    /*
    Set the selected locations to the dynamic location form fields
     */
    new Map<string, ILocation>(Object.entries(n.locations_tree))
      .forEach((location: ILocation, typeCode: string) => {
        let type = this.locations.find(l => l.type.code == typeCode)?.type
        if (type != null) {
          this.onLocationSelected(location, type)
        }
      })
  }

  onLocationSelected(location: ILocation | null, type: ILocationType) {

    /*
     *  Set the selected location
     *  Unset all selected locations for other types where type.level > selectedType.level
     */
    this.locations = this.locations.map(l => {
      if (l.type.id == type.id) {
        return {
          ...l,
          selectedLocation: location
        }
      } else {
        if (l.type.level > type.level) {
          return {
            ...l,
            selectedLocation: null
          }
        }
        return l
      }
    })

    console.log('selected location', this.locations)

    /*
    Set the location id in the form where level is the highest
     */
    let sortedAndFiltered = this.locations.filter(l => l.selectedLocation != null)
      .sort((a, b) => {
        return b.type.level - a.type.level
      })

    console.log('sorted and filtered', sortedAndFiltered)

    if (sortedAndFiltered.length > 0) {
      let selectedLocation = sortedAndFiltered[0].selectedLocation
      this.addressForm.controls.location_id.setValue(
        selectedLocation == null ? null : selectedLocation.id
      )
    }

    console.log('selected location in control', this.addressForm.controls.location_id.value)

    if (location != null) {
      this.fetchChildLocations({page: 0, size: 100, q: null, parent_id: location.id}, locations => {
        if (locations.length == 0) {
          return
        }
        let childLocationTypeId = locations[0].type_id
        this.locations = this.locations.map(l => {
          if (l.type.id == childLocationTypeId) {
            return {
              ...l,
              locations: locations
            }
          } else {
            return l
          }
        })
      })
    }
  }

  private fetchLocationTypes(params: IParams) {
    this.loading = true
    const key = this.componentKey(EventKey.LOCATIONTYPES.SEARCH(params))
    this.locationTypeService.search(key, this.config.locationTypeEndpoint, params)
    this.subscribe(
      key,
      null,
      (value: Boolean, meta: MetaData) => {
        let locationTypes = meta.data.content as ILocationType[]
        locationTypes.sort((a, b) => {
          return a.level - b.level
        })
        this.locations = locationTypes.map(type => {
          return {
            type: type,
            locations: [],
            selectedLocation: null
          }
        });
        this.loading = false

        // Now fetch ex address since location types are resolved
        if (this.exId != null) {
          this.fetchExAddress(this.exId)
        }
      })
    this.subscribe(this.getErrorKey(key), null, (value, meta) => {
      this.notifier.error('Error', meta.data.error.message)
    })
  }

  private fetchChildLocations(params: IParams, onLoaded: (locations: ILocation[]) => void) {
    this.loading = true
    const key = this.componentKey(EventKey.LOCATIONS.SEARCH(params))
    this.locationService.searchChildren(key, this.config.locationEndpoint, params)
    this.subscribe(
      key,
      null,
      (value: Boolean, meta: MetaData) => {
        let locations = meta.data.content as ILocation[]
        onLoaded(locations)
        this.loading = false
      })
    this.subscribe(this.getErrorKey(key), null, (value, meta) => {
      this.notifier.error('Error', meta.data.error.message)
    })
  }

}
