import {Component, Inject} from '@angular/core';
import {FormControl, FormGroup, Validators} from "@angular/forms";
import {EventBusService} from "../../../../../../services/eventbus/event-bus.service";
import {ActivatedRoute, Router} from "@angular/router";
import {SubscriptionContainerService} from "../../../../../../base/services/subscription-container.service";
import {NotifierService} from "../../../../../../services/notifier/notifier.service";
import {BreadcrumbItem} from "../../../../../common/modules/generic/breadcrumb/breadcrumb.model";
import {EventKey} from "../../../../../../services/eventbus/event.key";
import {HttpErrorResponse} from "@angular/common/http";
import {MAT_DIALOG_DATA} from "@angular/material/dialog";
import {BaseUpsertComponent} from "../../../../../../base/components/base-upsert.component";
import {BaseConfig} from "../../../../../../base/models/baseConfig";
import {defaultFeedbackActionConfig, FeedbackActionConfig} from "../../models/config.model";
import {FeedbackService} from "../../services/feedback.service";
import {statuses} from "../../models/feedback.model";


@Component({
  selector: 'app-feedbackaction-upsert',
  templateUrl: './feedbackaction-upsert.component.html',
  styleUrls: ['./feedbackaction-upsert.component.scss']
})
export class FeedbackActionUpsertComponent extends BaseUpsertComponent {
  protected readonly statuses = statuses;
  config: FeedbackActionConfig = defaultFeedbackActionConfig

  feedbackActionForm = new FormGroup({
    status: new FormControl('', [Validators.required]),
    comment: new FormControl('', [Validators.required]),
  })

  feedbackId: number = 0

  constructor(eventBus: EventBusService,
              router: Router,
              subContainer: SubscriptionContainerService,
              route: ActivatedRoute,
              private feedbackService: FeedbackService,
              private notifier: NotifierService,
              @Inject(MAT_DIALOG_DATA) dialogData: any) {
    super(eventBus, router, subContainer, route, dialogData)
    this.feedbackId = dialogData.feedbackId
  }

  protected override componentName(): string {
    return "FeedbackActionUpsertComponent"
  }

  protected override getBreadcrumbItems(): BreadcrumbItem[] {
    return [
      {
         text: this.translations["Dashboard"],
        link: null,
        active: false
      },
      {
        text: this.translations["Feedback Actions"],
        link: "/dash/feedbackactions",
        active: false
      },
      {
        text: this.translations["Upsert"],
        link: null,
        active: true
      }
    ]
  }

  override initialize(exId: number): void {

  }

  onSubmit() {
    if (!this.feedbackActionForm.valid) {
      return
    }
    this.takeAction(this.feedbackId)
  }

  getConfig(): BaseConfig {
    return this.config;
  }

  getListPageRoute(): string {
    return "";
  }

  takeAction(feedbackId: number) {
    this.loading = true
    let key = this.componentKey(EventKey.FEEDBACKS.TAKE_ACTION(feedbackId))
    // @ts-ignore
    this.feedbackService.takeAction(key, feedbackId, this.feedbackActionForm.value)
    this.subscribe(key, null, (value, meta) => {
      this.loading = false;
      let message = "Action has been taken successfully!"
      this.notifier.success("Success", message)
      this.feedbackActionForm.reset();

      this.onSuccess.emit()
    })
    this.subscribe(this.getErrorKey(key), null, (value, meta) => {
      let error: HttpErrorResponse = meta.data
      this.loading = false;
      this.notifier.warning('Failed!', error.error.message)
    })
  }

}
