import {BaseConfig} from "../../../../../../base/models/baseConfig";
import {Layout} from "../../../../../common/modules/generic/breadcrumb/breadcrumb.model";
import {DashEndpoint} from "../../../../dash.endpoint";


export interface LocationConfig extends BaseConfig {
	getByTypeEndpoint: string
}

export let defaultLocationConfig: LocationConfig = {
	defaultLayout: Layout.COLUMN,
	upsertOnDialog: false,
	showUserFilter: false,
	showDeleteButton: true,
	searchEndpoint: DashEndpoint.LOCATIONS.LOCATION_ENDPOINT,
	delEndpoint: DashEndpoint.LOCATIONS.LOCATION_ENDPOINT,
	getByTypeEndpoint: DashEndpoint.LOCATIONTYPES.LOCATIONTYPE_ENDPOINT
}

export let selfEndpoints = {
	endpoint: DashEndpoint.LOCATIONS.LOCATION_ENDPOINT,
	adminEndpoint: DashEndpoint.LOCATIONS.LOCATION_ENDPOINT_ADMIN,
}

export let selfRoutes = {
	list: "/dash/locations",
	getChildren(id: number) {
		return "/dash/locations?parent_id=" + id
	},
	create: "/dash/locations/create",
	update(id: number | null) {
		return id == null ? this.create : "/dash/locations/" + id + '/update'
	}
}
