import {Component, EventEmitter, Inject, Input, Output} from '@angular/core';
import {BreadcrumbItem} from "../../../../../common/modules/generic/breadcrumb/breadcrumb.model";
import {EventBusService} from "../../../../../../services/eventbus/event-bus.service";
import {Router} from "@angular/router";
import {SubscriptionContainerService} from "../../../../../../base/services/subscription-container.service";
import {NotifierService} from "../../../../../../services/notifier/notifier.service";
import {MAT_DIALOG_DATA, MatDialog} from "@angular/material/dialog";
import {FeedbackActionUpsertComponent} from "../feedbackaction-upsert/feedbackaction-upsert.component";
import {BaseListComponent} from "../../../../../../base/components/base-list.component";
import {ComponentType} from "@angular/cdk/overlay";
import {BaseUpsertComponent} from "../../../../../../base/components/base-upsert.component";
import {BaseConfig} from 'src/app/base/models/baseConfig';
import {IFeedback} from "../../models/feedback.model";
import {defaultFeedbackActionConfig, FeedbackActionConfig} from "../../models/config.model";
import {IParams} from "../../../../../../base/endpoints/iparams";

@Component({
  selector: 'app-feedbackaction-list',
  templateUrl: './feedbackaction-list.component.html',
  styleUrls: ['./feedbackaction-list.component.scss']
})
export class FeedbackActionListComponent extends BaseListComponent {

  config: FeedbackActionConfig = defaultFeedbackActionConfig

  @Input() feedbackId: number = 0
  @Input() feedback: IFeedback

  @Output() onCanceled: EventEmitter<void> = new EventEmitter<void>()
  @Output() onSuccess: EventEmitter<void> = new EventEmitter<void>()

  protected componentName(): string {
    return "FeedbackActionListComponent";
  }

  constructor(eventBus: EventBusService,
              router: Router,
              subContainer: SubscriptionContainerService,
              dialog: MatDialog,
              private notifier: NotifierService,
              @Inject(MAT_DIALOG_DATA) private dialogData: any,
              private matDialog: MatDialog
  ) {
    super(eventBus, router, subContainer, dialog)
  }

  initialize(): void {
    this.feedback = this.dialogData.feedback
    this.loading = false
  }

  protected override getBreadcrumbItems(): BreadcrumbItem[] {
    return []
  }

  openUpsert(feedbackId: number) {
    const dialogRef = this.matDialog.open(FeedbackActionUpsertComponent, {
      disableClose: true,
      data: {
        feedbackId: feedbackId
      },
    })
    let component = dialogRef.componentInstance
    component.onCanceled.subscribe(() => {
      dialogRef.close()
      this.onCanceled.emit()
    })
    component.onSuccess.subscribe(() => {
      this.onSuccess.emit()
      dialogRef.close()
    })
  }

  onDeleted(deleted: Boolean) {
    if (deleted) {
      this.notifier.success("Success", "FeedbackAction is deleted")
      this.ngOnInit()
    }
  }

  override getConfig(): BaseConfig {
    return this.config
  }

 override getRoutes(): { create: string, update: (id: number) => string, params: IParams } | null {
    return null
  }

  getUpsertComponent(): ComponentType<BaseUpsertComponent> {
    return FeedbackActionUpsertComponent
  }

}
