import {BaseConfig} from "../../../../../../base/models/baseConfig";
import {Layout} from "../../../../../common/modules/generic/breadcrumb/breadcrumb.model";
import {DashEndpoint} from "../../../../dash.endpoint";


export interface AddressConfig extends BaseConfig {
  locationEndpoint: string,
  locationTypeEndpoint: string
}

export let defaultAddressConfig: AddressConfig = {
  defaultLayout: Layout.GRID,
  upsertOnDialog: true,
  showUserFilter: false,
  showDeleteButton: true,
  searchEndpoint: DashEndpoint.ADDRESSS.ADDRESS_ENDPOINT,
  delEndpoint: DashEndpoint.ADDRESSS.ADDRESS_ENDPOINT,
  locationEndpoint: DashEndpoint.LOCATIONS.LOCATION_ENDPOINT,
  locationTypeEndpoint: DashEndpoint.LOCATIONTYPES.LOCATIONTYPE_ENDPOINT
}

export let selfEndpoints = {
  endpoint: DashEndpoint.ADDRESSS.ADDRESS_ENDPOINT,
  adminEndpoint: DashEndpoint.ADDRESSS.ADDRESS_ENDPOINT_ADMIN,
}

export let selfRoutes = {
  list: "/dash/addresses",
  create: "/dash/addresses/create",
  update(id: number | null) {
    return id == null ? this.create : "/dash/addresses/" + id + '/update'
  }
}

