import {Layout} from "../../../../../common/modules/generic/breadcrumb/breadcrumb.model";
import {BaseConfig, BaseConfigV2} from "../../../../../../base/models/baseConfig";
import {InventoryEndpoints} from "../../inventory.endpoints";

export interface StockConfig extends BaseConfigV2 {
  vendorEndpoint: string
}

export let defaultStockConfig: StockConfig = {
  defaultLayout: Layout.COLUMN,
  upsertOnDialog: true,
  showUserFilter: false,
  showDeleteButton: false,
  endpoint: InventoryEndpoints.STOCKS.STOCK_ENDPOINT_V2,
  delEndpoint: InventoryEndpoints.STOCKS.STOCK_ENDPOINT,
  vendorEndpoint: InventoryEndpoints.VENDORS.VENDOR_ENDPOINT_V2
}


export let selfRoutes = {
  list: "/dash/inventory/stocks",
  create: "/dash/inventory/stocks/create",
  update(id: number | null) {
    return id == null ? this.create : "/dash/inventory/stocks/" + id + '/update'
  }
}
