import {Injectable} from '@angular/core';
import {AuthService} from "../../common/modules/auth/services/auth.service";
import {NotifierService} from "../../../services/notifier/notifier.service";
import {HttpClient, HttpErrorResponse} from "@angular/common/http";
import {EventBusService} from "../../../services/eventbus/event-bus.service";
import {BaseService} from "../../../base/services/base.service";
import {WebAppEndpoint} from "../../webapp/web-app.endpoint";
import {IParams} from "../../../base/endpoints/iparams";
import {PollStats} from "../models/poll-stats.model";
import {IAuth} from "../../common/modules/auth/models/auth.model";

@Injectable({
	providedIn: 'root'
})
export class PollStatsService extends BaseService {

	constructor(
		authService: AuthService,
		notifier: NotifierService,
		private eventBusService: EventBusService,
		private http: HttpClient,
	) {
		super(authService, notifier);
	}

	fetchOverviewStats(key: string, endpoint: string, params: IParams) {
		this.http.get<PollStats>(WebAppEndpoint.POLL_STATISTICS.OVERVIEW_STATS(endpoint, params))
			.subscribe((pollStats: PollStats) => {
				this.eventBusService.publish(
					key,
					pollStats
				)
			}, error => {
				 this.handleError(error, (auth: IAuth) => {
           this.fetchOverviewStats(key, endpoint, params)
        }, (err: HttpErrorResponse) => {
          this.eventBusService.publish(this.getErrorKey(key), err)
        })
			})
	}

	fetchDateWisePollCounts(key: string, endpoint: string, params: IParams) {
		this.http.get<Map<string, number>>(WebAppEndpoint.POLL_STATISTICS.DATE_WISE_POLL_COUNTS(endpoint, params))
			.subscribe((dateWisePollStats: Map<string, number>) => {
				this.eventBusService.publish(
					key,
					dateWisePollStats
				)
			}, error => {
				 this.handleError(error, (auth: IAuth) => {
            this.fetchDateWisePollCounts(key, endpoint, params)
        }, (err: HttpErrorResponse) => {
          this.eventBusService.publish(this.getErrorKey(key), err)
        })
			})
	}
}
