import {Component, EventEmitter, Input, OnInit, Output} from '@angular/core';
import {BaseComponent} from "../../../../../../base/components/base.component";
import {FormGroup} from "@angular/forms";
import {IPage} from "../../../../../../base/models/IPage";
import {EventBusService} from "../../../../../../services/eventbus/event-bus.service";
import {ActivatedRoute, Router} from "@angular/router";
import {SubscriptionContainerService} from "../../../../../../base/services/subscription-container.service";
import {NotifierService} from "../../../../../../services/notifier/notifier.service";
import {IQuestionnaire} from "../../models/IQuestionnaire";
import {QuestionService} from "../../services/question.service";
import {EventKey} from "../../../../../../services/eventbus/event.key";
import {MetaData} from "ng-event-bus/lib/meta-data";
import {HttpErrorResponse} from "@angular/common/http";
import {IUploadFile} from "../../../../../webapp/models/uploads/IUploadFile";
import {FileUploadService} from "../../../../../../services/files/file-upload.service";
import {IImageUploadResponse} from "../../../../../../models/IImageUploadResponse";
import {ILabel} from "../../../labels-legacy/models/ilabel";
import {MatDialog} from "@angular/material/dialog";
import {
  LabelEditViewFormComponent
} from "../../../labels-legacy/components/label-edit-view-form/label-edit-view-form.component";
import {IParams} from "../../../../../../base/endpoints/iparams";
import {LabelService} from "../../../labels-legacy/services/label.service";
import {WebAppEndpoint} from "../../../../../webapp/web-app.endpoint";
import {Config} from "../../../poll/components/poll-list/config";
import {AuthService} from "../../../../../common/modules/auth/services/auth.service";
import {QuestionnaireConfig} from "../../models/config";
import {Location} from "@angular/common";

@Component({
  selector: 'app-question-edit-form',
  templateUrl: './question-edit-form.component.html',
  styleUrls: ['./question-edit-form.component.scss']
})
export class QuestionEditFormComponent extends BaseComponent implements OnInit {
  pollListConfig: Config = {
    showUserFilter: false,
    showDeleteButton: false,
    endpoint: WebAppEndpoint.POLLS.SEARCH_ENDPOINT
  }

  config: QuestionnaireConfig = {
    showUserFilter: false,
    showApprovalButton: false,
    showDeleteButton: false,
    labelsEndpoint: WebAppEndpoint.LABELS.SEARCH_ENDPOINT_FOR_POLLBOX,
    endpoint: WebAppEndpoint.QUESTIONNAIRES.SEARCH_ENDPOINT
  }

  questionForm: FormGroup
  @Input() exQuestion: IQuestionnaire | undefined
  @Output() questionOptionLoaded: EventEmitter<boolean> = new EventEmitter<boolean>()
  uploadFiles: IUploadFile[] = []
  paramId: number | undefined

  start_date: Date;
  end_date: Date;

  labels: IPage<ILabel>
  labelList: ILabel[] = []
  selectedLabel: ILabel[] = []


  constructor(
    eventBus: EventBusService,
    router: Router,
    route: ActivatedRoute,
    subContainer: SubscriptionContainerService,
    private fileUploadService: FileUploadService,
    private notifier: NotifierService,
    private questionService: QuestionService,
    private dialog: MatDialog,
    private labelService: LabelService,
    authService: AuthService,
    private location: Location
  ) {
    super(eventBus, router, subContainer)
    if (authService.isAdmin()) {
      this.pollListConfig = {
        showUserFilter: true,
        showDeleteButton: true,
        endpoint: WebAppEndpoint.POLLS.SEARCH_ENDPOINT_ADMIN
      }
      this.config = {
        showUserFilter: true,
        showApprovalButton: true,
        showDeleteButton: true,
        labelsEndpoint: WebAppEndpoint.LABELS.SEARCH_ENDPOINT_ADMIN,
        endpoint: WebAppEndpoint.QUESTIONNAIRES.SEARCH_ENDPOINT_ADMIN
      }
    }

    this.getQuestionDetails(route.snapshot.params['id'])
  }

  protected componentName(): string {
    return "QuestionEditFormComponent";
  }

  ngOnInit(): void {
    this.loading = false;
    this.questionForm = this.questionService.getQuestionForm(this.exQuestion)
    this.selectedLabel = this.exQuestion?.label ? [this.exQuestion.label] : []
    this.searchLabels({size: 20})
  }

  private getQuestionDetails(param: number | undefined) {
    if (param) {
      this.paramId = param
      this.loading = true
      const key = this.componentKey(EventKey.QUESTIONNAIRES.GET_DETAILS(param))
      this.questionService.getQuestionDetails(key, param)
      this.subscribe(
        key,
        null,
        (value: Boolean, meta: MetaData) => {
          this.exQuestion = meta.data
          if (meta.data.active_until) {
            this.start_date = new Date(meta.data.active_from)
            this.end_date = new Date(meta.data.active_until)
          }
          this.questionForm = this.questionService.getQuestionForm(this.exQuestion)
          this.selectedLabel = this.exQuestion?.label ? [this.exQuestion.label] : []
          this.loading = false
        })
      this.subscribe(this.getErrorKey(key), null, (value, meta) => {
        this.notifier.error('Error', meta.data.error.message)
      })
    }
  }


  createQuestionnaire(images: IImageUploadResponse[]) {
    let key = this.componentKey(EventKey.POLLS.CREATE(this.questionForm.value.name))
    if (this.questionForm.value.id) {
      this.questionService.updateQuestion(key, this.questionForm.value, images)
    } else {
      this.questionService.createQuestionnaire(key, this.questionForm.value, images)
    }
    this.subscribe(key, null, (value, meta) => {
      this.loading = false;
      this.notifier.success("Success", "Poll created")
      this.questionForm.reset();
      this.uploadFiles = []
      this.navigate("admin/question-form/" + meta.data.id, value => {
        this.reloadRoute()
      })
    })
    this.subscribe(this.getErrorKey(key), null, (value, meta) => {
      let error: HttpErrorResponse = meta.data
      this.loading = false;
      this.notifier.warning('Failed!', error.error.message)
    })
  }


  removeImage(uf: IUploadFile) {
    let index = this.uploadFiles.map(file => file.previewUrl).indexOf(uf.previewUrl)
    this.uploadFiles.splice(index, 1)
  }

  onImageSelected(event: any) {
    if (event.target.files && event.target.files[0]) {
      // validate image
      const mimeType = event.target.files[0].type;
      if (mimeType.match(/image\/*/) == null) {
        this.notifier.warning("Unsupported", "Only images are supported.");
        return;
      }

      let uf: IUploadFile = {
        file: event.target.files[0],
        previewUrl: null
      }

      const reader = new FileReader();
      reader.readAsDataURL(event.target.files[0]);
      reader.onload = (e) => { // called once readAsDataURL is completed
        let url = e.target?.result
        if (url !== undefined && url !== null) {
          uf.previewUrl = url
          this.uploadFiles.push(uf);
        }
      }
    }
  }

  isInvalidFileCount(): boolean {
    return (this.uploadFiles.length === 0 &&
        (this.questionForm.value.post?.images === null || this.questionForm.value.post?.images?.length === 0))
      || this.uploadFiles.length > 3
  }

  hasPreviousImage(): boolean {
    return this.questionForm.value.post?.images?.length > 0
  }

  uploadImages(): void {
    this.loading = true

    if (this.uploadFiles.length > 0) {
      let files: File[] = []
      this.uploadFiles.forEach(uf => {
        files.push(uf.file)
      });
      let key = this.componentKey(EventKey.FILES.IMAGE_UPLOAD(this.questionForm.value.name + '-verif-img'))
      this.fileUploadService.uploadImages(key, files)
      this.subscribe(
        key,
        null,
        (value, meta) => {
          let imageResponses = <IImageUploadResponse[]>meta.data
          this.createQuestionnaire(imageResponses)
        }
      );
    } else this.createQuestionnaire([])
  }

  selectStartDate() {
    if (this.start_date != null) {
      this.questionForm.controls['active_from'].setValue(this.start_date);
    } else {
      this.questionForm.controls['active_from'].setValue(null);
    }
  }

  selectEndDate() {
    if (this.end_date != null) {
      this.questionForm.controls['active_until'].setValue(this.end_date);
    } else {
      this.questionForm.controls['active_until'].setValue(null);
    }
  }

  addLabel(event: any) {
    for (let i = 0; i < this.labelList.length; i++) {
      if (this.labelList[i].id && this.labelList[i].name === event.name) {
        this.questionForm.controls['label_id'].setValue(this.labelList[i].id)
        this.selectedLabel = []
        this.selectedLabel[0] = this.labelList[i];
        return
      }
    }

    const dialogRef = this.dialog.open(LabelEditViewFormComponent, {
      width: '600px',
      height: '300px',
      data: {
        name: event.name,
        parentCode: "POLLBOX"
      },
    })

    dialogRef.afterClosed().subscribe((result: ILabel) => {
      if (result) {
        this.questionForm.controls['label_id'].setValue(result.id)
        this.selectedLabel = []
        this.selectedLabel[0] = result;
      }
    })
  }


  private searchLabels(params: IParams) {
    this.loading = true
    const key = this.componentKey(EventKey.LABELS_LEGACY.SEARCH())
    this.labelService.searchLabels(key, this.config.labelsEndpoint, params)
    this.subscribe(
      key,
      null,
      (value: Boolean, meta: MetaData) => {
        this.labels = meta.data
        this.labelList = this.labels.content
        this.loading = false
      })
    this.subscribe(this.getErrorKey(key), null, (value, meta) => {
      this.notifier.error('Error', meta.data.error.message)
    })
  }

  onSearchLabel(event: any) {
    if (event.target.value.length > 2) {
      this.searchLabels({
        size: 20,
        q: event?.target?.value
      })
    }
  }

  onClearLabel() {

  }

  onScrollLabelToEnd() {
    if (!this.labels.last) {
      this.searchLabels({
        size: this.labels.numberOfElements + 20,
      })
    }
  }

  getImage() {
    return this.questionForm.value.post.images[0]
  }

  goBack() {
    this.location.back()
  }

  approveQuestionnaire() {
    this.loading = true
    const key = this.componentKey(EventKey.QUESTIONNAIRES.TOGGLE_APPROVAL(this.questionForm.value.id))
    this.questionService.toggleApproval(key, this.questionForm.value.id, {
      approved: true
    })
    this.subscribe(key, null, (value: Boolean, meta: MetaData) => {
      this.loading = false
      this.goBack()
    })
    this.subscribe(this.getErrorKey(key), null, (value, meta) => {
      this.notifier.error('Error', meta.data.error.message)
    })
  }

}
