import {Component} from '@angular/core';
import {BaseConfig} from 'src/app/base/models/baseConfig';
import {BaseListComponent} from "../../../../../../../base/components/base-list.component";
import {defaultPOptionConfig, POptionConfig, selfRoutes} from "../../models/config.model";
import {defaultPage, IPage} from "../../../../../../../base/models/IPage";
import {EventBusService} from "../../../../../../../services/eventbus/event-bus.service";
import {ActivatedRoute, Router} from "@angular/router";
import {SubscriptionContainerService} from "../../../../../../../base/services/subscription-container.service";
import {AuthService} from "../../../../../../common/modules/auth/services/auth.service";
import {MatDialog} from "@angular/material/dialog";
import {POptionService} from "../../services/poption.service";
import {NotifierService} from "../../../../../../../services/notifier/notifier.service";
import {parseFilterData} from "../../../../../../common/modules/utility/components/basic-filter/basic-filter.model";
import {BreadcrumbItem} from "../../../../../../common/modules/generic/breadcrumb/breadcrumb.model";
import {IParams} from "../../../../../../../base/endpoints/iparams";
import {EventKey} from "../../../../../../../services/eventbus/event.key";
import {MetaData} from "ng-event-bus/lib/meta-data";
import {ComponentType} from "@angular/cdk/overlay";
import {POptionUpsertComponent} from "../poption-upsert/poption-upsert.component";
import {IPOption} from "../../models/poption.model";
import {BaseUpsertComponent} from "../../../../../../../base/components/base-upsert.component";
import {DashEndpoint} from "../../../../../dash.endpoint";

@Component({
  selector: 'app-poption-list',
  templateUrl: './poption-list.component.html',
  styleUrls: ['./poption-list.component.scss']
})
export class POptionListComponent extends BaseListComponent {

  config: POptionConfig = defaultPOptionConfig
  pOptions: IPage<IPOption> = defaultPage

  protected componentName(): string {
    return "POptionListComponent";
  }

  constructor(eventBus: EventBusService,
              router: Router,
              subContainer: SubscriptionContainerService,
              authService: AuthService,
              dialog: MatDialog,
              private activatedRoute: ActivatedRoute,
              private pOptionService: POptionService,
              private notifier: NotifierService,
  ) {
    super(eventBus, router, subContainer, dialog)
    if (authService.isAdmin()) {
      this.config.searchEndpoint = DashEndpoint.POPTIONS.POPTION_ENDPOINT_ADMIN
    }
  }

  initialize(): void {
    this.activatedRoute.queryParams.subscribe(params => {
      let filterData = parseFilterData(params)
      this.fetchPOptions({
        q: filterData.query,
        username: filterData.username,
        from_date: filterData.fromDate,
        to_date: filterData.toDate,
        page: filterData.page,
        size: filterData.size
      })
    });
  }

  protected override getBreadcrumbItems(): BreadcrumbItem[] {
    return [
      {
         text: this.translations["Dashboard"],
        link: null,
        active: false
      },
      {
        text: "POptions",
        link: null,
        active: true
      }
    ]
  }

  /*
  - Copy this to EventKey and delete afterward
   static POPTIONS = {
      SEARCH(params: IParams) {
        return "poptions:search:search".concat(BaseEndpoint.toParamString(params))
      }
    }
   */

  private fetchPOptions(params: IParams) {
    this.loading = true
    const key = this.componentKey(EventKey.POLL_OPTIONS.SEARCH(params))
    this.pOptionService.search(key, this.config.searchEndpoint, params)
    this.subscribe(
      key,
      null,
      (value: Boolean, meta: MetaData) => {
        this.pOptions = meta.data
        this.loading = false
      }
    )
    this.subscribe(this.getErrorKey(key), null, (value, meta) => {
      this.notifier.error('Error', meta.data.error.message)
    })
  }

  onDeleted(deleted: Boolean) {
    if (deleted) {
      this.notifier.success("Success", "POption is deleted")
      this.ngOnInit()
    }
  }

  override getConfig(): BaseConfig {
    return this.config
  }


  override getRoutes(): { create: string, update: (id: number) => string, params: IParams } | null {
    return {
      create: selfRoutes.create,
      update: (id: number) => selfRoutes.update(id),
      params: {}
    }
  }

  getUpsertComponent(): ComponentType<BaseUpsertComponent> | null {
    return POptionUpsertComponent
  }

}
