import {Inject, Injectable, LOCALE_ID} from '@angular/core';
import {BaseService} from "../../../../../base/services/base.service";
import {AuthService} from "../../../../common/modules/auth/services/auth.service";
import {NotifierService} from "../../../../../services/notifier/notifier.service";
import {HttpClient, HttpErrorResponse} from "@angular/common/http";
import {EventBusService} from "../../../../../services/eventbus/event-bus.service";
import {IParams} from "../../../../../base/endpoints/iparams";
import {IPage} from "../../../../../base/models/IPage";
import {WebAppEndpoint} from "../../../../webapp/web-app.endpoint";
import {IQuestionnaire} from "../models/IQuestionnaire";
import {FormBuilder, FormControl, Validators} from "@angular/forms";
import {PostService} from "../../poll/services/post.service";
import {LabelService} from "../../labels-legacy/services/label.service";
import {getDate} from "ngx-bootstrap/chronos/utils/date-getters";
import {IImageUploadResponse} from "../../../../../models/IImageUploadResponse";
import {IResultSheetItem} from "../../exam/result-sheet/result-sheet.model";
import {IAuth} from "../../../../common/modules/auth/models/auth.model";

@Injectable({
  providedIn: 'root'
})
export class QuestionService extends BaseService {

  constructor(authService: AuthService,
              notifier: NotifierService,
              private http: HttpClient,
              private formBuilder: FormBuilder,
              @Inject(LOCALE_ID) public locale: string,
              private postService: PostService,
              private eventBusService: EventBusService) {
    super(authService, notifier);
  }

  fetchQuestionnaires(key: string, endpoint: string, params: IParams) {
    this.http.get<IPage<IQuestionnaire>>(WebAppEndpoint.QUESTIONNAIRES.SEARCH(endpoint, params))
      .subscribe((questionnaires: IPage<IQuestionnaire>) => {
        this.eventBusService.publish(
          key,
          questionnaires
        )
      }, error => {
        this.handleError(error, (auth: IAuth) => {
          this.fetchQuestionnaires(key, endpoint, params)
        }, (err: HttpErrorResponse) => {
          this.eventBusService.publish(this.getErrorKey(key), err)
        })
      })
  }

  fetchResultSheet(key: string, questionnaireId: number) {
    this.http.get<IResultSheetItem[]>(WebAppEndpoint.QUESTIONNAIRES.RESULT_SHEET(questionnaireId))
      .subscribe((resultSheetItems: IResultSheetItem[]) => {
        this.eventBusService.publish(
          key,
          resultSheetItems
        )
      }, error => {
        this.handleError(error, (auth: IAuth) => {
          this.fetchResultSheet(key, questionnaireId)
        }, (err: HttpErrorResponse) => {
          this.eventBusService.publish(this.getErrorKey(key), err)
        })
      })
  }

  toggleApproval(key: string, questionnaireId: number, params: IParams) {
    this.http.patch<IQuestionnaire>(WebAppEndpoint.QUESTIONNAIRES.TOGGLE_APPROVAL(questionnaireId, params), {})
      .subscribe((questionnaire: IQuestionnaire) => {
        this.eventBusService.publish(
          key,
          questionnaire
        )
      }, error => {
        this.handleError(error, (auth: IAuth) => {
          this.toggleApproval(key, questionnaireId, params)
        }, (err: HttpErrorResponse) => {
          this.eventBusService.publish(this.getErrorKey(key), err)
        })
      })
  }

  getQuestionForm(exQuestion: IQuestionnaire | undefined) {
    return exQuestion ? this.initializeForm(exQuestion) : this.createForm();
  }

  private initializeForm(question: IQuestionnaire) {
    return this.formBuilder.group({
      id: new FormControl(question?.id),
      title: new FormControl(question?.title, [Validators.required, Validators.minLength(20)]),
      type: new FormControl(question?.type),
      active_from: new FormControl(question?.active_from),
      active_until: new FormControl(question?.active_until),
      post: this.postService.populatePostForm(question?.post),
      label: new FormControl(question?.label),
      label_id: new FormControl(question?.label_id),
      is_public: new FormControl(question?.is_public),
      approved: new FormControl(question?.approved),
    })
  }

  private createForm() {
    return this.formBuilder.group({
      title: new FormControl(null, [Validators.required, Validators.minLength(20)]),
      type: new FormControl("ADMIN_MANAGED"),
      active_from: new FormControl(new Date()),
      active_until: new FormControl(null),
      post: this.postService.getPostForm("QUESTION_SET"),
      label: new FormControl(),
      label_id: new FormControl(),
      is_public: new FormControl(true),
      approved: new FormControl(),
    })
  }

  getQuestionDetails(key: string, id: number) {
    this.http.get<IQuestionnaire>(WebAppEndpoint.QUESTIONNAIRES.GET_DETAILS(id))
      .subscribe((question: IQuestionnaire) => {
        this.eventBusService.publish(
          key,
          question
        )
      }, error => {
        this.handleError(error, (auth: IAuth) => {
          this.getQuestionDetails(key, id)
        }, (err: HttpErrorResponse) => {
          this.eventBusService.publish(this.getErrorKey(key), err)
        })
      })
  }

  createQuestionnaire(key: string, questionnaire: IQuestionnaire, images: IImageUploadResponse[]) {

    questionnaire.post.images = images.map(r => r.image_url)
    questionnaire.post.thumbs = images.map(r => r.thumb_url)
    questionnaire.active_from = this.validated(questionnaire.active_from)

    this.http.post<IQuestionnaire>(WebAppEndpoint.QUESTIONNAIRES.CREATE(), questionnaire)
      .subscribe((que: IQuestionnaire) => {
        this.eventBusService.publish(key, que)
      }, error => {
        this.handleError(error, (auth: IAuth) => {
          this.createQuestionnaire(key, questionnaire, images)
        }, (err: HttpErrorResponse) => {
          this.eventBusService.publish(this.getErrorKey(key), err)
        })
      })
  }

  updateQuestion(key: string, questionnaire: IQuestionnaire, images: IImageUploadResponse[]) {

    if (images.length > 0) {
      questionnaire.post.images = images.map(r => r.image_url)
      questionnaire.post.thumbs = images.map(r => r.thumb_url)

    }
    questionnaire.active_from = this.validated(questionnaire.active_from)

    this.http.patch<IQuestionnaire>(WebAppEndpoint.QUESTIONNAIRES.UPDATE(questionnaire.id), questionnaire)
      .subscribe((que: IQuestionnaire) => {
        this.eventBusService.publish(key, que)
      }, error => {
        this.handleError(error, (auth: IAuth) => {
          this.updateQuestion(key, questionnaire, images)
        }, (err: HttpErrorResponse) => {
          this.eventBusService.publish(this.getErrorKey(key), err)
        })
      })
  }

  private validated(activeFrom: Date) {
    let cDate: Date = activeFrom == null ? new Date() : new Date(activeFrom)
    cDate.setSeconds(cDate.getSeconds() + 30)
    if (cDate > new Date()) {
      return cDate
    } else {
      cDate = new Date()
      cDate.setSeconds(cDate.getSeconds() + 30)
      return cDate
    }

  }
}
