import {Component, EventEmitter, Inject, Input, OnInit, Output} from '@angular/core';
import {BaseComponent} from "../../../../../../base/components/base.component";
import {FormGroup} from "@angular/forms";
import {IUploadFile} from "../../../../../webapp/models/uploads/IUploadFile";
import {EventBusService} from "../../../../../../services/eventbus/event-bus.service";
import {Router} from "@angular/router";
import {SubscriptionContainerService} from "../../../../../../base/services/subscription-container.service";
import {FileUploadService} from "../../../../../../services/files/file-upload.service";
import {MAT_DIALOG_DATA, MatDialog, MatDialogRef} from "@angular/material/dialog";
import {NotifierService} from "../../../../../../services/notifier/notifier.service";
import {EventKey} from "../../../../../../services/eventbus/event.key";
import {IImageUploadResponse} from "../../../../../../models/IImageUploadResponse";
import {LabelService} from "../../services/label.service";
import {IParams} from "../../../../../../base/endpoints/iparams";
import {MetaData} from "ng-event-bus/lib/meta-data";
import {IPage} from "../../../../../../base/models/IPage";
import {ILabel} from "../../models/ilabel";
import {HttpErrorResponse} from "@angular/common/http";
import {WebAppEndpoint} from "../../../../../webapp/web-app.endpoint";
import {AuthService} from "../../../../../common/modules/auth/services/auth.service";

@Component({
	selector: 'app-label-edit-view-form',
	templateUrl: './label-edit-view-form.component.html',
	styleUrls: ['./label-edit-view-form.component.scss']
})
export class LabelEditViewFormComponent extends BaseComponent implements OnInit {
	@Input() parentCode: string | null

	@Output() formSubmitEvent: EventEmitter<boolean> = new EventEmitter<boolean>()
	labelForm: FormGroup
	uploadFiles: IUploadFile[] = []
	public color1: string = '#2889e9';
	labels: IPage<ILabel>
	labelList: ILabel[] = []

	endpoint: string = WebAppEndpoint.LABELS.SEARCH_ENDPOINT_FOR_POLLBOX

	protected componentName(): string {
		return "LabelEditViewFormComponent";
	}

	constructor(
		eventBus: EventBusService,
		router: Router,
		subContainer: SubscriptionContainerService,
		private fileUploadService: FileUploadService,
		@Inject(MAT_DIALOG_DATA) public data: {name: string, parentCode: string},
		private labelService: LabelService,
		private notifier: NotifierService,
		public dialog: MatDialog,
		public dialogRef: MatDialogRef<LabelEditViewFormComponent>,
		authService: AuthService
	) {
		super(eventBus, router, subContainer)
		this.parentCode = data.parentCode

		if (authService.isAdmin()) {
			this.endpoint = WebAppEndpoint.LABELS.SEARCH_ENDPOINT_ADMIN
		}
	}

	ngOnInit(): void {
		this.loading = false;
		this.getLabels({size: 20})
		this.labelForm = this.labelService.getLabelForm(this.data.name)
	}

	private getLabels(params: IParams) {
		this.loading = true
		const key = this.componentKey(EventKey.LABELS_LEGACY.SEARCH())
		this.labelService.searchLabels(key, this.endpoint, params)
		this.subscribe(
			key,
			null,
			(value: Boolean, meta: MetaData) => {
				this.labels = meta.data
				this.labelList = this.labels.content
				this.loading = false
			})
		this.subscribe(this.getErrorKey(key), null, (value, meta) => {
			this.notifier.error('Error', meta.data.error.message)
		})
	}

	removeImage(uf: IUploadFile) {
		let index = this.uploadFiles.map(file => file.previewUrl).indexOf(uf.previewUrl)
		this.uploadFiles.splice(index, 1)
	}

	onImageSelected(event: any) {
		if (event.target.files && event.target.files[0]) {
			// validate image
			const mimeType = event.target.files[0].type;
			if (mimeType.match(/image\/*/) == null) {
				this.notifier.warning("Unsupported", "Only images are supported.");
				return;
			}

			let uf: IUploadFile = {
				file: event.target.files[0],
				previewUrl: null
			}

			const reader = new FileReader();
			reader.readAsDataURL(event.target.files[0]);
			reader.onload = (e) => { // called once readAsDataURL is completed
				let url = e.target?.result
				if (url !== undefined && url !== null) {
					uf.previewUrl = url
					this.uploadFiles.push(uf);
				}
			}
		}
	}


	uploadImages(): void {
		this.loading = true

		if (this.uploadFiles.length > 0) {
			let files: File[] = []
			this.uploadFiles.forEach(uf => {
				files.push(uf.file)
			});
			let key = this.componentKey(EventKey.FILES.IMAGE_UPLOAD(this.labelForm.value.name + '-verif-img'))
			this.fileUploadService.uploadImages(key, files)
			this.subscribe(
				key,
				null,
				(value, meta) => {
					let imageResponses = <IImageUploadResponse[]>meta.data
					let images = imageResponses.map(r => r.image_url)
					this.createLabel(images)
				}
			);
		} else {
			this.createLabel([])
		}

	}

	createLabel(images: String[]) {
		this.labelForm.controls['color'].setValue(this.color1)
		if (images.length > 0) {
			this.labelForm.controls['image'].setValue(images[0]);
		}

		let key = this.componentKey(EventKey.LABELS_LEGACY.CREATE(this.labelForm.value.name))

		this.labelService.createLabel(key, this.parentCode, this.labelForm.value)

		this.subscribe(key, null, (value, meta) => {
			this.loading = false;
			this.closeDialog(meta.data)
		})
		this.subscribe(this.getErrorKey(key), null, (value, meta) => {
			let error: HttpErrorResponse = meta.data
			this.loading = false;
			this.notifier.warning('Failed!', error.error.message)
		})

	}

	closeDialog(data: ILabel) {
		console.log(data);
		if (!this.dialog.openDialogs || !this.dialog.openDialogs.length) {
			return;
		} else if (this.dialogRef.getState() == 0) {
			this.dialogRef.close(data);
		}
	}

	onScrollLabelToEnd() {
		if (!this.labels.last) {
			this.getLabels({
				size: this.labels.numberOfElements + 20,
			})
		}
	}

	onSearchLabel(event: any) {
		this.getLabels({
			size: 20,
			q: event?.target?.value
		})
	}

}
