import {Component} from '@angular/core';
import {IPollVoteStats, IUserVoteStats} from "../../../domains/poll/models/vote.model";
import {EventKey} from "../../../../../services/eventbus/event.key";
import {BaseComponent} from "../../../../../base/components/base.component";
import {EventBusService} from "../../../../../services/eventbus/event-bus.service";
import {Router} from "@angular/router";
import {SubscriptionContainerService} from "../../../../../base/services/subscription-container.service";
import {VoteService} from "../../../domains/poll/services/vote.service";
import {IParams} from "../../../../../base/endpoints/iparams";
import {
  ActionDef,
  defaultMonthFilterValue,
  IBarChartData,
  IBarChartItem,
  monthFilterData
} from "../../barchart/chart-data.model";
import {TimeUtility} from "../../../../common/modules/utility/time.utility";

@Component({
  selector: 'app-poll-vote-stats-chart',
  templateUrl: './poll-vote-stats-chart.component.html',
  styleUrls: ['./poll-vote-stats-chart.component.scss']
})
export class PollVoteStatsChartComponent extends BaseComponent {

  chartData: IBarChartData = {
    seriesName: {primary: 'Vote Count', alternate: 'Vote Count'},
    data: []
  }
  protected readonly filterData = monthFilterData;
  protected defaultFilterValue: string = defaultMonthFilterValue

  constructor(
    eventBus: EventBusService,
    router: Router,
    subContainer: SubscriptionContainerService,
    private voteService: VoteService
  ) {
    super(eventBus, router, subContainer)
  }

  protected componentName(): string {
    return "PollVoteStatsChartComponent";
  }

  ngOnInit(): void {
    let date = TimeUtility.dateFromMonth(this.defaultFilterValue)
    let range = TimeUtility.getFirstAndLastDayOfMonth(date)
    this.fetchStats(
      {
        "from_date": range.firstDay,
        "to_date": range.lastDay,
        "size": 10
      }
    )
  }

  onChartBarChartFilter(month: string) {
    let date = TimeUtility.dateFromMonth(month)
    let range = TimeUtility.getFirstAndLastDayOfMonth(date)
    this.fetchStats(
      {
        "from_date": range.firstDay,
        "to_date": range.lastDay,
        "size": 10
      }
    )
  }

  actionDef: ActionDef = {
    title: "View All",
    alternateTitle: "View Top 10",
    showAlternate: false,
    action: ()=>{
      let date = TimeUtility.dateFromMonth(defaultMonthFilterValue)
      let range = TimeUtility.getFirstAndLastDayOfMonth(date)
      this.fetchStats(
        {
          "from_date": range.firstDay,
          "to_date": range.lastDay,
          "size": 10
        }
      )
    }
  }


  fetchStats(params: IParams) {
    this.loading = true
    let key = this.componentKey(EventKey.VOTES.FETCH_POLL_VOTE_STATS())
    this.voteService.fetchPollVoteStats(key, params)
    this.subscribe(key, null, (value, meta) => {
      let pollVotes = meta.data
      this.chartData = this.buildChartData(pollVotes)
      this.loading = false
    })
  }

  private buildChartData(userVotes: IPollVoteStats[]): IBarChartData {
    let chartItems: IBarChartItem[] = userVotes.map(stat => {
      return {
        attribute: stat.title,
        value: {
          primary: stat.count,
          alternate: stat.count
        }
      }
    })
    return {
      seriesName: {
        primary: "Votes for poll",
        alternate: "Votes for poll"
      },
      data: chartItems
    }
  }


}
