import {Component, Inject} from "@angular/core";
import {BaseUpsertComponentV2} from "../../../../../../../base/components/base-upsert-v2.component";
import {defaultLocationTypeConfig, LocationTypeConfig, selfRoutes} from "../../models/config.model";
import {FormControl, FormGroup, Validators} from "@angular/forms";
import {EventBusService} from "../../../../../../../services/eventbus/event-bus.service";
import {ActivatedRoute, Router} from "@angular/router";
import {SubscriptionContainerService} from "../../../../../../../base/services/subscription-container.service";
import {LocationTypeService} from "../../services/locationtype.service";
import {NotifierService} from "../../../../../../../services/notifier/notifier.service";
import {BreadcrumbItem} from "../../../../../../common/modules/generic/breadcrumb/breadcrumb.model";
import {EventKey} from "../../../../../../../services/eventbus/event.key";
import {HttpErrorResponse} from "@angular/common/http";
import {ILocationType, ILocationTypeDetail} from "../../models/locationtype.model";
import {MAT_DIALOG_DATA} from "@angular/material/dialog";
import {BaseConfig} from "../../../../../../../base/models/baseConfig";
import {TextUtility} from "../../../../../../common/modules/utility/text.utility";


@Component({
  selector: 'app-locationtype-upsert',
  templateUrl: './locationtype-upsert.component.html',
  styleUrls: ['./locationtype-upsert.component.scss']
})
export class LocationTypeUpsertComponent extends BaseUpsertComponentV2<ILocationType> {

  config: LocationTypeConfig = defaultLocationTypeConfig

  selectedParent: ILocationType | null = null

  locationTypeForm = new FormGroup({
    label: new FormControl('', [Validators.required, Validators.minLength(3), Validators.maxLength(100)]),
    code: new FormControl('', [Validators.required, Validators.minLength(3), Validators.maxLength(100)]),
    parent_id: new FormControl<number | null>(null, []),
    level: new FormControl<number>(0, [Validators.required]),
    description: new FormControl('', [Validators.required, Validators.minLength(20)]),
  })

  constructor(eventBus: EventBusService,
              router: Router,
              subContainer: SubscriptionContainerService,
              route: ActivatedRoute,
              private locationTypeService: LocationTypeService,
              private notifier: NotifierService,
              @Inject(MAT_DIALOG_DATA) dialogData: any) {
    super(eventBus, router, subContainer, route, dialogData)

    if (dialogData.title!=null){
      this.locationTypeForm.controls.label.setValue(dialogData.label)
    }

  }

  protected override componentName(): string {
    return "LocationTypeUpsertComponent"
  }

  protected override getBreadcrumbItems(): BreadcrumbItem[] {
    return [
      {
         text: this.translations["Dashboard"],
        link: null,
        active: false
      },
      {
        text: "LocationTypes",
        link: "/dash/locationtypes",
        active: false
      },
      {
        text: "Upsert",
        link: null,
        active: true
      }
    ]
  }

  override ngOnInit() {
    super.ngOnInit();
    this.locationTypeForm.controls.label.valueChanges.subscribe(value => {
      this.locationTypeForm.controls.code.setValue(TextUtility.codify(value))
    })
  }

  override initialize(exId: number): void {
    this.loading = true
    let key = this.componentKey(EventKey.LOCATIONTYPES.GET(exId))
    this.locationTypeService.fetchSingle(key, exId)
    this.subscribe(key, null, (value, meta) => {
      this.loading = false;
      this.populateForm(meta.data)
    })
    this.subscribe(this.getErrorKey(key), null, (value, meta) => {
      let error: HttpErrorResponse = meta.data
      console.log(error)
      this.loading = false;
    })
  }

  onSubmit() {
    if (!this.locationTypeForm.valid) {
      return
    }
    this.upsert(this.exId)
  }

  getConfig(): BaseConfig {
    return this.config;
  }

  getListPageRoute(): string {
    return selfRoutes.list;
  }

  upsert(exId: number | null) {
    this.loading = true
    let key = this.componentKey(EventKey.LOCATIONTYPES.UPSERT(exId))
    // @ts-ignore
    this.locationTypeService.upsert(key, this.locationTypeForm.value, exId)
    this.subscribe(key, null, (value, meta) => {
      this.loading = false;
      let message = "LocationType has been " + (exId ? "updated" : "created")
      this.notifier.success("Success", message)
      this.locationTypeForm.reset();

      if (this.config.upsertOnDialog) {
        this.onCompleted.emit(meta.data)
      } else {
        this.navigateToListPage( )
      }
    })
    this.subscribe(this.getErrorKey(key), null, (value, meta) => {
      let error: HttpErrorResponse = meta.data
      this.loading = false;
      this.notifier.warning('Failed!', error.error.message)
    })
  }

  private populateForm(n: ILocationTypeDetail) {
    this.selectedParent = n.parent
    this.locationTypeForm.controls.label.setValue(n.label)
    this.locationTypeForm.controls.code.setValue(n.code)
    this.locationTypeForm.controls.level.setValue(n.level)
    this.locationTypeForm.controls.parent_id.setValue(n.parent_id)
    this.locationTypeForm.controls.description.setValue(n.description)
  }

  onLocationTypeSelected(locationType: ILocationType | null) {
    this.selectedParent = locationType
    this.locationTypeForm.controls.parent_id.setValue(
      locationType == null ? null : locationType.id
    )
  }
}
