import {Component, EventEmitter, Input, OnChanges, Output, SimpleChanges, ViewChild} from '@angular/core';
import {ApexAxisChartSeries, ApexChart, ApexTitleSubtitle, ApexXAxis, ChartComponent} from "ng-apexcharts";
import {ActionDef, IBarChartData, IFilterData} from "./chart-data.model";
import {Router} from "@angular/router";

export type ChartOptions = {
  series: ApexAxisChartSeries;
  chart: ApexChart;
  xaxis: ApexXAxis;
  title: ApexTitleSubtitle;
};


@Component({
  selector: 'app-barchart',
  templateUrl: './barchart.component.html',
  styleUrls: ['./barchart.component.scss']
})
export class BarchartComponent implements OnChanges {
  @ViewChild("chart") chart!: ChartComponent;

  @Input() symbol: string = "$"
  @Input() title: string = "Chart Title"
  @Input() subtitleLeft: string = "Left Subtitle"
  @Input() subtitleRight: { primary: string, alternate: string } =
    {primary: "Right Subtitle", alternate: "Right Subtitle Alternate"}
  @Input() attrName: string = "Attribute"

  @Input() showFilter: boolean = false
  @Input() filterData: IFilterData
  @Input() selectedValue: string
  @Input() data: IBarChartData
  attrs: string[] = []
  values: number[] = []
  @Input() totalCanonical: number = 100
  @Input() textCanonical: string = 'Canonical Text'
  @Input() actionDef: ActionDef

  @Output() onFilter: EventEmitter<string> = new EventEmitter<string>()

  public chartOptions: Partial<ChartOptions>;

  sumCalculatedValue() {
    return this.data.data
      .map(item => item.value.alternate)
      .reduce((sum, current) => sum + current, 0);
  }

  constructor(
    private router: Router
  ) {
    this.filterData = {name: 'Filter', options: []}
    this.data = {seriesName: {primary: 'Series Name', alternate: 'alternate'}, data: []}
  }

  ngOnInit() {
    this.loadChart()
  }

  ngOnChanges(changes: SimpleChanges) {
    this.loadChart()
  }

  loadChart() {
    this.attrs = this.data.data.map(item => item.attribute)
    this.values = this.data.data.map(item => {
      let value = this.actionDef.showAlternate ?
        item.value.alternate : item.value.primary
      return parseFloat(value.toFixed(2))
    })
    // this.data.data.forEach(item => {
    //   this.attrs.push(item.attribute)
    //
    //   if (this.actionDef.showAlternate) {
    //     this.values.push(parseFloat(item.value.alternate.toFixed(2)))
    //   } else {
    //     this.values.push(item.value.primary)
    //   }
    // })

    this.chartOptions = {
      series: [
        {
          name: this.actionDef.showAlternate ? this.data.seriesName.alternate : this.data.seriesName.primary,
          data: this.values
        }
      ],
      chart: {
        height: 350,
        type: "bar"
      },
      title: {
        text: this.actionDef.showAlternate ? this.subtitleRight.alternate : this.subtitleRight.primary
      },
      xaxis: {
        categories: this.attrs
      }
    };
  }

  percent() {
    if (this.totalCanonical == 0) return 0
    return (this.sumCalculatedValue() * 100) / this.totalCanonical
  }

  clsFromPercent() {
    if (this.percent() >= 80) {
      return "bg-success"
    } else if (this.percent() >= 30) {
      return "bg-warning"
    } else {
      return "bg-danger"
    }
  }

  onFilterSelected(event: Event) {
    this.attrs = []
    this.values = []
    let target = event.target as HTMLSelectElement
    this.selectedValue = target.value
    this.onFilter.emit(this.selectedValue)
  }

  onBtnClick() {
    if (this.actionDef.action == null) {
      this.actionDef.showAlternate = !this.actionDef.showAlternate
      this.attrs = []
      this.values = []
      this.loadChart()
    } else {
      this.actionDef.showAlternate = !this.actionDef.showAlternate
      this.actionDef.action(this.actionDef.showAlternate)
    }
  }
}
