import {Component, EventEmitter, Input, OnChanges, OnInit, Output, SimpleChanges, ViewChild} from '@angular/core';
import {ActionDef, IBarChartData, IBarChartItem, IFilterData} from "../barchart/chart-data.model";
import {ActivatedRoute, Router} from "@angular/router";
import {EventKey} from "../../../../services/eventbus/event.key";
import {MetaData} from "ng-event-bus/lib/meta-data";
import {BaseComponent} from "../../../../base/components/base.component";
import {AuthService} from "../../../common/modules/auth/services/auth.service";
import {NotifierService} from "../../../../services/notifier/notifier.service";
import {EventBusService} from "../../../../services/eventbus/event-bus.service";
import {SubscriptionContainerService} from "../../../../base/services/subscription-container.service";
import {PollStatsService} from "../../services/poll-stats.service";
import {TargetAchievement} from "../../domains/poll/models/data";
import * as moment from "moment/moment";
import {TimeUtility} from "../../../common/modules/utility/time.utility";
import {EarningChartConfig} from "./config";
import {WebAppEndpoint} from "../../../webapp/web-app.endpoint";
import {from} from "rxjs";
import {parseFilterData} from "../../../common/modules/utility/components/basic-filter/basic-filter.model";

@Component({
  selector: 'app-earning-chart',
  templateUrl: './earning-chart.component.html',
  styleUrls: ['./earning-chart.component.scss']
})
export class EarningChartComponent extends BaseComponent {
  @Input() config: EarningChartConfig = {
    endpoint: WebAppEndpoint.POLL_STATISTICS.DATE_WISE_POLL_COUNTS_ENDPOINT
  }

  @Input() totalPollCount: number = 0
  @Input() approvedPollCount: number = 0
  @Input() username: string | null

  earnings: IBarChartData = {
    seriesName: {primary: 'Count', alternate: 'Earning'},
    data: []
  }

  filterData: IFilterData = {
    name: "Month",
    options: [
      {title: 'January', value: 'january'},
      {title: 'February', value: 'february'},
      {title: 'March', value: 'march'},
      {title: 'April', value: 'april'},
      {title: 'May', value: 'may'},
      {title: 'June', value: 'june'},
      {title: 'July', value: 'july'},
      {title: 'August', value: 'august'},
      {title: 'September', value: 'september'},
      {title: 'October', value: 'october'},
      {title: 'November', value: 'november'},
      {title: 'December', value: 'december'},
    ]
  }
  defaultFilterValue: string = this.filterData.options[new Date().getMonth()].value
  actionDef: ActionDef = {
    title: "Show Earning",
    alternateTitle: "Show Count",
    showAlternate: false,
    action: null
  }

  showDateWiseStats: boolean = true

  target: number = 200
  salary: number = 10000
  workingDaysCount: number = 26

  constructor(private authService: AuthService,
              private notifier: NotifierService,
              eventBus: EventBusService,
              router: Router,
              subContainer: SubscriptionContainerService,
              private pollStatsService: PollStatsService,
              private activatedRoute: ActivatedRoute) {
    super(eventBus, router, subContainer)

  }

  protected componentName(): string {
    return "EarningChartComponent";
  }

  ngOnInit(): void {
    // let date = new Date()
    // let fromDate = new Date(date.getFullYear(), date.getMonth(), 1)
    // let toDate = new Date(date.getFullYear(), date.getMonth() + 1, 0)
    // this.loadDateWisePollCounts(this.username, fromDate, toDate)

    this.activatedRoute.queryParams.subscribe(params => {
      let filterData = parseFilterData(params)
      this.loadDateWisePollCounts(filterData.username, filterData.fromDate, filterData.toDate)
    });
  }

  getQueryParams(date: string | null) {
    let resolvedDate = date == null ? new Date() : moment(date, "DD-MM-YYYY").toDate()
    return {
      created_from: TimeUtility.getDayStart(resolvedDate).toISOString(),
      created_to: TimeUtility.getDayEnd(resolvedDate).toISOString()
    }
  }

  getUsername(): string {
    return this.authService.getAuth().username
  }

  onChartBarChartFilter(month: string) {
    let date = this.dateFromMonth(month)
    let fromDate = new Date(date.getFullYear(), date.getMonth(), 1)
    let toDate = new Date(date.getFullYear(), date.getMonth() + 1, 0)
    this.loadDateWisePollCounts(this.username, fromDate, toDate)
  }

  dateFromMonth(month: string): Date {
    const monthIndex = this.filterData.options.map(m => m.value)
      .findIndex(name => name.toLowerCase() === month.toLowerCase());

    const date = new Date();
    date.setUTCMonth(monthIndex !== -1 ? monthIndex : new Date().getMonth());
    date.setUTCDate(1);
    date.setUTCHours(0, 0, 0, 0);
    return date;
  }

  private loadDateWisePollCounts(username: string | null, from: Date, to: Date) {
    this.loading = true
    let params = {
      username: username,
      from_date: new Date(from.getFullYear(), from.getMonth(), 1),
      to_date: new Date(to.getFullYear(), to.getMonth() + 1, 0)
    }
    const key = this.componentKey(EventKey.POLL_STATS.DATE_WISE_POLL_COUNT())
    this.pollStatsService.fetchDateWisePollCounts(key, this.config.endpoint, params)
    this.subscribe(
      key,
      null,
      (value: Boolean, meta: MetaData) => {
        this.earnings = this.processData(meta.data)
        this.loading = false
      })
    this.subscribe(this.getErrorKey(key), null, (value, meta) => {
      this.notifier.log(meta.data)
    })
  }

  private processData(data: object): IBarChartData {
    let items: IBarChartItem[] = []
    for (let entry of Object.entries(data)) {
      let item: IBarChartItem = {
        attribute: entry[0],
        value: {
          primary: entry[1],
          alternate: this.targetAchievement(entry[1]).earningsToday
        }
      }
      items.push(item)
    }
    return {
      seriesName: this.earnings.seriesName,
      data: items.sort((a, b) => a.attribute.localeCompare(b.attribute))
    };
  }

  achievement(): TargetAchievement {
    return this.targetAchievement(this.approvedPollCount)
  }

  targetAchievement(value: number | undefined): TargetAchievement {
    if (value == undefined) value = 0
    let a = new TargetAchievement()
    a.targetLeft = this.target - value;
    a.achievedPercent = (100 * value) / this.target
    a.dailyEarningsCanonical = this.salary / this.workingDaysCount
    a.monthlyEarningsCanonical = this.salary
    a.earningsToday = (a.dailyEarningsCanonical / this.target) * value
    let count = this.earnings.data.length
    let sum = this.earnings.data.map(item => item.value.primary)
      .reduce((sum, current) => sum + current, 0);
    let avgCount = count === 0 ? 0 : sum / count
    a.avgDailyEarnings = (a.dailyEarningsCanonical / this.target) * avgCount
    a.monthlyForecastedEarnings = a.avgDailyEarnings * this.workingDaysCount
    a.monthlyEarningsAdjustments = a.monthlyForecastedEarnings - a.monthlyEarningsCanonical
    if (a.achievedPercent >= 80) {
      a.achievementMarkClass = "text-success"
    } else if (a.achievedPercent < 80 && a.achievedPercent >= 30) {
      a.achievementMarkClass = "text-warning"
    } else {
      a.achievementMarkClass = "text-danger"
    }
    return a
  }

}
