import {IProductSpecification} from "../../productspecifications/models/productspecification.model";
import {ICategoryBrief} from "../../categories/models/category.model";
import {IVariation} from "../../variations/models/variation.model";
import {IBrand} from "../../brands/models/brand.model";
import {IParams} from "../../../../../../base/endpoints/iparams";
import {Locales} from "../../../../models/global.config";
import {IUnit} from "../../units/models/unit.model";

export interface IProductBrief {
  id: number,
  created_at: Date;
  updated_at: Date;
  created_by: string;

  name: string
  name_bn: string | null
  brand: IBrand
  images: string[]
  categories: ICategoryBrief[]
  weight: number,
  approved: boolean,
  unit: IUnit | null
}

export interface IProduct {
  id: number;
  created_at: Date;
  updated_at: Date;
  created_by: string;

  name: string;
  name_bn: string | null
  slug: string;
  description: string;
  images: string[];
  specifications: Map<string, string>;
  approved: boolean;
  weight: number;
  frozen_item: boolean;
  category_ids: number[];
  variant_ids: number[];
  brand_id: number;
  average_rating: number;
  action: string;
  discount_amount: number;
  sku: string;
  vendor_phone?: any;
  vendor_name: string;
  min_retail_price: number;
  max_retail_price: number;
  price_to_show: string;
  discounted_price: number;
  specification_list: IProductSpecification[];
  category_names: string[];
  variant_names: string[];
  brand_name: string;
  cover_photo: string | null,
  unit: IUnit | null
}

export interface IProductSingle {
  id: number;
  created_at: Date;
  updated_at: Date;
  created_by: string;

  name: string;
  name_bn: string | null
  slug: string;
  description: string;
  images: string[];
  specifications: IProductSpecification[];
  approved: boolean;
  weight: number;
  frozen_item: boolean;
  categories: ICategoryBrief[];
  variations: IVariation[];
  brand: IBrand;
  average_rating: number;
  action: string;
  discount_amount: number;
  sku: string;
  vendor_phone?: any;
  vendor_name: string;
  min_retail_price: number;
  max_retail_price: number;
  price_to_show: string;
  discounted_price: number;
  cover_photo: string | null
  unit: IUnit | null
}

export interface IProductReq {
  name: string
  name_bn: string | null
  slug: string
  description: string | null
  brand_id: number
  category_ids: number[]
  specifications: IParams
  variant_ids: Array<number>
  action: string | null
  images: string[]
  approved: boolean
  frozen_item: boolean
  weight: number | null
  unit_id: number | null
}


export function toProductBrief(n: IProduct): IProductBrief {
  return {
    id: n.id,
    created_at: n.created_at,
    updated_at: n.updated_at,
    created_by: n.created_by,
    name: n.name,
    name_bn: n.name_bn,
    brand: {
      id: n.brand_id,
      name: n.brand_name,
      description: "",
      image: "",
      created_at: new Date(),
      updated_at: new Date(),
      created_by: ""
    },
    images: n.images,
    categories: n.category_ids.map((id, i) => {
      return {
        id: id,
        name: n.category_names[i]
      } as ICategoryBrief
    }),
    weight: n.weight,
    approved: n.approved,
    unit: n.unit
  }

}

export function resolveProductName(p: IProductBrief, locale: string) {
  if (locale === Locales.BN_BN) {
    return p.name_bn == null ? p.name : p.name_bn
  } else {
    return p.name
  }
}
