import {Component} from '@angular/core';
import {BaseListComponentV2} from "../../../../../../../base/components/base-list-v2.component";
import {defaultLocationConfig, LocationConfig, selfEndpoints, selfRoutes} from "../../models/config.model";
import {
  BasicFilterData,
  defaultFilterData,
  parseFilterData,
  toParams
} from "../../../../../../common/modules/utility/components/basic-filter/basic-filter.model";
import {defaultPage, IPage} from "../../../../../../../base/models/IPage";
import {EventBusService} from "../../../../../../../services/eventbus/event-bus.service";
import {ActivatedRoute, Router} from "@angular/router";
import {SubscriptionContainerService} from "../../../../../../../base/services/subscription-container.service";
import {AuthService} from "../../../../../../common/modules/auth/services/auth.service";
import {DialogService} from "../../../../../../common/modules/utility/components/popup/dialog.service";
import {LocationService} from "../../services/location.service";
import {NotifierService} from "../../../../../../../services/notifier/notifier.service";
import {BreadcrumbItem} from "../../../../../../common/modules/generic/breadcrumb/breadcrumb.model";
import {IParams} from "../../../../../../../base/endpoints/iparams";
import {EventKey} from "../../../../../../../services/eventbus/event.key";
import {MetaData} from "ng-event-bus/lib/meta-data";
import {BaseConfig} from "../../../../../../../base/models/baseConfig";
import {ComponentType} from "@angular/cdk/overlay";
import {LocationUpsertComponent} from "../location-upsert/location-upsert.component";
import {ILocation} from "../../models/location.model";
import {BaseUpsertComponentV2} from "../../../../../../../base/components/base-upsert-v2.component";

@Component({
  selector: 'app-location-list',
  templateUrl: './location-list.component.html',
  styleUrls: ['./location-list.component.scss']
})
export class LocationListComponent extends BaseListComponentV2<ILocation> {
  protected readonly selfRoutes = selfRoutes;

  config: LocationConfig = defaultLocationConfig
  filterData: BasicFilterData = defaultFilterData

  locations: IPage<ILocation> = defaultPage

  protected componentName(): string {
    return "LocationListComponent";
  }

  constructor(eventBus: EventBusService,
              router: Router,
              subContainer: SubscriptionContainerService,
              authService: AuthService,
              dialog: DialogService<ILocation>,
              private activatedRoute: ActivatedRoute,
              private locationService: LocationService,
              private notifier: NotifierService,
  ) {
    super(eventBus, router, subContainer, dialog)
    if (authService.isAdmin()) {
      this.config.searchEndpoint = selfEndpoints.adminEndpoint
    }
  }

  initialize(): void {
    this.activatedRoute.queryParams.subscribe(params => {
      this.filterData = parseFilterData(params)
      console.log(toParams(this.filterData))
      this.fetchChildren(toParams(this.filterData))
    });
  }

  protected override getBreadcrumbItems(): BreadcrumbItem[] {
    return [
      {
         text: this.translations["Dashboard"],
        link: null,
        active: false
      },
      {
        text: "Locations",
        link: null,
        active: true
      }
    ]
  }

  private fetchChildren(params: IParams) {
    this.loading = true
    const key = this.componentKey(EventKey.LOCATIONS.SEARCH(params))
    this.locationService.searchChildren(key, this.config.searchEndpoint, params)
    this.subscribe(
      key,
      null,
      (value: Boolean, meta: MetaData) => {
        this.locations = meta.data
        this.loading = false
      })
    this.subscribe(this.getErrorKey(key), null, (value, meta) => {
      this.notifier.error('Error', meta.data.error.message)
    })
  }

  onDeleted(deleted: Boolean) {
    if (deleted) {
      this.notifier.success("Success", "Location is deleted")
      this.ngOnInit()
    }
  }

  override getConfig(): BaseConfig {
    return this.config
  }

  override getRoutes(): { create: string, update: (id: number) => string, params: IParams } | null {
    return {
      create: selfRoutes.create,
      update: (id: number) => selfRoutes.update(id),
      params: toParams(this.filterData)
    }
  }

  getUpsertComponent(): ComponentType<BaseUpsertComponentV2<ILocation>> | null {
    return LocationUpsertComponent
  }

  onTitleClick(item: ILocation) {
    this.navigateByUrl(selfRoutes.getChildren(item.id), then => {
      this.getMultipleByIds(item.absolute_path.split(':').map(id => parseInt(id)))
    })
  }

  private getMultipleByIds(ids: number[]) {
    this.loading = true
    const key = this.componentKey(EventKey.LOCATIONS.GET_MULTIPLE(ids))
    this.locationService.fetchMultiple(key, ids)
    this.subscribe(
      key,
      null,
      (value: Boolean, meta: MetaData) => {
        let locations = meta.data as ILocation[]
        let breadcrumbItems: BreadcrumbItem[] = [{
          text: "Locations",
          link: selfRoutes.list,
          active: false
        }]
        locations.map(location => {
          // map label to breadcrumb item
          breadcrumbItems.push({
            text: location.label,
            link: selfRoutes.getChildren(location.id),
            active: false
          })
        })
        this.breadcrumbItems = breadcrumbItems
        this.loading = false
      })
    this.subscribe(this.getErrorKey(key), null, (value, meta) => {
      this.notifier.error('Error', meta.data.error.message)
    })
  }

}
