import {Component} from '@angular/core';
import {IPage} from "../../../../../base/models/IPage";
import {IParams} from "../../../../../base/endpoints/iparams";
import {EventBusService} from "../../../../../services/eventbus/event-bus.service";
import {Router} from "@angular/router";
import {SubscriptionContainerService} from "../../../../../base/services/subscription-container.service";
import {NotificationService} from "../../../../common/modules/social/notifications/services/notification.service";
import {EventKey} from "../../../../../services/eventbus/event.key";
import {PageEvent} from "@angular/material/paginator";
import {BaseComponent} from "../../../../../base/components/base.component";
import {INotification, Priorities} from "../../../../common/modules/social/notifications/models/notification.model";
import {MatDialog} from "@angular/material/dialog";
import {StringUtil} from "../../../../../utils/string.util";
import {WebAppEndpoint} from "../../../../webapp/web-app.endpoint";

@Component({
  selector: 'app-notification-list',
  templateUrl: './notification-list.component.html',
  styleUrls: ['./notification-list.component.scss']
})
export class NotificationListComponent extends BaseComponent {
  protected readonly StringUtil = StringUtil;

  notifications: IPage<INotification> | null = null
  params: IParams = {
    page: 0
  }
  deleteurl: string = WebAppEndpoint.NOTIFICATIONS.DELETE();

  constructor(eventBus: EventBusService,
              router: Router,
              subContainer: SubscriptionContainerService,
              private notificationService: NotificationService,
              private dialog: MatDialog,
  ) {
    super(eventBus, router, subContainer)
  }

  protected componentName(): string {
    return NotificationListComponent.name;
  }

  ngOnInit(): void {
    this.fetchNotifications(
      {
        page: 0,
        user_id: null
      }
    )
  }

  private fetchNotifications(params: IParams) {
    this.loading = true
    let key = this.componentKey(EventKey.NOTIFICATIONS.SEARCH())
    this.notificationService.searchNotifications(key, params)
    this.subscribe(key, null, (value, meta) => {
      this.notifications = meta.data
      this.loading = false
    })
  }

  onNotificationItemClick(notification: INotification) {
    if (notification.redirect_url !== "") {
      this.router.navigateByUrl(notification.redirect_url)
    }
  }

  markRead(actionRead: { notificationId: number, read: boolean }) {
    let key = this.componentKey(EventKey.NOTIFICATIONS.MARK_READ())
    let countKey = this.globalKey(EventKey.NOTIFICATIONS.COUNT_UNREAD())
    this.notificationService.markRead(key, countKey, actionRead.notificationId, actionRead.read)
    this.subscribe(key, null, (value, meta) => {
      this.fetchNotifications(this.params)
    })
  }

  makeSticky(actionSticky: { sticky: boolean; notificationId: number }) {
    let key = this.componentKey(EventKey.NOTIFICATIONS.MAKE_STICKY())
    this.notificationService.makeSticky(key, actionSticky.notificationId, actionSticky.sticky)
    this.subscribe(key, null, (value, meta) => {
      this.fetchNotifications(this.params)
    })
  }

  typeBg(priority: string) {
    switch (priority) {
      case Priorities.IMMEDIATE:
        return 'badge bg-danger'
      case Priorities.URGENT:
        return 'badge bg-danger'
      case Priorities.HIGH:
        return 'badge bg-warning'
      case Priorities.NORMAL:
        return 'badge bg-primary'
      case Priorities.LOW:
        return 'badge bg-info'
      default:
        return 'badge bg-info'
    }
  }


  onChange($event: PageEvent) {

  }

  deleteEvent(deleted: Boolean) {
    if (deleted) {
      this.fetchNotifications(
        {
          page: 0,
          user_id: null
        }
      )
    }
  }

}
