import {Component} from '@angular/core';
import {StringUtil} from "../../../../../../utils/string.util";
import {IPage} from "../../../../../../base/models/IPage";
import {IPoll} from "../../models/ipoll";
import {NotifierService} from "../../../../../../services/notifier/notifier.service";
import {EventBusService} from "../../../../../../services/eventbus/event-bus.service";
import {ActivatedRoute, Router} from "@angular/router";
import {SubscriptionContainerService} from "../../../../../../base/services/subscription-container.service";
import {PollService} from "../../services/poll.service";
import * as moment from "moment/moment";
import {TimeUtility} from "../../../../../common/modules/utility/time.utility";
import {EventKey} from "../../../../../../services/eventbus/event.key";
import {IParams} from "../../../../../../base/endpoints/iparams";
import {MetaData} from "ng-event-bus/lib/meta-data";
import {PageEvent} from "@angular/material/paginator";
import {BaseComponent} from "../../../../../../base/components/base.component";
import {VoteService} from "../../services/vote.service";
import {IVote} from "../../models/vote.model";
import {WebAppEndpoint} from "../../../../../webapp/web-app.endpoint";
import {TextUtility} from "../../../../../common/modules/utility/text.utility";

@Component({
	selector: 'app-vote-list',
	templateUrl: './vote-list.component.html',
	styleUrls: ['./vote-list.component.scss']
})
export class VoteListComponent extends BaseComponent {

	size: number = 10;
	page: number = 1;
	votes: IPage<IVote> | undefined

	polls: IPoll[] = []

	created_from: Date = new Date()
	created_to: Date = new Date()
	username: string | null = null
	poll_id: number | null = null

	pollsLoading = false

	protected readonly StringUtil = StringUtil;

	constructor(private notifier: NotifierService,
				eventBus: EventBusService,
				router: Router,
				subContainer: SubscriptionContainerService,
				private voteService: VoteService,
				private activatedRoute: ActivatedRoute,
				private pollService: PollService) {
		super(eventBus, router, subContainer)
	}


	protected componentName(): string {
		return "VoteListComponent";
	}

	ngOnInit(): void {
		this.activatedRoute.queryParams.subscribe(params => {
			let from = params['created_from']
			let to = params['created_to']
			let username = params['username'] as string
			let poll_id = params['poll_id'] as number

			this.username = username != null && username.length > 0 ? username : null
			if (from != null && to != null) {
				this.created_from = moment(from).toDate();
				this.created_to = moment(to).toDate();
			} else {
				this.created_from = new Date(0)
				this.created_to = new Date()
			}
			this.poll_id = poll_id
		});

		this.fetchVotes({
			username: this.username,
			created_from: this.created_from == null ? this.created_from : TimeUtility.getDayStart(this.created_from),
			created_to: this.created_to == null ? this.created_to : TimeUtility.getDayEnd(this.created_to),
			poll_id: this.poll_id,
			page: 0, size: this.size,
		})
	}

	searchPollsForEvent(event: any) {
    let query = TextUtility.removeSpecialCharacters(event.target?.value)
		if (query.length > 2) {
			this.searchPolls(query)
		}
	}

	private searchPolls(query: string) {
		this.pollsLoading = true
		const key = this.componentKey(EventKey.POLLS.SEARCH())
		this.pollService.search(key, WebAppEndpoint.POLLS.SEARCH_ENDPOINT_ADMIN, {q: query})
		this.subscribe(key, null, (value: Boolean, meta: MetaData) => {
			this.polls = meta.data.content
			this.pollsLoading = false
		})
		this.subscribe(this.getErrorKey(key), null, (value, meta) => {
			this.notifier.log(meta.data)
		})
	}

	onFilterClick() {
		this.fetchVotes({
			username: this.username,
			created_from: TimeUtility.getDayStart(this.created_from),
			created_to: TimeUtility.getDayEnd(this.created_to),
			poll_id: this.poll_id,
			page: 0,
			size: this.size,
		})
	}

	onPollTitleClicked(v: IVote) {
		this.poll_id = v.poll_id
		this.onFilterClick()
	}

	private fetchVotes(params: IParams) {
		this.loading = true
		const key = this.componentKey(EventKey.VOTES.SEARCH())
		this.voteService.fetchVotes(key, params)
		this.subscribe(key, null, (value: Boolean, meta: MetaData) => {
			this.votes = meta.data
			this.loading = false
		})
		this.subscribe(this.getErrorKey(key), null, (value, meta) => {
			this.notifier.log(meta.data)
		})
	}

	onPageChanged(event: PageEvent) {
		this.fetchVotes({
			username: this.username,
			created_from: this.created_from,
			created_to: this.created_to,
			poll_id: this.poll_id,
			page: event.pageIndex,
			size: this.size,
		})
	}

	filterForToday() {
		this.created_from = TimeUtility.getDayStart(new Date())
		this.created_to = TimeUtility.getDayEnd(new Date())

		this.fetchVotes({
			username: this.username,
			created_from: this.created_from,
			created_to: this.created_to,
			poll_id: this.poll_id,
			page: 0,
			size: this.size,
		})
	}

	clearFilter() {
		this.username = null
		this.created_from = TimeUtility.getDayStart(new Date(0))
		this.created_to = TimeUtility.getDayEnd(new Date())
		this.fetchVotes({
			username: this.username,
			created_from: this.created_from,
			created_to: this.created_to,
			questionnaire_id: this.poll_id,
			page: 0,
			size: this.size,
		})
	}

}
