import {Component, EventEmitter, Inject, Output} from "@angular/core";
import {BaseUpsertComponent} from "../../../../../../../base/components/base-upsert.component";
import {defaultPOptionConfig, POptionConfig, selfRoutes} from "../../models/config.model";
import {FormControl, FormGroup, Validators} from "@angular/forms";
import {EventBusService} from "../../../../../../../services/eventbus/event-bus.service";
import {ActivatedRoute, Router} from "@angular/router";
import {SubscriptionContainerService} from "../../../../../../../base/services/subscription-container.service";
import {POptionService} from "../../services/poption.service";
import {NotifierService} from "../../../../../../../services/notifier/notifier.service";
import {BreadcrumbItem} from "../../../../../../common/modules/generic/breadcrumb/breadcrumb.model";
import {EventKey} from "../../../../../../../services/eventbus/event.key";
import {HttpErrorResponse, HttpStatusCode} from "@angular/common/http";
import {IImageSlice} from "../../../../../../common/modules/cms/files/models/file.model";
import {IPOption} from "../../models/poption.model";
import {MAT_DIALOG_DATA} from "@angular/material/dialog";
import {BaseConfig} from "../../../../../../../base/models/baseConfig";


@Component({
  selector: 'app-poption-upsert',
  templateUrl: './poption-upsert.component.html',
  styleUrls: ['./poption-upsert.component.scss']
})
export class POptionUpsertComponent extends BaseUpsertComponent {

  config: POptionConfig = defaultPOptionConfig

  pOptionForm = new FormGroup({
    name: new FormControl('', [Validators.required, Validators.minLength(4)]),
    hint: new FormControl('', ),
    image: new FormControl<string | null>(null)
  })

  @Output() onResult: EventEmitter<IPOption> = new EventEmitter<IPOption>()

  constructor(eventBus: EventBusService,
              router: Router,
              subContainer: SubscriptionContainerService,
              route: ActivatedRoute,
              private pOptionService: POptionService,
              private notifier: NotifierService,
              @Inject(MAT_DIALOG_DATA) dialogData: any) {
    super(eventBus, router, subContainer, route, dialogData)
    console.log(dialogData)
    this.pOptionForm.controls.name.setValue(dialogData?.name)
  }

  protected override componentName(): string {
    return "POptionUpsertComponent"
  }

  protected override getBreadcrumbItems(): BreadcrumbItem[] {
    return [
      {
         text: this.translations["Dashboard"],
        link: null,
        active: false
      },
      {
        text: "POptions",
        link: "/dash/poptions",
        active: false
      },
      {
        text: "Upsert",
        link: null,
        active: true
      }
    ]
  }

  override initialize(exId: number): void {
    this.loading = true
    let key = this.componentKey(EventKey.POLL_OPTIONS.GET(exId))
    this.pOptionService.fetchSingle(key, exId)
    this.subscribe(key, null, (value, meta) => {
      this.loading = false;
      this.populateForm(meta.data)
    })
    this.subscribe(this.getErrorKey(key), null, (value, meta) => {
      let error: HttpErrorResponse = meta.data
      console.log(error)
      this.loading = false;
    })
  }

  onSubmit() {
    if (!this.pOptionForm.valid) {
      return
    }
    this.upsert(this.exId)
  }

  images: IImageSlice[] = []

  onImgUploaded(images: IImageSlice[]) {
    if (images.length > 0) {
      /*
      Think about the thumbnail, this will only upload the image, not thumbnail
       */
      this.images = images
    }
  }

  getExImages(): IImageSlice[] {
    return this.images.map(img => {
      return {
        image_url: img.image_url,
        thumb_url: img.thumb_url
      }
    })
  }

  getConfig(): BaseConfig {
    return this.config;
  }

  getListPageRoute(): string {
    return selfRoutes.list;
  }

  /*
  - Add this to EventKey class and delete it afterward
     static POPTIONS = {
      UPSERT(exId: number | null): string {
        return 'poptions:upsert:' + exId
      },
      GET(exId: number): string {
        return 'poptions:get-single:' + exId
      }
    }
*/
  upsert(exId: number | null) {
    this.loading = true
    let key = this.componentKey(EventKey.POLL_OPTIONS.UPSERT(exId))
    // @ts-ignore
    this.pOptionService.upsert(key, this.pOptionForm.value, this.images, exId)
    this.subscribe(key, null, (value, meta) => {
      this.loading = false;
      let message = "POption has been " + (exId ? "updated" : "created")
      this.notifier.success("Success", message)
      this.pOptionForm.reset();

      if (this.config.upsertOnDialog) {
        this.onResult.emit(meta.data)
        this.onSuccess.emit()
      } else {
        this.navigate(selfRoutes.list, value => {
          this.notifier.success("Success!", message)
        })
      }
    })
    this.subscribe(this.getErrorKey(key), null, (value, meta) => {
      let error: HttpErrorResponse = meta.data
      this.loading = false;
      if (error.status == HttpStatusCode.Conflict) {
        this.onCanceled.emit()
        return
      }
      this.notifier.warning('Failed!', error.error.message)
    })
  }

  private populateForm(n: IPOption) {
    this.images = n.images.map(img => {
        return {
          image_url: img,
          thumb_url: img
        }
      }
    )
    this.pOptionForm.controls.name.setValue(n.name)
    this.pOptionForm.controls.hint.setValue(n.hint)
    this.pOptionForm.controls.image.setValue(n.images.length > 0 ? n.images[0] : null)
  }

}
