import {BaseConfig} from "../../../../../../base/models/baseConfig";
import {Layout} from "../../../../../common/modules/generic/breadcrumb/breadcrumb.model";
import {UserEndpoints} from "../../user.endpoint";

export interface UserConfig extends BaseConfig {
}

export let defaultUserConfig: UserConfig = {
  defaultLayout: Layout.GRID,
  upsertOnDialog: true,
  showUserFilter: false,
  showDeleteButton: true,
  searchEndpoint: UserEndpoints.USERS.USER_ENDPOINT,
  delEndpoint: UserEndpoints.USERS.USER_ENDPOINT
}

export let selfEndpoints = {
  endpoint: UserEndpoints.USERS.USER_ENDPOINT,
  adminEndpoint: UserEndpoints.USERS.USER_ENDPOINT_ADMIN,
}

export let selfRoutes = {
  list: "/dash/users",
  create: "/dash/users/create",
  update(id: number | null) {
    return id == null ? this.create : "/dash/users/" + id + '/update'
  }
}

