import {Component, EventEmitter, Inject, Input, OnInit, Output} from '@angular/core';
import {BaseComponent} from "../../../../../../base/components/base.component";
import {EventBusService} from "../../../../../../services/eventbus/event-bus.service";
import {Router} from "@angular/router";
import {SubscriptionContainerService} from "../../../../../../base/services/subscription-container.service";
import {NotifierService} from "../../../../../../services/notifier/notifier.service";
import {PollService} from "../../services/poll.service";
import {FormGroup} from "@angular/forms";
import {IPoll} from "../../models/ipoll";
import {IPollOption} from "../../models/ipoll-option";
import {EventKey} from "../../../../../../services/eventbus/event.key";
import {MetaData} from "ng-event-bus/lib/meta-data";
import {HttpErrorResponse} from "@angular/common/http";
import {FileUploadService} from "../../../../../../services/files/file-upload.service";
import {IUploadFile} from "../../../../../webapp/models/uploads/IUploadFile";
import {IImageUploadResponse} from "../../../../../../models/IImageUploadResponse";
import {MAT_DIALOG_DATA, MatDialog} from "@angular/material/dialog";
import {POptionUpsertComponent} from "../../poll-options/components/poption-upsert/poption-upsert.component";
import {IPOption} from "../../poll-options/models/poption.model";

@Component({
  selector: 'app-poll-edit-form',
  templateUrl: './poll-edit-form.component.html',
  styleUrls: ['./poll-edit-form.component.scss']
})
export class PollEditFormComponent extends BaseComponent implements OnInit {

  pollForm: FormGroup
  options: IPollOption[] = []
  selectedOptions: IPOption[] = []
  selectedOptionsTemp: IPOption[] = []
  correct_options: any = []
  @Input() exPoll: IPoll | undefined
  @Output() pollOptionLoaded: EventEmitter<boolean> = new EventEmitter<boolean>()
  @Output() formSubmitEvent: EventEmitter<boolean> = new EventEmitter<boolean>()
  uploadFiles: IUploadFile[] = []
  minStartDate: Date = new Date();
  maxStartDate: Date | undefined;
  minEndDate: Date = new Date();
  start_date: Date;
  end_date: Date;


  constructor(
    eventBus: EventBusService,
    router: Router,
    subContainer: SubscriptionContainerService,
    private fileUploadService: FileUploadService,
    @Inject(MAT_DIALOG_DATA) public data: any,
    private notifier: NotifierService,
    private pollService: PollService,
    private dialog: MatDialog
  ) {
    super(eventBus, router, subContainer)
    this.getPollDetails(data.id)

  }

  protected componentName(): string {
    return "PollEditFormComponent";
  }

  ngOnInit(): void {
    this.loading = false;
    this.pollForm = this.pollService.getPollEditForm(this.exPoll, this.data.questionnaireId)
  }

  createPoll(images: string[]) {
    let key = this.componentKey(EventKey.POLLS.CREATE(this.pollForm.value.name))
    this.pollForm.controls['options'].setValue(this.selectedOptionsTemp)
    if (this.pollForm.value.id) {
      this.pollService.updatePoll(key, this.pollForm.value, images)
    } else {
      this.pollService.createPoll(key, this.pollForm.value, images)
    }
    this.subscribe(key, null, (value, meta) => {
      this.notifier.success("Success", "Poll created")
      this.pollForm.reset();
      this.uploadFiles = []
      this.formSubmitEvent.emit(meta.data)
      this.loading = false;

    })
    this.subscribe(this.getErrorKey(key), null, (value, meta) => {
      let error: HttpErrorResponse = meta.data
      this.notifier.warning('Failed!', error.error.message)
    })

  }

  private getPollDetails(param: any) {
    if (param) {
      this.loading = true
      const key = this.componentKey(EventKey.POLLS.GET_DETAILS(param))
      this.pollService.getPollDetails(key, param)
      this.subscribe(
        key,
        null,
        (value: Boolean, meta: MetaData) => {
          this.populateData(meta.data)

        })
      this.subscribe(this.getErrorKey(key), null, (value, meta) => {
        this.notifier.error('Error', meta.data.error.message)
      })
    }
  }


  addOption(event: any) {
    const dialogRef = this.dialog.open(POptionUpsertComponent, {
      width: '600px',
      data: {
        id: null,
        name: event.name
      },
    })

    dialogRef.componentInstance.onResult.subscribe((result: IPOption) => {
      this.selectedOptionsTemp.push(result)
      this.correct_options.push(result.name)
      dialogRef.close()
    })

    dialogRef.componentInstance.onCanceled.subscribe(() => {
      dialogRef.close()
    })
  }

  onRemove(event: any) {
    const indexToDelete = this.selectedOptionsTemp.findIndex(item => item.name === event.value.name);
    const correctAnswers = this.pollForm.controls['correct_options'].getRawValue()
    const correctAnswerIndex = correctAnswers.indexOf(event.value.name)
    if (correctAnswerIndex > -1) {
      correctAnswers.splice(correctAnswerIndex, 1)
      this.pollForm.controls['correct_options'].setValue(correctAnswers)
    }

    if (indexToDelete !== -1) {
      this.selectedOptionsTemp.splice(indexToDelete, 1);
    }
  }

  removeImage(uf: IUploadFile) {
    let index = this.uploadFiles.map(file => file.previewUrl).indexOf(uf.previewUrl)
    this.uploadFiles.splice(index, 1)
  }

  onImageSelected(event: any) {
    if (event.target.files && event.target.files[0]) {
      // validate image
      const mimeType = event.target.files[0].type;
      if (mimeType.match(/image\/*/) == null) {
        this.notifier.warning("Unsupported", "Only images are supported.");
        return;
      }

      let uf: IUploadFile = {
        file: event.target.files[0],
        previewUrl: null
      }

      const reader = new FileReader();
      reader.readAsDataURL(event.target.files[0]);
      reader.onload = (e) => { // called once readAsDataURL is completed
        let url = e.target?.result
        if (url !== undefined && url !== null) {
          uf.previewUrl = url
          this.uploadFiles.push(uf);
        }
      }
    }
  }

  isInvalidFileCount(): boolean {
    return (this.uploadFiles.length === 0 && (this.pollForm.value.post?.images == null || this.pollForm.value.post?.images?.length === 0))
      || this.uploadFiles.length > 3
  }

  hasPreviousImage(): boolean {
    return this.pollForm.value.post?.images?.length > 0
  }

  getImage() {
    return this.pollForm.value.post.images[0]
  }

  uploadImages(): void {
    this.loading = true

    if (this.uploadFiles.length > 0) {
      let files: File[] = []
      this.uploadFiles.forEach(uf => {
        files.push(uf.file)
      });
      let key = this.componentKey(EventKey.FILES.IMAGE_UPLOAD(this.pollForm.value.name + '-verif-img'))
      this.fileUploadService.uploadImages(key, files)
      this.subscribe(
        key,
        null,
        (value, meta) => {
          let imageResponses = <IImageUploadResponse[]>meta.data
          let images = imageResponses.map(r => r.thumb_url)
          this.createPoll(images)
        }
      );
    } else this.createPoll([])
  }

  selectStartDate() {
    if (this.start_date != null) {
      this.minEndDate = this.start_date;
      this.pollForm.controls['active_from'].setValue(new Date(this.start_date));
    } else {
      this.minEndDate = new Date();
      this.pollForm.controls['active_from'].setValue(null);
    }
  }

  selectEndDate() {
    if (this.end_date != null) {
      this.maxStartDate = this.end_date;
      this.pollForm.controls['active_until'].setValue(new Date(this.end_date));
    } else {
      this.maxStartDate = undefined;
      this.pollForm.controls['active_until'].setValue(null);
    }
  }


  private populateData(data: any) {
    this.exPoll = data

    if (this.exPoll?.options) {
      this.options = this.exPoll.options
      this.selectedOptions = this.exPoll.options
      this.selectedOptionsTemp = this.exPoll.options
    }
    this.pollForm = this.pollService.getPollEditForm(this.exPoll, this.data.questionnaireId)
    if (data.active_until)
      this.end_date = new Date(data.active_until)
    if (data.active_from) {
      this.start_date = new Date(data.active_from)
    }
    this.loading = false
  }

}
