import {Component, Inject, Input, OnInit} from '@angular/core';
import {ILabel} from "../../models/ilabel";
import {LabelTreeService} from "../../services/label-tree.service";
import {EventKey} from "../../../../../../services/eventbus/event.key";
import {MetaData} from "ng-event-bus/lib/meta-data";
import {BaseComponent} from "../../../../../../base/components/base.component";
import {EventBusService} from "../../../../../../services/eventbus/event-bus.service";
import {Router} from "@angular/router";
import {SubscriptionContainerService} from "../../../../../../base/services/subscription-container.service";
import {FileUploadService} from "../../../../../../services/files/file-upload.service";
import {MAT_DIALOG_DATA, MatDialog, MatDialogRef} from "@angular/material/dialog";
import {NotifierService} from "../../../../../../services/notifier/notifier.service";
import {IParams} from "../../../../../../base/endpoints/iparams";
import {Subject, takeUntil} from "rxjs";
import {PollEditFormComponent} from "../../../poll/components/poll-edit-form/poll-edit-form.component";
import {LabelUpdateComponent} from "../../dialogues/label-update/label-update.component";

@Component({
  selector: 'app-label-tree',
  templateUrl: './label-tree.component.html',
  styleUrls: ['./label-tree.component.scss']
})
export class LabelTreeComponent extends BaseComponent implements OnInit {
  @Input() parentId: number;

  labels: ILabel[];
  secondaryParent: number
  private onDestroy$ = new Subject<void>();

  protected componentName(): string {
    return "LabelTreeComponent";
  }

  constructor(
    eventBus: EventBusService,
    router: Router,
    subContainer: SubscriptionContainerService,
    private labelTreeService: LabelTreeService,
    private dialog: MatDialog,
    private notifier: NotifierService,
  ) {
    super(eventBus, router, subContainer)
  }

  ngOnInit(): void {
    if (this.parentId) {
      this.getLabels({parent_id: this.parentId})
    }
  }

  private getLabels(params: IParams) {
    this.loading = true
    const key = this.componentKey(EventKey.LABELS_LEGACY.GET_LABEL_TREE(this.parentId))
    this.labelTreeService.getLabelTree(key, params)
    this.subscribe(
      key,
      null,
      (value: Boolean, meta: MetaData) => {
        this.labels = meta.data.content
        this.loading = false
      })
    this.subscribe(this.getErrorKey(key), null, (value, meta) => {
      this.notifier.error('Error', meta.data.error.message)
      this.loading = false

    })
  }

  isNodeExpanded(nodeId: number): boolean {
    return this.labelTreeService.expandedNodesSubject.value.includes(nodeId);
  }

  onNodeClick(nodeId: number) {
    this.labelTreeService.setExpandedNodes([...this.labelTreeService.expandedNodesSubject.value, nodeId]);
  }

  override ngOnDestroy() {
    this.onDestroy$.next();
    this.onDestroy$.complete();
  }

  editNode(label: ILabel) {
    const dialogRef = this.dialog.open(LabelUpdateComponent, {
      width:"300px",
      data: label,
    })

    let instance = dialogRef.componentInstance
    instance.formSubmitEvent.subscribe((v: boolean) => {
      dialogRef.close()
    })

    dialogRef.afterClosed().subscribe(result => {
      this.reloadRoute()
    })
  }
}
