import {Component, EventEmitter, Input, Output} from '@angular/core';
import {BaseComponent} from "../../../../../../base/components/base.component";
import {NotifierService} from "../../../../../../services/notifier/notifier.service";
import {EventBusService} from "../../../../../../services/eventbus/event-bus.service";
import {ActivatedRoute, Router} from "@angular/router";
import {SubscriptionContainerService} from "../../../../../../base/services/subscription-container.service";
import {IParams} from "../../../../../../base/endpoints/iparams";
import {QuestionService} from "../../services/question.service";
import {EventKey} from "../../../../../../services/eventbus/event.key";
import {MetaData} from "ng-event-bus/lib/meta-data";
import {IQuestionnaire} from "../../models/IQuestionnaire";
import {defaultPage, IPage} from "../../../../../../base/models/IPage";
import {PageEvent} from "@angular/material/paginator";
import {ILabel} from "../../../labels-legacy/models/ilabel";
import {TimeUtility} from "../../../../../common/modules/utility/time.utility";
import {QuestionnaireConfig} from "../../models/config";
import {WebAppEndpoint} from "../../../../../webapp/web-app.endpoint";
import {StringUtil} from "../../../../../../utils/string.util";
import {BreadcrumbItem, Layout} from "../../../../../common/modules/generic/breadcrumb/breadcrumb.model";
import {slideIn} from "../../../../../../base/animations/component.animation";
import {query} from "@angular/animations";

@Component({
  selector: 'app-questionnaire-list',
  templateUrl: './questionnaire-list.component.html',
  styleUrls: ['./questionnaire-list.component.scss'],
  animations: [
    slideIn('onGrid', 'left'),
    slideIn('onColumn', 'right')
  ]
})
export class QuestionnaireListComponent extends BaseComponent {

  @Input() config: QuestionnaireConfig = {
    showUserFilter: false,
    showApprovalButton: false,
    showDeleteButton: false,
    labelsEndpoint: WebAppEndpoint.LABELS.SEARCH_ENDPOINT_FOR_POLLBOX,
    endpoint: WebAppEndpoint.QUESTIONNAIRES.SEARCH_ENDPOINT
  }

  size = 10;
  page: number = 0;
  questionnaires: IPage<IQuestionnaire> = defaultPage
  @Output() questionLoaded: EventEmitter<boolean> = new EventEmitter<boolean>()
  labels: ILabel[] = []

  query: string | null = null
  created_from: Date = new Date(0)
  created_to: Date = new Date()
  username: string | null = null
  isApproved: Boolean | null = null
  labelId: number | null = null
  deleteurl: string = WebAppEndpoint.QUESTIONNAIRES.DELETE();

  breadcrumbItems: BreadcrumbItem[] = []
  layout: Layout = Layout.GRID

  constructor(private notifier: NotifierService,
              eventBus: EventBusService,
              router: Router,
              subContainer: SubscriptionContainerService,
              private questionService: QuestionService,
              private activatedRoute: ActivatedRoute) {
    super(eventBus, router, subContainer)
    this.breadcrumbItems = [
      {
        text: "Pollbox",
        link: null,
        active: false
      },
      {
        text: "Questionnaires",
        link: "/dash/questionnaires",
        active: true
      }
    ]
  }

  onLayoutSelected(layout: Layout) {
    this.layout = layout
  }

  onFilterClick() {
    this.fetchQuestionnaires({
      a: this.query,
      created_by: this.username,
      created_from: TimeUtility.getDayStart(this.created_from),
      created_to: TimeUtility.getDayEnd(this.created_to),
      label_id: this.labelId,
      approved: this.isApproved,
      page: this.page,
      size: this.size,
    })
  }

  clearFilter() {
    this.username = null
    this.isApproved = null
    this.created_from = TimeUtility.getDayStart(new Date(0))
    this.created_to = TimeUtility.getDayEnd(new Date())
    this.fetchQuestionnaires({
      a: this.query,
      created_by: this.username,
      created_from: this.created_from,
      created_to: this.created_to,
      label_id: this.labelId,
      page: 0,
      size: this.size,
      // questionnaire_id: this.questionnaireId
    })
  }

  filterForToday() {
    this.created_from = TimeUtility.getDayStart(new Date())
    this.created_to = TimeUtility.getDayEnd(new Date())

    this.fetchQuestionnaires({
      a: this.query,
      created_by: this.username,
      created_from: this.created_from,
      created_to: this.created_to,
      label_id: this.labelId,
      page: 0,
      size: this.size,
      // questionnaire_id: this.questionnaireId
    })
  }

  typeBg(type: string) {
    if (type == 'USER_CREATED')
      return 'badge bg-success'
    if (type == 'ADMIN_MANAGED')
      return 'badge bg-info'
    else
      return 'badge bg-primary'
  }

  protected componentName(): string {
    return "QuestionsListComponent";
  }

  ngOnInit(): void {
    this.activatedRoute.queryParams.subscribe(params => {
      this.query = params['query']
      let from = params['from_date']
      let to = params['to_date']
      let page = params['page'] ? params['page'] : 0
      let size = params['size'] ? params['size'] : 0
      let username = params['username'] as string
      let label = params['label_id'] as string

      this.page = params['page'] ? params['page'] : 0
      this.fetchQuestionnaires({
        q: this.query,
        created_from: from,
        created_to: to,
        username: username,
        page: page,
        size: size,
        label_id: label
      })
    })

  }


  private fetchQuestionnaires(params: IParams) {
    this.loading = true
    const key = this.componentKey(EventKey.QUESTIONNAIRES.SEARCH())
    this.questionService.fetchQuestionnaires(key, this.config.endpoint, params)
    this.subscribe(
      key,
      null,
      (value: Boolean, meta: MetaData) => {
        this.questionnaires = meta.data
        this.loading = false
        this.questionLoaded.emit(true)
      })
    this.subscribe(this.getErrorKey(key), null, (value, meta) => {
      this.notifier.error('Error', meta.data.error.message)
    })
  }

  onChange(event: PageEvent) {
    this.page = event.pageIndex
    this.router.navigate(['.'], {
      relativeTo: this.activatedRoute,
      queryParams: {
        page: event.pageIndex
      },
      queryParamsHandling: 'merge',
    }).then(r => {
    });
  }


  toggleApproval(questionnaireId: number, approved: boolean) {
    this.loading = true
    const key = this.componentKey(EventKey.QUESTIONNAIRES.TOGGLE_APPROVAL(questionnaireId))
    this.questionService.toggleApproval(key, questionnaireId, {
      approved: approved
    })
    this.subscribe(key, null, (value: Boolean, meta: MetaData) => {
      this.loading = false
      this.onFilterClick()
    })
    this.subscribe(this.getErrorKey(key), null, (value, meta) => {
      this.notifier.error('Error', meta.data.error.message)
    })
  }

  deleteEvent(deleted: Boolean) {
    if (deleted) {
      this.onFilterClick()
    }
  }

  protected readonly StringUtil = StringUtil;
  protected readonly Layout = Layout;
}
