import {Component, Inject} from '@angular/core';
import {FormControl, FormGroup, Validators} from "@angular/forms";
import {EventBusService} from "../../../../../../services/eventbus/event-bus.service";
import {ActivatedRoute, Router} from "@angular/router";
import {SubscriptionContainerService} from "../../../../../../base/services/subscription-container.service";
import {OrganizationService} from "../../services/organization.service";
import {NotifierService} from "../../../../../../services/notifier/notifier.service";
import {OrganizationConfig, defaultOrganizationConfig, selfRoutes, selfEndpoints} from "../../models/config.model";
import {BreadcrumbItem} from "../../../../../common/modules/generic/breadcrumb/breadcrumb.model";
import {IOrganization, IOrganizationDetail, IOrganizationReq} from "../../models/organization.model";
import {EventKey} from "../../../../../../services/eventbus/event.key";
import {HttpErrorResponse} from "@angular/common/http";
import {MAT_DIALOG_DATA} from "@angular/material/dialog";
import {BaseConfig} from "../../../../../../base/models/baseConfig";
import {IImageSlice} from "../../../../../common/modules/cms/files/models/file.model";
import {BaseUpsertComponentV2} from "../../../../../../base/components/base-upsert-v2.component";
import {AuthService} from "../../../../../common/modules/auth/services/auth.service";
import {OrganizationComponent} from "../../organization.component";
import {IUserBrief} from "../../../users/users/models/user.model";

@Component({
  selector: 'app-organization-upsert',
  templateUrl: './organization-upsert.component.html',
  styleUrls: ['./organization-upsert.component.scss']
})
export class OrganizationUpsertComponent extends BaseUpsertComponentV2<IOrganizationDetail> {

  config: OrganizationConfig = defaultOrganizationConfig
  endpoint = selfEndpoints.endpoint

  selectedUser: IUserBrief | null = null

  organizationForm = new FormGroup({
    logo: new FormControl<string | null>(null),
    name: new FormControl('', [Validators.required, Validators.minLength(3)]),
    email: new FormControl<string | null>(null, [Validators.email, Validators.maxLength(100)]),
    description: new FormControl<string>(""),
    type: new FormControl('', [Validators.required]),
    active: new FormControl<boolean>(false),
    owner_id: new FormControl<number | null>(null, [Validators.required]),
  })

  constructor(eventBus: EventBusService,
              router: Router,
              subContainer: SubscriptionContainerService,
              route: ActivatedRoute,
              private organizationService: OrganizationService,
              private notifier: NotifierService,
              auth: AuthService,
              @Inject(MAT_DIALOG_DATA) dialogData: any) {
    super(eventBus, router, subContainer, route, dialogData)

    if (dialogData.name != null) {
      this.organizationForm.controls.name.setValue(dialogData.name)
    }

    if (auth.isAdmin()) {
      this.endpoint = selfEndpoints.adminEndpoint
    }

  }

  protected override componentName(): string {
    return OrganizationComponent.name
  }

  protected override getBreadcrumbItems(): BreadcrumbItem[] {
    return [
      {
        text: this.translations["Dashboard"],
        link: null,
        active: false
      },
      {
        text: "Organizations",
        link: "/dash/organizations",
        active: false
      },
      {
        text: "Upsert",
        link: null,
        active: true
      }
    ]
  }

  override initialize(exId: number): void {
    this.loading = true
    let key = this.componentKey(EventKey.ORGANIZATIONS.GET(exId))
    this.organizationService.fetchSingle(key, this.endpoint, exId)
    this.subscribe(key, null, (value, meta) => {
      this.loading = false;
      this.populateForm(meta.data)
    })
    this.subscribe(this.getErrorKey(key), null, (value, meta) => {
      let error: HttpErrorResponse = meta.data
      console.log(error)
      this.loading = false;
    })
  }

  onSubmit() {
    if (!this.organizationForm.valid) {
      return
    }
    this.upsert(this.exId)
  }

  onImgUploaded(images: IImageSlice[]) {
    if (images.length > 0) {
      /*
      Think about the thumbnail, this will only upload the image, not thumbnail
       */
      this.organizationForm.controls.logo.setValue(images[0].image_url)
    }
  }

  getExImages(): IImageSlice[] {
    let image = this.organizationForm.controls.logo.value
    if (image == null) {
      return []
    }
    return [{image_url: image, thumb_url: image}]
  }

  getConfig(): BaseConfig {
    return this.config;
  }

  getListPageRoute(): string {
    return selfRoutes.list;
  }

  upsert(exId: number | null) {
    this.loading = true
    let key = this.componentKey(EventKey.ORGANIZATIONS.UPSERT(exId))
    let req = this.organizationForm.value as IOrganizationReq
    this.organizationService.upsert(key, req, exId)
    this.subscribe(key, null, (value, meta) => {
      this.notifier.success("Success", "Organization has been " + (exId ? "updated" : "created"))
      this.organizationForm.reset();
      this.onSuccess(meta.data)
    })
    this.subscribe(this.getErrorKey(key), null, (value, meta) => {
      let error: HttpErrorResponse = meta.data
      this.loading = false;
      this.notifier.warning('Failed!', error.error.message)
    })
  }

  private populateForm(n: IOrganization) {
    this.selectedUser = n.owner

    this.organizationForm.controls.logo.setValue(n.logo)
    this.organizationForm.controls.name.setValue(n.name)
    this.organizationForm.controls.description.setValue(n.description)
    this.organizationForm.controls.type.setValue(n.type)
    this.organizationForm.controls.active.setValue(n.active)
    this.organizationForm.controls.owner_id.setValue(n.owner.id)
    this.organizationForm.controls.email.setValue(n.email)
  }

  onUserSelected($event: IUserBrief | null) {
    this.selectedUser = $event
    this.organizationForm.controls.owner_id.setValue(
      this.selectedUser == null ? null : this.selectedUser.id
    )
  }
}
