import {Component, Input, OnChanges, ViewChild} from '@angular/core';
import {ApexChart, ApexNonAxisChartSeries, ApexResponsive, ChartComponent} from "ng-apexcharts";
import {BaseComponent} from "../../../../../../base/components/base.component";
import {EventBusService} from "../../../../../../services/eventbus/event-bus.service";
import {Router} from "@angular/router";
import {SubscriptionContainerService} from "../../../../../../base/services/subscription-container.service";
import {defaultSalesOverview, ISalesOverview} from "../models/stats.model";
import {translations} from "../../../../layout/left-nav/menu.model";
import {Utility} from "../../../../../common/modules/utility/utility";

export type ChartOptions = {
  series: ApexNonAxisChartSeries;
  chart: ApexChart;
  responsive: ApexResponsive[];
  labels: any;
};

@Component({
  selector: 'app-payment-pi-chart',
  templateUrl: './payment-pi-chart.component.html',
  styleUrls: ['./payment-pi-chart.component.scss']
})
export class PaymentPiChartComponent extends BaseComponent implements OnChanges {
  @ViewChild("chart") chart: ChartComponent;

  chartOptions: Partial<ChartOptions> = defaultOptions;

  @Input() data: ISalesOverview = defaultSalesOverview

  constructor(eventBus: EventBusService,
              router: Router,
              subContainer: SubscriptionContainerService
  ) {
    super(eventBus, router, subContainer)
    this.chartOptions = defaultOptions
  }

  protected componentName(): string {
    return PaymentPiChartComponent.name;
  }

  ngOnInit(): void {
    console.log(this.data)
  }

  ngOnChanges(): void {
    let totalDue = Utility.toFloat(this.data.total_due, 2)
    let totalPaid = Utility.toFloat(this.data.total_revenue - this.data.total_due, 2)
    this.chartOptions = {
      series: [totalDue, totalPaid],
      chart: {
        type: "donut"
      },
      labels: [
        translations["Total Due"] + "<br/>(" + translations['$'] + totalDue + ")",
        translations["Total Paid"] + "<br/>(" + translations['$'] + totalPaid + ")"
      ],
      responsive: [
        {
          breakpoint: 480,
          options: {
            chart: {
              width: 200
            },
            legend: {
              position: "bottom"
            }
          }
        }
      ]
    }
  }

}


const defaultOptions: Partial<ChartOptions> = {
  series: [0, 0],
  chart: {
    type: "donut"
  },
  labels: ["Due", "Paid"],
  responsive: [
    {
      breakpoint: 480,
      options: {
        chart: {
          width: 200
        },
        legend: {
          position: "bottom"
        }
      }
    }
  ]
}
