import {BaseService} from "../../../../../../base/services/base.service";
import {AuthService} from "../../../../../common/modules/auth/services/auth.service";
import {NotifierService} from "../../../../../../services/notifier/notifier.service";
import {HttpClient, HttpErrorResponse} from "@angular/common/http";
import {EventBusService} from "../../../../../../services/eventbus/event-bus.service";
import {IParams} from "../../../../../../base/endpoints/iparams";
import {IAuth} from "../../../../../common/modules/auth/models/auth.model";
import {SalesEndpoints} from "../../../../domains/sales/sales.endpoints";
import {
  IMostDueCustomer,
  IMostPayingCustomer,
  ILowStock,
  IMostFrequentCustomer,
  ISalesOverview,
  ITopBuyingCustomer,
  ITopProduct, IBuySellItem
} from "../models/stats.model";
import {Injectable} from "@angular/core";

@Injectable({
  providedIn: 'root'
})
export class SalesStatisticsService extends BaseService {

  constructor(
    authService: AuthService,
    notifier: NotifierService,
    private http: HttpClient,
    private eventBusService: EventBusService
  ) {
    super(authService, notifier)
  }

  overview(key: string, params: IParams) {
    let url = SalesEndpoints.STATISTICS.OVERVIEW_STATS(params)
    this.http.get<ISalesOverview>(url)
      .subscribe((overview: ISalesOverview) => {
        this.eventBusService.publish(key, overview)
      }, error => {
        this.handleError(error, (auth: IAuth) => {
          this.overview(key, params)
        }, (err: HttpErrorResponse) => {
          this.eventBusService.publish(this.getErrorKey(key), err)
        })
      })
  }

  topBuyingCustomers(key: string, params: IParams) {
    let url = SalesEndpoints.STATISTICS.TOP_BUYING_CUSTOMERS(params)
    this.http.get<ITopBuyingCustomer[]>(url)
      .subscribe((topCustomers: ITopBuyingCustomer[]) => {
        this.eventBusService.publish(key, topCustomers)
      }, error => {
        this.handleError(error, (auth: IAuth) => {
          this.topBuyingCustomers(key, params)
        }, (err: HttpErrorResponse) => {
          this.eventBusService.publish(this.getErrorKey(key), err)
        })
      })
  }

  mostFrequentCustomers(key: string, params: IParams) {
    let url = SalesEndpoints.STATISTICS.MOST_FREQUENT_CUSTOMERS(params)
    this.http.get<IMostFrequentCustomer[]>(url)
      .subscribe((topCustomers: IMostFrequentCustomer[]) => {
        this.eventBusService.publish(key, topCustomers)
      }, error => {
        this.handleError(error, (auth: IAuth) => {
          this.mostFrequentCustomers(key, params)
        }, (err: HttpErrorResponse) => {
          this.eventBusService.publish(this.getErrorKey(key), err)
        })
      })
  }

  mostPayingCustomers(key: string, params: IParams) {
    let url = SalesEndpoints.STATISTICS.MOST_PAYING(params)
    this.http.get<IMostPayingCustomer[]>(url)
      .subscribe((topCustomers: IMostPayingCustomer[]) => {
        this.eventBusService.publish(key, topCustomers)
      }, error => {
        this.handleError(error, (auth: IAuth) => {
          this.mostPayingCustomers(key, params)
        }, (err: HttpErrorResponse) => {
          this.eventBusService.publish(this.getErrorKey(key), err)
        })
      })
  }

  mostDueCustomers(key: string, params: IParams) {
    let url = SalesEndpoints.STATISTICS.MOST_DUE(params)
    this.http.get<IMostDueCustomer[]>(url)
      .subscribe((topCustomers: IMostDueCustomer[]) => {
        this.eventBusService.publish(key, topCustomers)
      }, error => {
        this.handleError(error, (auth: IAuth) => {
          this.mostDueCustomers(key, params)
        }, (err: HttpErrorResponse) => {
          this.eventBusService.publish(this.getErrorKey(key), err)
        })
      })
  }

  lowStocks(key: string, params: IParams) {
    let url = SalesEndpoints.STATISTICS.LOW_STOCK_STATS(params)
    this.http.get<ILowStock[]>(url)
      .subscribe((lowStocks: ILowStock[]) => {
        this.eventBusService.publish(key, lowStocks)
      }, error => {
        this.handleError(error, (auth: IAuth) => {
          this.lowStocks(key, params)
        }, (err: HttpErrorResponse) => {
          this.eventBusService.publish(this.getErrorKey(key), err)
        })
      })
  }

  mostProfitableProducts(key: string, params: IParams) {
    let url = SalesEndpoints.STATISTICS.MOST_PROFITABLE_PRODUCTS(params)
    this.http.get<ITopProduct[]>(url)
      .subscribe((topProducts: ITopProduct[]) => {
        this.eventBusService.publish(key, topProducts)
      }, error => {
        this.handleError(error, (auth: IAuth) => {
          this.mostProfitableProducts(key, params)
        }, (err: HttpErrorResponse) => {
          this.eventBusService.publish(this.getErrorKey(key), err)
        })
      })
  }

  buyingSellingPriceStat(key: string, params: IParams) {
    let url = SalesEndpoints.STATISTICS.BUYING_SELLING_PRICE(params)
    this.http.get<IBuySellItem[]>(url)
      .subscribe((items: IBuySellItem[]) => {
        this.eventBusService.publish(key, items)
      }, error => {
        this.handleError(error, (auth: IAuth) => {
          this.mostProfitableProducts(key, params)
        }, (err: HttpErrorResponse) => {
          this.eventBusService.publish(this.getErrorKey(key), err)
        })
      })
  }




}
