import {Component, Inject} from "@angular/core";
import {BaseUpsertComponentV2} from "../../../../../../../base/components/base-upsert-v2.component";
import {defaultLocationConfig, LocationConfig, selfRoutes} from "../../models/config.model";
import {FormControl, FormGroup, Validators} from "@angular/forms";
import {EventBusService} from "../../../../../../../services/eventbus/event-bus.service";
import {ActivatedRoute, Router} from "@angular/router";
import {SubscriptionContainerService} from "../../../../../../../base/services/subscription-container.service";
import {LocationService} from "../../services/location.service";
import {NotifierService} from "../../../../../../../services/notifier/notifier.service";
import {BreadcrumbItem} from "../../../../../../common/modules/generic/breadcrumb/breadcrumb.model";
import {EventKey} from "../../../../../../../services/eventbus/event.key";
import {HttpErrorResponse} from "@angular/common/http";
import {IImageSlice} from "../../../../../../common/modules/cms/files/models/file.model";
import {BaseConfig} from "../../../../../../../base/models/baseConfig";
import {ILocation} from "../../models/location.model";
import {MAT_DIALOG_DATA} from "@angular/material/dialog";
import {ILocationType} from "../../../locationtypes/models/locationtype.model";
import {TextUtility} from "../../../../../../common/modules/utility/text.utility";
import {IPage} from "../../../../../../../base/models/IPage";

@Component({
  selector: 'app-location-upsert',
  templateUrl: './location-upsert.component.html',
  styleUrls: ['./location-upsert.component.scss']
})
export class LocationUpsertComponent extends BaseUpsertComponentV2<ILocation> {

  config: LocationConfig = defaultLocationConfig

  selectedLocationType: ILocationType | null = null

  parentLocations: ILocation[] = []

  locationForm = new FormGroup({
    label: new FormControl('', [Validators.required, Validators.minLength(3)]),
    code: new FormControl('', [Validators.required, Validators.minLength(3)]),
    parent_id: new FormControl<number | null>(null, []),
    type_id: new FormControl<number | null>(null, [Validators.required, Validators.min(1)]),
    description: new FormControl('', [Validators.required, Validators.minLength(20)]),
    image: new FormControl<string | null>(null),
    latitude: new FormControl<number>(0, [Validators.required]),
    longitude: new FormControl<number>(0, [Validators.required]),
    altitude: new FormControl<number>(0, [Validators.required]),
  })

  constructor(eventBus: EventBusService,
              router: Router,
              subContainer: SubscriptionContainerService,
              route: ActivatedRoute,
              private locationService: LocationService,
              private notifier: NotifierService,
              @Inject(MAT_DIALOG_DATA) dialogData: any) {
    super(eventBus, router, subContainer, route, dialogData)

    if (dialogData.title != null) {
      this.locationForm.controls.label.setValue(dialogData.title)
    }

  }

  protected override componentName(): string {
    return "LocationUpsertComponent"
  }

  protected override getBreadcrumbItems(): BreadcrumbItem[] {
    return [
      {
         text: this.translations["Dashboard"],
        link: null,
        active: false
      },
      {
        text: "Locations",
        link: "/dash/locations",
        active: false
      },
      {
        text: "Upsert",
        link: null,
        active: true
      }
    ]
  }

  override ngOnInit() {
    super.ngOnInit();
    this.locationForm.controls.label.valueChanges.subscribe(value => {
      this.locationForm.controls.code.setValue(TextUtility.codify(value))
    })

    this.locationForm.controls.parent_id.setValue(null)
    this.locationForm.controls.parent_id.disable()
  }

  override initialize(exId: number): void {
    this.loading = true
    let key = this.componentKey(EventKey.LOCATIONS.GET(exId))
    this.locationService.fetchSingle(key, exId)
    this.subscribe(key, null, (value, meta) => {
      this.loading = false;
      this.populateForm(meta.data)
    })
    this.subscribe(this.getErrorKey(key), null, (value, meta) => {
      let error: HttpErrorResponse = meta.data
      console.log(error)
      this.loading = false;
    })
  }

  onSubmit() {
    if (!this.locationForm.valid) {
      return
    }
    this.upsert(this.exId)
  }

  onImgUploaded(images: IImageSlice[]) {
    if (images.length > 0) {
      /*
      Think about the thumbnail, this will only upload the image, not thumbnail
       */
      this.locationForm.controls.image.setValue(images[0].image_url)
    }
  }

  getExImages(): IImageSlice[] {
    let image = this.locationForm.controls.image.value
    if (image == null) {
      return []
    }
    return [{image_url: image, thumb_url: image}]
  }

  getConfig(): BaseConfig {
    return this.config;
  }

  getListPageRoute(): string {
    return selfRoutes.list;
  }

  upsert(exId: number | null) {
    this.loading = true
    let key = this.componentKey(EventKey.LOCATIONS.UPSERT(exId))
    // @ts-ignore
    this.locationService.upsert(key, this.locationForm.value, exId)
    this.subscribe(key, null, (value, meta) => {
      this.loading = false;
      let message = "Location has been " + (exId ? "updated" : "created")
      this.notifier.success("Success", message)
      this.locationForm.reset();

      if (this.config.upsertOnDialog) {
        this.onCompleted.emit(meta.data)
      } else {
        this.navigateToListPage()
      }
    })
    this.subscribe(this.getErrorKey(key), null, (value, meta) => {
      let error: HttpErrorResponse = meta.data
      this.loading = false;
      this.notifier.warning('Failed!', error.error.message)
    })
  }

  private populateForm(n: ILocation) {
    this.locationForm.controls.label.setValue(n.label)
    this.locationForm.controls.description.setValue(n.description)
    this.locationForm.controls.image.setValue(n.image == null ? null : n.image)

    this.locationForm.controls.code.setValue(n.code)
    this.locationForm.controls.parent_id.setValue(n.parent_id)
    this.locationForm.controls.type_id.setValue(n.type_id)
    this.locationForm.controls.latitude.setValue(n.latitude)
    this.locationForm.controls.longitude.setValue(n.longitude)
    this.locationForm.controls.altitude.setValue(n.altitude)
  }

  onTypeSelected(type: ILocationType | null) {
    this.selectedLocationType = type
    this.locationForm.controls.type_id.setValue(
      type == null ? null : type.id
    )
    if (type != null) {
      if (type.parent_id == null) {
        this.locationForm.controls.parent_id.setValue(null)
        this.locationForm.controls.parent_id.disable()
      } else {
        this.locationForm.controls.parent_id.setValidators([Validators.required])
        this.loadLocationsForType(type.parent_id)
      }
    }
  }


  loadLocationsForType(typeId: number) {
    this.loading = true
    let key = this.componentKey(EventKey.LOCATIONS.GET_BY_TYPE(typeId))

    this.locationService.getLocationsForType(key, this.config.getByTypeEndpoint, typeId)

    this.subscribe(key, null, (value, meta) => {
      this.loading = false;
      let locations = meta.data as IPage<ILocation>
      this.parentLocations = locations.content
      this.locationForm.controls.parent_id.enable()
    })
    this.subscribe(this.getErrorKey(key), null, (value, meta) => {
      let error: HttpErrorResponse = meta.data
      this.loading = false;
      this.notifier.warning('Failed!', error.error.message)
    })
  }
}
