import {Component, EventEmitter, Input, Output} from '@angular/core';
import {BaseComponent} from "../../../../../../base/components/base.component";
import {defaultPage, IPage} from "../../../../../../base/models/IPage";
import {NotifierService} from "../../../../../../services/notifier/notifier.service";
import {EventBusService} from "../../../../../../services/eventbus/event-bus.service";
import {ActivatedRoute, Router} from "@angular/router";
import {SubscriptionContainerService} from "../../../../../../base/services/subscription-container.service";
import {IParams} from "../../../../../../base/endpoints/iparams";
import {EventKey} from "../../../../../../services/eventbus/event.key";
import {MetaData} from "ng-event-bus/lib/meta-data";
import {IPoll} from "../../models/ipoll";
import {PollService} from "../../services/poll.service";
import {MatDialog} from "@angular/material/dialog";
import {PollEditFormComponent} from "../poll-edit-form/poll-edit-form.component";
import {TimeUtility} from "../../../../../common/modules/utility/time.utility";
import * as moment from 'moment';
import {StringUtil} from "../../../../../../utils/string.util";
import {Config} from "./config";
import {WebAppEndpoint} from "../../../../../webapp/web-app.endpoint";
import {parseFilterData} from "../../../../../common/modules/utility/components/basic-filter/basic-filter.model";
import {slideIn} from "../../../../../../base/animations/component.animation";

@Component({
  selector: 'app-poll-list',
  templateUrl: './poll-list.component.html',
  styleUrls: ['./poll-list.component.scss'],
  animations: [
    slideIn('onGrid', 'left'),
    slideIn('onColumn', 'right')
  ]
})
export class PollListComponent extends BaseComponent {
  @Input() config: Config = {
    showUserFilter: false,
    showDeleteButton: false,
    endpoint: WebAppEndpoint.POLLS.SEARCH_ENDPOINT
  }

  size: number;
  page: number = 1;
  polls: IPage<IPoll> = defaultPage
  @Input() questionnaireId: number | undefined
  @Output() pollLoaded: EventEmitter<boolean> = new EventEmitter<boolean>()

  created_from: Date = new Date()
  created_to: Date = new Date()
  username: string | null = null

  protected readonly StringUtil = StringUtil;
  deleteurl: string = WebAppEndpoint.POLLS.DELETE();

  constructor(
    private notifier: NotifierService,
    eventBus: EventBusService,
    router: Router,
    subContainer: SubscriptionContainerService,
    private dialog: MatDialog,
    private pollService: PollService,
    private activatedRoute: ActivatedRoute,
  ) {
    super(eventBus, router, subContainer)
    this.size = this.questionnaireId == null ? 20 : 10
  }


  protected componentName(): string {
    return "PollListComponent";
  }

  ngOnInit(): void {
    this.activatedRoute.queryParams.subscribe(params => {
      let filterData = parseFilterData(params)

      this.username = filterData.username

      if (filterData.fromDate != null && filterData.toDate != null) {
        this.created_from = moment(filterData.fromDate).toDate();
        this.created_to = moment(filterData.toDate).toDate();
      } else {
        this.created_from = new Date(0)
        this.created_to = new Date()
      }

      this.fetchPolls({
        q: filterData.query,
        created_by: this.username,
        created_from: this.created_from == null ? this.created_from : TimeUtility.getDayStart(this.created_from),
        created_to: this.created_to == null ? this.created_to : TimeUtility.getDayEnd(this.created_to),
        page: filterData.page,
        size: filterData.size,
        questionnaire_id: this.questionnaireId
      })
    });
  }

  private fetchPolls(params: IParams) {
    // console.log(this.username)
    this.loading = true
    const key = this.componentKey(EventKey.POLLS.SEARCH())
    this.pollService.search(key, this.config.endpoint, params)
    this.subscribe(
      key,
      null,
      (value: Boolean, meta: MetaData) => {
        this.polls = meta.data
        this.loading = false
        this.pollLoaded.emit(true)
      })
    this.subscribe(this.getErrorKey(key), null, (value, meta) => {
      this.notifier.error('Error', meta.data.error.message)
    })
  }


  openEditForm(id: number | null) {
    const dialogRef = this.dialog.open(PollEditFormComponent, {
      width: '500px',
      height: '500px',
      data: {
        questionnaireId: this.questionnaireId,
        id: id
      },
    })

    let instance = dialogRef.componentInstance
    instance.formSubmitEvent.subscribe((value: IPoll) => {
      console.log(value);
      this.pushNewData(value);
      dialogRef.close()
    })
  }

  private pushNewData(value: IPoll) {
    if (this.polls?.content) {
      for (let i = 0; i < this.polls.content.length; i++) {
        if (this.polls.content[i].id === value.id) {
          this.polls.content[i] = value;
          return;
        }
      }
      this.polls.content = [...this.polls.content, value];
      this.polls.totalElements = this.polls.content.length
    }
  }

  deleteEvent(deleted: Boolean) {
    if (deleted) {
      // don't know why it doesn't work
      this.notifier.info(
        'Delete!',
        'Item successfully'
      )
      this.ngOnInit()
    }
  }

}
