import {BaseConfig} from "../../../../../../base/models/baseConfig";
import {Layout} from "../../../../../common/modules/generic/breadcrumb/breadcrumb.model";
import {InventoryEndpoints} from "../../inventory.endpoints";

export interface UnitConfig extends BaseConfig {
}

export let defaultUnitConfig: UnitConfig = {
  defaultLayout: Layout.COLUMN,
  upsertOnDialog: true,
  showUserFilter: false,
  showDeleteButton: true,
  searchEndpoint: InventoryEndpoints.UNITS.UNIT_ENDPOINT,
  delEndpoint: InventoryEndpoints.UNITS.UNIT_ENDPOINT
}

export let selfEndpoints = {
  endpoint: InventoryEndpoints.UNITS.UNIT_ENDPOINT,
  adminEndpoint: InventoryEndpoints.UNITS.UNIT_ENDPOINT_ADMIN,
}

export let selfRoutes = {
  list: "/dash/units",
  create: "/dash/units/create",
  update(id: number | null) {
    return id == null ? this.create : "/dash/units/" + id + '/update'
  }
}

