import {Component, Inject, LOCALE_ID} from '@angular/core';
import {BaseComponent} from "../../../../../../base/components/base.component";
import {
  BasicFilterData,
  defaultFilterData,
  parseFilterData
} from "../../../../../common/modules/utility/components/basic-filter/basic-filter.model";
import {NotifierService} from "../../../../../../services/notifier/notifier.service";
import {EventBusService} from "../../../../../../services/eventbus/event-bus.service";
import {ActivatedRoute, Router} from "@angular/router";
import {SubscriptionContainerService} from "../../../../../../base/services/subscription-container.service";
import {SalesStatisticsService} from "../services/sales-statistics.service";
import {
  ILowStock,
  IMostDueCustomer,
  IMostFrequentCustomer,
  IMostPayingCustomer,
  ITopBuyingCustomer,
  ITopProduct, resolveProductName
} from "../models/stats.model";
import {IParams} from "../../../../../../base/endpoints/iparams";
import {EventKey} from "../../../../../../services/eventbus/event.key";
import {MetaData} from "ng-event-bus/lib/meta-data";
import {IPiChartData} from "../../../../components/pi-chart/pi-chart.component";
import {Utility} from "../../../../../common/modules/utility/utility";
import {ILineChartData, ILineChartItem} from "../../../../components/linechart/line-chart.model";
import {DialogService} from "../../../../../common/modules/utility/components/popup/dialog.service";
import {IStockDetail} from "../../../../domains/inventory/stocks/models/stock.model";
import {
  StockUpsertComponent
} from "../../../../domains/inventory/stocks/components/stock-upsert/stock-upsert.component";
import {DateUtil} from "../../../../../../utils/date.util";
import {translations} from "../../../../layout/left-nav/menu.model";
import {BaseComponentForOrg} from "../../../../domains/organizations/components/org-base-select.component";
import {OrganizationService} from "../../../../domains/organizations/services/organization.service";

@Component({
  selector: 'app-sales-dashboard',
  templateUrl: './sales-dashboard.component.html',
  styleUrls: ['./sales-dashboard.component.scss']
})
export class SalesDashboardComponent extends BaseComponentForOrg {

  filterData: BasicFilterData = defaultFilterData

  mostProfitableProducts: ITopProduct[] = []
  predictedProducts: ITopProduct[] = []
  lowStocks: ILowStock[] = []

  topBuyingCustomers: ITopBuyingCustomer[] = []
  mostDueCustomers: IMostDueCustomer[] = []
  mostFrequentCustomers: IMostFrequentCustomer[] = []
  mostPayingCustomers: IMostPayingCustomer[] = []
  buyingSellingPrices: ILineChartData = {
    titles: {top: '', xAxes: '', yAxes: ''},
    series: []
  }

  topBuyingCustomersChartData: IPiChartData
  mostDueCustomersChartData: IPiChartData
  mostFrequentCustomersChartData: IPiChartData
  mostPayingCustomersChartData: IPiChartData

  isTabletScreen = window.innerWidth <= 1280 && window.innerWidth >= 992

  constructor(private notifier: NotifierService,
              eventBus: EventBusService,
              router: Router,
              subContainer: SubscriptionContainerService,
              private statsService: SalesStatisticsService,
              private activatedRoute: ActivatedRoute,
              private popup: DialogService<IStockDetail>,
              orgService: OrganizationService,
              @Inject(LOCALE_ID) public locale: string
  ) {
    super(eventBus, router, subContainer, orgService)
  }

  protected componentName(): string {
    return SalesDashboardComponent.name;
  }

  onOverviewLoaded(){
    this.activatedRoute.queryParams.subscribe(params => {
      this.filterData = parseFilterData(params)

      this.onOrgLoaded((orgId: number) => {

        let p: IParams = {
          username: this.filterData.username,
          from_date: this.filterData.fromDate,
          to_date: this.filterData.toDate,
          org_id: orgId
        }
        this.loadMostProfitableProducts(p)

        let dateRange = DateUtil.getPreviousYearDateRange(
          this.filterData.toDate,
          this.filterData.toDate
        )
        this.loadPredictedProducts(
          {
            ...p,
            from_date: dateRange.from_date,
            to_date: dateRange.to_date,
          }
        )
        this.loadLowStocks(p)
        this.loadTopBuyingCustomers(p)
        this.loadMostFrequentCustomers(p)
        this.loadMostPayingCustomers(p)
        this.loadMostDueCustomers(p)
        this.loadBuyingSellingOverview({
          org_id: orgId,
        })
      });

    })
  }
  ngOnInit(): void {

  }

  private loadMostProfitableProducts(params: IParams) {
    this.loading = true
    const key = this.componentKey(EventKey.STATISTICS.MOST_PROFITABLE_PRODUCTS(params))
    this.statsService.mostProfitableProducts(key, params)
    this.subscribe(key, null, (value: Boolean, meta: MetaData) => {
      this.mostProfitableProducts = meta.data
      this.loading = false
    })
    this.subscribe(this.getErrorKey(key), null, (value, meta) => {
      this.notifier.log(meta.data)
    })
  }

  private loadPredictedProducts(params: IParams) {
    this.loading = true
    const key = this.componentKey(EventKey.STATISTICS.PREDICTED_PRODUCTS(params))
    this.statsService.mostProfitableProducts(key, params)
    this.subscribe(key, null, (value: Boolean, meta: MetaData) => {
      this.predictedProducts = meta.data
      this.loading = false
    })
    this.subscribe(this.getErrorKey(key), null, (value, meta) => {
      this.notifier.log(meta.data)
    })
  }

  private loadLowStocks(params: IParams) {
    this.loading = true
    const key = this.componentKey(EventKey.STATISTICS.LOW_STOCKS(params))
    this.statsService.lowStocks(key, params)
    this.subscribe(key, null, (value: Boolean, meta: MetaData) => {
      this.lowStocks = meta.data
      this.loading = false
    })
    this.subscribe(this.getErrorKey(key), null, (value, meta) => {
      this.notifier.log(meta.data)
    })
  }

  private loadTopBuyingCustomers(params: IParams) {
    this.loading = true
    const key = this.componentKey(EventKey.STATISTICS.TOP_BUYING_CUSTOMERS(params))
    this.statsService.topBuyingCustomers(key, params)
    this.subscribe(key, null, (value: Boolean, meta: MetaData) => {
      this.topBuyingCustomers = meta.data
      this.topBuyingCustomersChartData = this.topCustomersAsChartData()
      this.loading = false
    })
    this.subscribe(this.getErrorKey(key), null, (value, meta) => {
      this.notifier.log(meta.data)
    })
  }

  private loadMostDueCustomers(params: IParams) {
    this.loading = true
    const key = this.componentKey(EventKey.STATISTICS.TOP_DUE_CUSTOMERS(params))
    this.statsService.mostDueCustomers(key, params)
    this.subscribe(key, null, (value: Boolean, meta: MetaData) => {
      this.mostDueCustomers = meta.data
      this.loading = false
    })
    this.subscribe(this.getErrorKey(key), null, (value, meta) => {
      this.notifier.log(meta.data)
    })
  }

  private loadMostFrequentCustomers(params: IParams) {
    this.loading = true
    const key = this.componentKey(EventKey.STATISTICS.MOST_FREQUENT_CUSTOMERS(params))
    this.statsService.mostFrequentCustomers(key, params)
    this.subscribe(key, null, (value: Boolean, meta: MetaData) => {
      this.mostFrequentCustomers = meta.data
      this.loading = false
    })
    this.subscribe(this.getErrorKey(key), null, (value, meta) => {
      this.notifier.log(meta.data)
    })
  }

  private loadMostPayingCustomers(params: IParams) {
    this.loading = true
    const key = this.componentKey(EventKey.STATISTICS.CUSTOMERS_WITH_GOOD_TRACT_RECORD(params))
    this.statsService.mostPayingCustomers(key, params)
    this.subscribe(key, null, (value: Boolean, meta: MetaData) => {
      this.mostPayingCustomers = meta.data
      this.loading = false
    })
    this.subscribe(this.getErrorKey(key), null, (value, meta) => {
      this.notifier.log(meta.data)
    })
  }

  private loadBuyingSellingOverview(params: IParams) {
    this.loading = true
    const key = this.componentKey(EventKey.STATISTICS.BUYING_SELLING_OVERVIEW(params))
    this.statsService.buyingSellingPriceStat(key, params)
    this.subscribe(key, null, (value: Boolean, meta: MetaData) => {
      this.buyingSellingPrices = this.convertToLineChartData(meta.data)
      // console.log("buyingSellingPrices", this.buyingSellingPrices)
      this.loading = false
    })
    this.subscribe(this.getErrorKey(key), null, (value, meta) => {
      this.notifier.log(meta.data)
    })
  }

  onAddStockClick(stock_id: number) {

  }

  private topCustomersAsChartData(): IPiChartData {
    return {
      items: this.topBuyingCustomers.map((value: ITopBuyingCustomer) => {
        return {
          label: value.customer_name ? value.customer_name : value.customer_phone,
          value: value.amount
        }
      }),
      labelFormatter: (val: string) => {
        return val
      }
    }
  }

  private mostDueAsChartData(): IPiChartData {
    return {
      items: this.mostDueCustomers.map((value: IMostDueCustomer) => {
        return {
          label: value.customer_name ? value.customer_name : value.customer_phone,
          value: value.amount
        }
      }),
      labelFormatter: (val: string) => {
        return val
      }
    }
  }

  private mostPayingAsChartData(): IPiChartData {
    return {
      items: this.mostPayingCustomers.map((value: IMostPayingCustomer) => {
        return {
          label: value.customer_name ? value.customer_name : value.customer_phone,
          value: value.paid_amount
        }
      }),
      labelFormatter: (val: string) => {
        return val
      }
    }
  }

  private mostFrequentAsChartData(): IPiChartData {
    return {
      items: this.mostFrequentCustomers.map((value: IMostFrequentCustomer) => {
        return {
          label: value.customer_name ? value.customer_name : value.customer_phone,
          value: value.count
        }
      }),
      labelFormatter: (val: string) => {
        return val
      }
    }
  }

  protected readonly Utility = Utility;

  activeTab = 0

  activateTab(index: number) {
    this.activeTab = index

    this.mostFrequentCustomersChartData = this.mostFrequentAsChartData()
    this.mostDueCustomersChartData = this.mostDueAsChartData()
    this.mostPayingCustomersChartData = this.mostPayingAsChartData()
  }

  private convertToLineChartData(prices: any) {
    return {
      titles: {
        top: translations['Last 1 year'], xAxes: translations['Month'], yAxes: translations['Amount']
      },
      series: [
        {
          seriesName: this.translations['Total Investment'],
          data: this.convertToLineChartItem(prices, 'buying_price'),
          color: '#d09428'
        },
        {
          seriesName: translations['Total Sales'],
          data: this.convertToLineChartItem(prices, 'selling_price'),
          color: '#2acc38'
        }
      ]
    }
  }

  private convertToLineChartItem(prices: any, priceType: 'buying_price' | 'selling_price') {
    let items: ILineChartItem[] = []
    for (let key in prices) {
      let buyingPrice: number = prices[key]?.buying_price == null ? 0 : prices[key]?.buying_price!!
      let sellingPrince: number = prices[key]?.selling_price == null ? 0 : prices[key]?.selling_price!!
      let item: ILineChartItem = {
        attribute: key,
        value: priceType == "buying_price" ? Utility.toFloat(buyingPrice) : Utility.toFloat(sellingPrince)
      }
      items.push(item)
    }
    return items
  }

  openStockUpsertComponent(id: number | null) {
    this.popup.open(StockUpsertComponent, {id: id})
    this.popup.onCompleted.subscribe((result: IStockDetail | null) => {
      this.loadLowStocks({
        username: this.filterData.username,
        from_date: this.filterData.fromDate,
        to_date: this.filterData.toDate
      })
      console.log("onSuccess. closing dialog")
    })
    this.popup.onCanceled.subscribe(() => {
      console.log("onCanceled. closing dialog")
    })
  }

  protected readonly resolveProductName = resolveProductName;
}
