import {BaseConfig} from "../../../../../base/models/baseConfig";
import {Layout} from "../../../../common/modules/generic/breadcrumb/breadcrumb.model";
import {DashEndpoint} from "../../../dash.endpoint";
import {WebAppEndpoint} from "../../../../webapp/web-app.endpoint";

export interface LabelConfig extends BaseConfig {
  searchEndpointAdmin: string
}

export let defaultLabelConfig: LabelConfig = {
  defaultLayout: Layout.COLUMN,
  upsertOnDialog: true,
  showUserFilter: false,
  showDeleteButton: false,
  searchEndpointAdmin: DashEndpoint.LABELS.LABEL_ENDPOINT_ADMIN,
  searchEndpoint: DashEndpoint.LABELS.LABEL_ENDPOINT,
  delEndpoint: DashEndpoint.LABELS.LABEL_ENDPOINT
}

export let selfRoutes = {
  list: "/dash/labels",
  getChildren(id: number) {
    return "/dash/labels?parent_id=" + id
  },
  create: "/dash/labels/create",
  update(id: number | null) {
    return id == null ? this.create : "/dash/labels/" + id + '/update'
  }
}
