import {Component, Inject, ViewChild} from '@angular/core';
import {FormControl, FormGroup, Validators} from "@angular/forms";
import {EventBusService} from "../../../../../../services/eventbus/event-bus.service";
import {ActivatedRoute, Router} from "@angular/router";
import {SubscriptionContainerService} from "../../../../../../base/services/subscription-container.service";
import {FeedbackService} from "../../services/feedback.service";
import {NotifierService} from "../../../../../../services/notifier/notifier.service";
import {FeedbackConfig, defaultFeedbackConfig, selfRoutes} from "../../models/config.model";
import {BreadcrumbItem} from "../../../../../common/modules/generic/breadcrumb/breadcrumb.model";
import {IFeedback} from "../../models/feedback.model";
import {UploadedImages} from "../../../../../webapp/models/uploads/IUploadFile";
import {EventKey} from "../../../../../../services/eventbus/event.key";
import {HttpErrorResponse} from "@angular/common/http";
import {
  ImageUploaderComponent
} from "../../../../../common/modules/cms/files/components/image-uploader/image-uploader.component";
import {MAT_DIALOG_DATA} from "@angular/material/dialog";
import {BaseUpsertComponent} from "../../../../../../base/components/base-upsert.component";
import {BaseConfig} from "../../../../../../base/models/baseConfig";


@Component({
  selector: 'app-feedback-upsert',
  templateUrl: './feedback-upsert.component.html',
  styleUrls: ['./feedback-upsert.component.scss']
})
export class FeedbackUpsertComponent extends BaseUpsertComponent {

  config: FeedbackConfig = defaultFeedbackConfig

  @ViewChild(ImageUploaderComponent, {static: false})
  private uploadComponent: ImageUploaderComponent

  feedbackForm = new FormGroup({
    title: new FormControl('', [Validators.required, Validators.minLength(4)]),
    description: new FormControl('', [Validators.required, Validators.minLength(20)]),
    image: new FormControl<string | null>(null)
  })

  constructor(eventBus: EventBusService,
              router: Router,
              subContainer: SubscriptionContainerService,
              route: ActivatedRoute,
              private feedbackService: FeedbackService,
              private notifier: NotifierService,
              @Inject(MAT_DIALOG_DATA) dialogData: any) {
    super(eventBus, router, subContainer, route, dialogData)
  }

  protected override componentName(): string {
    return "FeedbackUpsertComponent"
  }

  protected override getBreadcrumbItems(): BreadcrumbItem[] {
    return [
      {
         text: this.translations["Dashboard"],
        link: null,
        active: false
      },
      {
        text: "Feedbacks",
        link: "/dash/feedbacks",
        active: false
      },
      {
        text: "Upsert",
        link: null,
        active: true
      }
    ]
  }

  override initialize(exId: number): void {
    this.loading = true
    let key = this.componentKey(EventKey.FEEDBACKS.GET(exId))
    this.feedbackService.fetchSingle(key, exId)
    this.subscribe(key, null, (value, meta) => {
      this.loading = false;
      this.populateForm(meta.data)
    })
    this.subscribe(this.getErrorKey(key), null, (value, meta) => {
      let error: HttpErrorResponse = meta.data
      console.log(error)
      this.loading = false;
    })
  }

  onSubmit() {
    if (!this.feedbackForm.valid) {
      return
    }
    this.uploadComponent.uploadImages()
  }

  onImgUploaded(images: UploadedImages) {
    if (images.thumbs != null && images.thumbs.length > 0) {
      this.feedbackForm.controls.image.setValue(images.thumbs[0])
    }
    this.upsert(this.exId)
  }

  getConfig(): BaseConfig {
    return this.config;
  }

  getListPageRoute(): string {
    return selfRoutes.list;
  }

  upsert(exId: number | null) {
    this.loading = true
    let key = this.componentKey(EventKey.FEEDBACKS.UPSERT(exId))
    // @ts-ignore
    this.feedbackService.upsert(key, this.feedbackForm.value, exId)
    this.subscribe(key, null, (value, meta) => {
      this.loading = false;
      let message = "Feedback has been " + (exId ? "updated" : "created")
      this.notifier.success("Success", message)
      this.feedbackForm.reset();

      if (this.config.upsertOnDialog) {
        this.onSuccess.emit()
      } else {
        this.navigate(selfRoutes.list, value => {
          this.notifier.success("Success!", message)
        })
      }
    })
    this.subscribe(this.getErrorKey(key), null, (value, meta) => {
      let error: HttpErrorResponse = meta.data
      this.loading = false;
      this.notifier.warning('Failed!', error.error.message)
    })
  }

  private populateForm(n: IFeedback) {
    this.feedbackForm.controls.title.setValue(n.category)
    this.feedbackForm.controls.description.setValue(n.description)
    this.feedbackForm.controls.image.setValue(n.image == null ? null : n.image)
  }

}
