import {Component} from '@angular/core';
import {BaseListComponentV2} from "../../../../../../../base/components/base-list-v2.component";
import {AcValidationTokenConfig, defaultAcValidationTokenConfig} from "../../models/config.model";
import {defaultPage, IPage} from "../../../../../../../base/models/IPage";
import {EventBusService} from "../../../../../../../services/eventbus/event-bus.service";
import {ActivatedRoute, Router} from "@angular/router";
import {SubscriptionContainerService} from "../../../../../../../base/services/subscription-container.service";
import {AuthService} from "../../../../../../common/modules/auth/services/auth.service";
import {AcValidationTokenService} from "../../services/acvalidationtoken.service";
import {NotifierService} from "../../../../../../../services/notifier/notifier.service";
import {DashEndpoint} from "../../../../../dash.endpoint";
import {parseFilterData} from "../../../../../../common/modules/utility/components/basic-filter/basic-filter.model";
import {BreadcrumbItem} from "../../../../../../common/modules/generic/breadcrumb/breadcrumb.model";
import {IParams} from "../../../../../../../base/endpoints/iparams";
import {EventKey} from "../../../../../../../services/eventbus/event.key";
import {MetaData} from "ng-event-bus/lib/meta-data";
import {ComponentType} from "@angular/cdk/overlay";
import {IAcValidationToken} from "../../models/acvalidationtoken.model";
import {BaseUpsertComponentV2} from "../../../../../../../base/components/base-upsert-v2.component";
import {DialogService} from "../../../../../../common/modules/utility/components/popup/dialog.service";
import {BaseConfig} from "../../../../../../../base/models/baseConfig";

@Component({
  selector: 'app-acvalidationtoken-list',
  templateUrl: './acvalidationtoken-list.component.html',
  styleUrls: ['./acvalidationtoken-list.component.scss']
})
export class AcValidationTokenListComponent extends BaseListComponentV2<IAcValidationToken> {

  config: AcValidationTokenConfig = defaultAcValidationTokenConfig
  acValidationTokens: IPage<IAcValidationToken> = defaultPage

  protected componentName(): string {
    return "AcValidationTokenListComponent";
  }

  constructor(eventBus: EventBusService,
              router: Router,
              subContainer: SubscriptionContainerService,
              authService: AuthService,
              dialog: DialogService<IAcValidationToken>,
              private activatedRoute: ActivatedRoute,
              private acValidationTokenService: AcValidationTokenService,
              private notifier: NotifierService,
  ) {
    super(eventBus, router, subContainer, dialog)
    if (authService.isAdmin()) {
      this.config.searchEndpoint = DashEndpoint.ACVALIDATIONTOKENS.ACVALIDATIONTOKEN_ENDPOINT_ADMIN
    }
  }

  initialize(): void {
    this.activatedRoute.queryParams.subscribe(params => {
      let filterData = parseFilterData(params)
      this.fetchAcValidationTokens({
        q: filterData.query,
        username: filterData.username,
        from_date: filterData.fromDate,
        to_date: filterData.toDate,
        page: filterData.page,
        size: filterData.size
      })
    });
  }

  protected override getBreadcrumbItems(): BreadcrumbItem[] {
    return [
      {
         text: this.translations["Dashboard"],
        link: null,
        active: false
      },
      {
        text: "AcValidationTokens",
        link: null,
        active: true
      }
    ]
  }

  private fetchAcValidationTokens(params: IParams) {
    this.loading = true
    const key = this.componentKey(EventKey.ACVALIDATIONTOKENS.SEARCH(params))
    this.acValidationTokenService.search(key, this.config.searchEndpoint, params)
    this.subscribe(
      key,
      null,
      (value: Boolean, meta: MetaData) => {
        this.acValidationTokens = meta.data
        this.loading = false
      })
    this.subscribe(this.getErrorKey(key), null, (value, meta) => {
      this.notifier.error('Error', meta.data.error.message)
    })
  }

  onDeleted(deleted: Boolean) {
    if (deleted) {
      this.notifier.success("Success", "AcValidationToken is deleted")
      this.ngOnInit()
    }
  }

  override getConfig(): BaseConfig {
    return this.config
  }


 override getRoutes(): { create: string, update: (id: number) => string, params: IParams } | null {
    return null
  }

  getUpsertComponent(): ComponentType<BaseUpsertComponentV2<IAcValidationToken>> | null {
    return null
  }

}
