import {Component} from '@angular/core';
import {BaseComponent} from "../../../../base/components/base.component";
import {EventBusService} from "../../../../services/eventbus/event-bus.service";
import {Router} from "@angular/router";
import {SubscriptionContainerService} from "../../../../base/services/subscription-container.service";
import {INotification} from "../../../common/modules/social/notifications/models/notification.model";
import {IPage} from "../../../../base/models/IPage";
import {AuthService} from "../../../common/modules/auth/services/auth.service";

@Component({
  selector: 'app-home-page',
  templateUrl: './home-page.component.html',
  styleUrls: ['./home-page.component.scss']
})
export class HomePageComponent extends BaseComponent {

  notifications: IPage<INotification>

  constructor(eventBus: EventBusService,
              router: Router,
              subContainer: SubscriptionContainerService,
              private authService: AuthService,
  ) {
    super(eventBus, router, subContainer)
  }


  ngOnInit(): void {
    if (!this.hasPermission("CAN_MANAGE_INVENTORY")){
      this.router.navigate(['/dash/sales/leads'])
    }
  }


  protected componentName(): string {
    return "HomePageComponent";
  }

  hasPermission(permission: string): boolean {
    return this.authService.isAdmin() || this.authService.hasPrivilege(permission)
  }

}
