import {months} from "../../../common/modules/utility/time.utility";

export interface IBarChartData {
  seriesName: { primary: string, alternate: string }
  data: IBarChartItem[]
}

export interface IBarChartItem {
  attribute: string
  value: { primary: number, alternate: number }
}

export interface IFilterData {
  name: string
  options: { title: string, value: string } []
}

export class ActionDef {
  title: string
  alternateTitle: string
  showAlternate: boolean = false
  action: ((showAlternative: boolean)=> void) | null
}

export let monthFilterData: IFilterData = {
  name: "Month",
  options: months
}
export let defaultMonthFilterValue: string = monthFilterData.options[new Date().getMonth()].value
