import {Injectable} from '@angular/core';
import {IParams} from "../../../../../base/endpoints/iparams";
import {WebAppEndpoint} from "../../../../webapp/web-app.endpoint";
import {IPage} from "../../../../../base/models/IPage";
import {AuthService} from "../../../../common/modules/auth/services/auth.service";
import {NotifierService} from "../../../../../services/notifier/notifier.service";
import {HttpClient, HttpErrorResponse} from "@angular/common/http";
import {EventBusService} from "../../../../../services/eventbus/event-bus.service";
import {BaseService} from "../../../../../base/services/base.service";
import {IPollVoteStats, IUserVoteStats, IVote} from "../models/vote.model";
import {IAuth} from "../../../../common/modules/auth/models/auth.model";

@Injectable({
  providedIn: 'root'
})
export class VoteService extends BaseService {

  constructor(authService: AuthService,
              notifier: NotifierService,
              private http: HttpClient,
              private eventBusService: EventBusService
  ) {
    super(authService, notifier);
  }

  fetchVotes(key: string, params: IParams) {
    let url = WebAppEndpoint.VOTES.SEARCH(params)
    this.http.get<IPage<IVote>>(url)
      .subscribe((votes: IPage<IVote>) => {
        this.eventBusService.publish(key, votes)
      }, error => {
        this.handleError(error, (auth: IAuth) => {
          this.fetchVotes(key, params)
        }, (err: HttpErrorResponse) => {
          this.eventBusService.publish(this.getErrorKey(key), err)
        })
      })
  }

  fetchUserVoteStats(key: string, params: IParams) {
    let url = WebAppEndpoint.VOTES.FETCH_USER_VOTE_STATS(params)
    this.http.get<IUserVoteStats[]>(url)
      .subscribe((stats: IUserVoteStats[]) => {
        this.eventBusService.publish(key, stats)
      }, error => {
        this.handleError(error, (auth: IAuth) => {
          this.fetchUserVoteStats(key, params)
        }, (err: HttpErrorResponse) => {
          this.eventBusService.publish(this.getErrorKey(key), err)
        })
      })
  }

  fetchPollVoteStats(key: string, params: IParams) {
    let url = WebAppEndpoint.VOTES.FETCH_POLL_VOTE_STATS(params)
    this.http.get<IPollVoteStats[]>(url)
      .subscribe((stats: IPollVoteStats[]) => {
        this.eventBusService.publish(key, stats)
      }, error => {
        this.handleError(error, (auth: IAuth) => {
          this.fetchPollVoteStats(key, params)
        }, (err: HttpErrorResponse) => {
          this.eventBusService.publish(this.getErrorKey(key), err)
        })
      })
  }
}
