import {Layout} from "../../../../../common/modules/generic/breadcrumb/breadcrumb.model";
import {DashEndpoint} from "../../../../dash.endpoint";
import {BaseConfig} from "../../../../../../base/models/baseConfig";


export interface POptionConfig extends BaseConfig {
}

export let defaultPOptionConfig: POptionConfig = {
  defaultLayout: Layout.GRID,
  upsertOnDialog: true,
  showUserFilter: false,
  showDeleteButton: true,
  searchEndpoint: DashEndpoint.POPTIONS.POPTION_ENDPOINT,
  delEndpoint: DashEndpoint.POPTIONS.POPTION_ENDPOINT
}


export let selfRoutes = {
  list: "/dash/v2/poll-options",
  create: "/dash/v2/poll-options/create",
  update(id: number | null) {
    return id == null ? this.create : "/dash/v2/poll-options/" + id + '/update'
  }
}
