import {Component} from '@angular/core';
import {defaultPage, IPage} from "../../../../../../base/models/IPage";
import {IFeedback, Priorities} from "../../models/feedback.model";
import {FeedbackConfig, defaultFeedbackConfig, selfRoutes} from "../../models/config.model";
import {BreadcrumbItem} from "../../../../../common/modules/generic/breadcrumb/breadcrumb.model";
import {AuthService} from "../../../../../common/modules/auth/services/auth.service";
import {EventBusService} from "../../../../../../services/eventbus/event-bus.service";
import {ActivatedRoute, Router} from "@angular/router";
import {SubscriptionContainerService} from "../../../../../../base/services/subscription-container.service";
import {DashEndpoint} from "../../../../dash.endpoint";
import {parseFilterData} from "../../../../../common/modules/utility/components/basic-filter/basic-filter.model";
import {IParams} from "../../../../../../base/endpoints/iparams";
import {EventKey} from "../../../../../../services/eventbus/event.key";
import {MetaData} from "ng-event-bus/lib/meta-data";
import {FeedbackService} from "../../services/feedback.service";
import {NotifierService} from "../../../../../../services/notifier/notifier.service";
import {MatDialog} from "@angular/material/dialog";
import {FeedbackUpsertComponent} from "../feedback-upsert/feedback-upsert.component";
import {BaseListComponent} from "../../../../../../base/components/base-list.component";
import {ComponentType} from "@angular/cdk/overlay";
import {BaseUpsertComponent} from "../../../../../../base/components/base-upsert.component";
import {BaseConfig} from 'src/app/base/models/baseConfig';
import {FeedbackActionListComponent} from "../feedbackaction-list/feedbackaction-list.component";


@Component({
  selector: 'app-feedback-list',
  templateUrl: './feedback-list.component.html',
  styleUrls: ['./feedback-list.component.scss']
})
export class FeedbackListComponent extends BaseListComponent {

  config: FeedbackConfig = defaultFeedbackConfig
  feedbacks: IPage<IFeedback> = defaultPage

  protected componentName(): string {
    return "FeedbackListComponent";
  }

  constructor(eventBus: EventBusService,
              router: Router,
              subContainer: SubscriptionContainerService,
              authService: AuthService,
              dialog: MatDialog,
              private activatedRoute: ActivatedRoute,
              private feedbackService: FeedbackService,
              private notifier: NotifierService,
              private matDialog: MatDialog
  ) {
    super(eventBus, router, subContainer, dialog)
    if (authService.isAdmin()) {
      this.config.searchEndpoint = DashEndpoint.FEEDBACKS.FEEDBACK_ENDPOINT_ADMIN
    }
  }

  initialize(): void {
    this.activatedRoute.queryParams.subscribe(params => {
      let filterData = parseFilterData(params)
      this.fetchFeedbacks({
        username: filterData.username,
        from_date: filterData.fromDate,
        to_date: filterData.toDate,
        page: filterData.page,
        size: filterData.size
      })
    });

  }

  protected override getBreadcrumbItems(): BreadcrumbItem[] {
    return [
      {
         text: this.translations["Dashboard"],
        link: null,
        active: false
      },
      {
        text: "Feedbacks",
        link: null,
        active: true
      }
    ]
  }

  typeBg(priority: string) {
    switch (priority) {
      case Priorities.EMERGENCY:
        return 'badge bg-danger'
      case Priorities.HIGH:
        return 'badge bg-warning'
      case Priorities.MEDIUM:
        return 'badge bg-warning'
      case Priorities.NORMAL:
        return 'badge bg-primary'
      case Priorities.LOW:
        return 'badge bg-info'
      default:
        return 'badge bg-info'
    }
  }

  /*
  - Copy this to EventKey and delete afterward
   static FEEDBACKS = {
      SEARCH(params: IParams) {
        return "feedbacks:search:search".concat(BaseEndpoint.toParamString(params))
      }
    }
   */

  private fetchFeedbacks(params: IParams) {
    this.loading = true
    const key = this.componentKey(EventKey.FEEDBACKS.SEARCH(params))
    this.feedbackService.search(key, this.config.searchEndpoint, params)
    this.subscribe(
      key,
      null,
      (value: Boolean, meta: MetaData) => {
        this.feedbacks = meta.data
        this.loading = false
      })
    this.subscribe(this.getErrorKey(key), null, (value, meta) => {
      this.notifier.error('Error', meta.data.error.message)
    })
  }

  takeAction(feedback: IFeedback) {
    const dialogRef = this.matDialog.open(FeedbackActionListComponent, {
      data: {
        feedback: feedback
      },
    })
    let component = dialogRef.componentInstance
    component.onCanceled.subscribe(() => {
      dialogRef.close()
    })
    component.onSuccess.subscribe(() => {
      this.ngOnInit()
      dialogRef.close()
    })
  }

  onDeleted(deleted: Boolean) {
    if (deleted) {
      this.notifier.success("Success", "Feedback is deleted")
      this.ngOnInit()
    }
  }

  override getConfig(): BaseConfig {
    return this.config
  }

  override getRoutes(): { create: string; update: (id: number) => string; } {
    return {
      create: selfRoutes.create,
      update: (id: number) => selfRoutes.update(id)
    }
  }

  getUpsertComponent(): ComponentType<BaseUpsertComponent> {
    return FeedbackUpsertComponent
  }

}
