import {Component} from '@angular/core';
import {BaseListComponent} from "../../../../../base/components/base-list.component";
import {BreadcrumbItem, Layout} from "../../../../common/modules/generic/breadcrumb/breadcrumb.model";
import {ComponentType} from "@angular/cdk/overlay";
import {BaseUpsertComponent} from "../../../../../base/components/base-upsert.component";
import {BaseConfig} from "../../../../../base/models/baseConfig";
import {selfEndpoints} from "../../crudexample/models/config.model";
import {EventKey} from "../../../../../services/eventbus/event.key";
import {MetaData} from "ng-event-bus/lib/meta-data";
import {EventBusService} from "../../../../../services/eventbus/event-bus.service";
import {ActivatedRoute, Router} from "@angular/router";
import {SubscriptionContainerService} from "../../../../../base/services/subscription-container.service";
import {MatDialog} from "@angular/material/dialog";
import {NotifierService} from "../../../../../services/notifier/notifier.service";
import {QuestionService} from "../../questionnaires/services/question.service";
import {IResultSheetItem} from "../result-sheet/result-sheet.model";

@Component({
  selector: 'app-participants',
  templateUrl: './participants.component.html',
  styleUrls: ['./participants.component.scss']
})
export class ParticipantsComponent extends BaseListComponent {

  config: BaseConfig = {
    defaultLayout: Layout.GRID,
    upsertOnDialog: true,
    showUserFilter: false,
    showDeleteButton: true,
    searchEndpoint: selfEndpoints.endpoint,
    delEndpoint: selfEndpoints.endpoint
  }
  resultSheetItems: IResultSheetItem[] = []

  constructor(eventBus: EventBusService,
              router: Router,
              subContainer: SubscriptionContainerService,
              dialog: MatDialog,
              private route: ActivatedRoute,
              private questionnaireService: QuestionService,
              private notifier: NotifierService,
  ) {
    super(eventBus, router, subContainer, dialog)

  }

  protected componentName(): string {
    return "ParticipantsComponent";
  }

  protected getBreadcrumbItems(): BreadcrumbItem[] {
    return [];
  }

  getConfig(): BaseConfig {
    return this.config;
  }

  getRoutes(): { create: string; update: (id: number) => string } | null {
    return null;
  }

  getUpsertComponent(): ComponentType<BaseUpsertComponent> | null {
    return null;
  }

  protected initialize(): void {
    this.route.paramMap.subscribe(paramMap => {
      let exId = paramMap.get('id');
      if (exId != null) {
        this.fetchResultSheet(parseInt(exId))
      }
    })
  }

  private fetchResultSheet(questionnaireId: number) {
    this.loading = true
    const key = this.componentKey(EventKey.QUESTIONNAIRES.FETCH_RESULT_SHEET(questionnaireId))
    this.questionnaireService.fetchResultSheet(key, questionnaireId)
    this.subscribe(
      key,
      null,
      (value: Boolean, meta: MetaData) => {
        this.resultSheetItems = meta.data
        this.loading = false
      })
    this.subscribe(this.getErrorKey(key), null, (value, meta) => {
      this.notifier.error('Error', meta.data.error.message)
    })
  }

}
