import {Component, EventEmitter, Output} from '@angular/core';
import {BaseComponent} from "../../../../base/components/base.component";
import {EventBusService} from "../../../../services/eventbus/event-bus.service";
import {ActivatedRoute, Router} from "@angular/router";
import {SubscriptionContainerService} from "../../../../base/services/subscription-container.service";
import * as moment from "moment";

export class DateRange {

	from: Date = new Date(0)
	to: Date = new Date()

	constructor(from: Date, to: Date) {
		this.from = from
		this.to = to
	}
}

@Component({
	selector: 'app-date-range-filter',
	templateUrl: './date-range-filter.component.html',
	styleUrls: ['./date-range-filter.component.scss']
})
export class DateRangeFilterComponent extends BaseComponent {
	created_from: Date = new Date()
	created_to: Date = new Date()

	@Output() onDateRangeChange: EventEmitter<DateRange> = new EventEmitter<DateRange>()

	protected componentName(): string {
		return "DateRangeFilterComponent";
	}

	constructor(
		eventBus: EventBusService,
		router: Router,
		subContainer: SubscriptionContainerService,
		private activatedRoute: ActivatedRoute,
	) {
		super(eventBus, router, subContainer)
	}

	ngOnInit(): void {
		this.activatedRoute.queryParams.subscribe(params => {
			let from = params['created_from']
			let to = params['created_to']

			if (from != null && to != null) {
				this.created_from = moment(from).toDate();
				this.created_to = moment(to).toDate();
			} else {
				this.created_from = new Date(0)
				this.created_to = new Date()
			}
			this.onDateRangeChange.emit(
				new DateRange(this.created_from, this.created_to)
			)
		});
	}

	onFromDateChanged() {
		console.log(this.created_from)
	}
	onToDateChanged() {
		console.log(this.created_to)
	}
}
