import {Component, Inject, ViewChild} from '@angular/core';
import {FormControl, FormGroup, Validators} from "@angular/forms";
import {EventBusService} from "../../../../../../services/eventbus/event-bus.service";
import {ActivatedRoute, Router} from "@angular/router";
import {SubscriptionContainerService} from "../../../../../../base/services/subscription-container.service";
import {LabelService} from "../../services/label.service";
import {NotifierService} from "../../../../../../services/notifier/notifier.service";
import {defaultLabelConfig, LabelConfig, selfRoutes} from "../../models/config.model";
import {BreadcrumbItem} from "../../../../../common/modules/generic/breadcrumb/breadcrumb.model";
import {ILabel} from "../../models/label.model";
import {UploadedImages} from "../../../../../webapp/models/uploads/IUploadFile";
import {EventKey} from "../../../../../../services/eventbus/event.key";
import {HttpErrorResponse} from "@angular/common/http";
import {
  ImageUploaderComponent
} from "../../../../../common/modules/cms/files/components/image-uploader/image-uploader.component";
import {BaseUpsertComponent} from "../../../../../../base/components/base-upsert.component";
import {MAT_DIALOG_DATA} from "@angular/material/dialog";
import {BaseConfig} from "../../../../../../base/models/baseConfig";
import {BaseUpsertComponentV2} from "../../../../../../base/components/base-upsert-v2.component";
import {IImageSlice} from "../../../../../common/modules/cms/files/models/file.model";

@Component({
  selector: 'app-label-upsert',
  templateUrl: './label-upsert.component.html',
  styleUrls: ['./label-upsert.component.scss']
})
export class LabelUpsertComponent extends BaseUpsertComponentV2<ILabel> {
  config: LabelConfig = defaultLabelConfig

  labelForm = new FormGroup({
    name: new FormControl('', [Validators.required, Validators.minLength(3)]),
    description: new FormControl(''),
    code: new FormControl('', [
      Validators.required,
      Validators.minLength(3),
      Validators.maxLength(100)
    ]),
    parent_id: new FormControl<number | null>(null),
    parent_code: new FormControl("", [Validators.maxLength(100)]),
    icon: new FormControl(),
    image: new FormControl(),
    flagship: new FormControl(false),
    serial: new FormControl(0),
    color: new FormControl('', Validators.required),
    background_color: new FormControl('', Validators.required),
    ui_height: new FormControl(0, [Validators.min(0), Validators.max(500)])
  });

  constructor(eventBus: EventBusService,
              router: Router,
              subContainer: SubscriptionContainerService,
              route: ActivatedRoute,
              @Inject(MAT_DIALOG_DATA) dialogData: any,
              private labelService: LabelService,
              private notifier: NotifierService) {
    super(eventBus, router, subContainer, route, dialogData)
  }

  protected getBreadcrumbItems(): BreadcrumbItem[] {
    return [
      {
         text: this.translations["Dashboard"],
        link: null,
        active: false
      },
      {
        text: "Labels",
        link: "/dash/labels",
        active: false
      },
      {
        text: "Upsert",
        link: null,
        active: true
      }
    ];
  }

  getExIcon(): IImageSlice[] {
    let image = this.labelForm.controls.icon.value
    if (image == null) {
      return []
    }
    return [{image_url: image, thumb_url: image}]
  }

  getExImage(): IImageSlice[] {
    let image = this.labelForm.controls.image.value
    if (image == null) {
      return []
    }
    return [{image_url: image, thumb_url: image}]
  }

  protected override componentName(): string {
    return "LabelUpsertComponent"
  }

  getConfig(): BaseConfig {
    return this.config;
  }

  getListPageRoute(): string {
    return selfRoutes.list;
  }

  initialize(exId: number): void {
    this.loading = true
    let key = this.componentKey(EventKey.LABELS.GET(exId))
    this.labelService.fetchSingle(key, exId)
    this.subscribe(key, null, (value, meta) => {
      this.loading = false;
      this.populateForm(meta.data)
    })
    this.subscribe(this.getErrorKey(key), null, (value, meta) => {
      let error: HttpErrorResponse = meta.data
      console.log(error)
      this.loading = false;
    })
  }


  onSubmit() {
    if (!this.labelForm.valid) {
      return
    }
    this.upsert(this.exId)
  }

  onIconUploaded(images: IImageSlice[]) {
    if (images.length > 0) {
      /*
      Think about the thumbnail, this will only upload the image, not thumbnail
       */
      this.labelForm.controls.icon.setValue(images[0].image_url)
    }
  }
  onImgUploaded(images: IImageSlice[]) {
    if (images.length > 0) {
      /*
      Think about the thumbnail, this will only upload the image, not thumbnail
       */
      this.labelForm.controls.image.setValue(images[0].image_url)
    }
  }

  upsert(exId: number | null) {
    this.loading = true
    let key = this.componentKey(EventKey.LABELS.UPSERT(exId))
    // @ts-ignore
    this.labelService.upsert(key, this.labelForm.value, exId)
    this.subscribe(key, null, (value, meta) => {
      this.loading = false;
      let message = "Label has been " + (exId ? "updated" : "created")
      this.notifier.success("Success", message)
      this.labelForm.reset();

      if (this.config.upsertOnDialog) {
        this.onCompleted.emit()
      } else {
        this.navigate(selfRoutes.list, value => {
          this.notifier.success("Success!", message)
        })
      }

    })
    this.subscribe(this.getErrorKey(key), null, (value, meta) => {
      let error: HttpErrorResponse = meta.data
      this.loading = false;
      this.notifier.warning('Failed!', error.error.message)
    })
  }


  private populateForm(n: ILabel) {
    this.labelForm.controls.name.setValue(n.name)
    this.labelForm.controls.description.setValue(n.description)
    this.labelForm.controls.icon.setValue(n.icon == null ? null : n.icon)
    this.labelForm.controls.image.setValue(n.image == null ? null : n.image)
    this.labelForm.controls.code.setValue(n.code)
    this.labelForm.controls.parent_id.setValue(n.parent_id)
    this.labelForm.controls.parent_code.setValue(n.parent_code)
    this.labelForm.controls.flagship.setValue(n.flagship)
    this.labelForm.controls.serial.setValue(n.serial)
    this.labelForm.controls.color.setValue(n.color)
    this.labelForm.controls.background_color.setValue(n.background_color)
    this.labelForm.controls.ui_height.setValue(n.ui_height)
  }

}
