import {BaseComponent} from "./base.component";
import {BreadcrumbItem} from "../../modules/common/modules/generic/breadcrumb/breadcrumb.model";
import {EventBusService} from "../../services/eventbus/event-bus.service";
import {ActivatedRoute, Router} from "@angular/router";
import {SubscriptionContainerService} from "../services/subscription-container.service";
import {Component, EventEmitter, Inject, Output} from "@angular/core";
import {MAT_DIALOG_DATA} from "@angular/material/dialog";
import {BaseConfig} from "../models/baseConfig";
import {UploadedImages} from "../../modules/webapp/models/uploads/IUploadFile";
import {translations} from "../../modules/dash/layout/left-nav/menu.model";

@Component({
  template: ''
})
export abstract class BaseUpsertComponent extends BaseComponent {

  protected readonly UploadedImages = UploadedImages;

  breadcrumbItems: BreadcrumbItem[] = []

  @Output() onCanceled: EventEmitter<void> = new EventEmitter<void>()
  @Output() onSuccess: EventEmitter<void> = new EventEmitter<void>()

  exId: number | null = null

  protected constructor(
    eventBus: EventBusService,
    router: Router,
    subContainer: SubscriptionContainerService,
    private route: ActivatedRoute,
    @Inject(MAT_DIALOG_DATA) private dialogData: any
  ) {
    super(eventBus, router, subContainer);
    this.breadcrumbItems = this.getBreadcrumbItems();
  }

  override ngOnInit(): void {
    this.loading = false
    this.route.paramMap.subscribe(paramMap => {
      let exId = paramMap.get('id');
      if (exId == null) exId = this.dialogData?.id
      if (exId != null) {
        this.exId = parseInt(exId)
        this.initialize(this.exId)
      }
    })
  }

  onCancel() {
    if (!this.getConfig().upsertOnDialog) {
      this.navigate(this.getListPageRoute(), value => {
      })
    } else {
      this.onCanceled.emit()
    }
  }

  protected abstract getBreadcrumbItems(): BreadcrumbItem[];

  abstract initialize(exId: number): void

  abstract getConfig(): BaseConfig

  abstract getListPageRoute(): string
}
