import {Component, ViewChild} from '@angular/core';
import {BaseComponent} from "../../../../../base/components/base.component";
import {EventBusService} from "../../../../../services/eventbus/event-bus.service";
import {ActivatedRoute, Router} from "@angular/router";
import {SubscriptionContainerService} from "../../../../../base/services/subscription-container.service";
import {NotificationService} from "../../../../common/modules/social/notifications/services/notification.service";
import {FormControl, FormGroup, Validators} from "@angular/forms";
import {
  INotification,
  INotificationReq,
} from "../../../../common/modules/social/notifications/models/notification.model";
import {UploadedImages} from "../../../../webapp/models/uploads/IUploadFile";
import {
  ImageUploaderComponent
} from "../../../../common/modules/cms/files/components/image-uploader/image-uploader.component";
import {EventKey} from "../../../../../services/eventbus/event.key";
import {HttpErrorResponse} from "@angular/common/http";
import {NotifierService} from "../../../../../services/notifier/notifier.service";
import {UserSlice} from "../../../../common/modules/auth/models/user.model";
import {UserService} from "../../../../common/modules/auth/services/user.service";
import {TextUtility} from "../../../../common/modules/utility/text.utility";

@Component({
  selector: 'app-notification-upsert',
  templateUrl: './notification-upsert.component.html',
  styleUrls: ['./notification-upsert.component.scss']
})
export class NotificationUpsertComponent extends BaseComponent {
  protected readonly UploadedImages = UploadedImages;

  exId: number | null = null

  @ViewChild(ImageUploaderComponent, {static: false})
  private uploadComponent: ImageUploaderComponent

  notification: INotificationReq = new INotificationReq()
  users: UserSlice[] = []

  notificationForm = new FormGroup(
    {
      title: new FormControl(this.notification.title, [
        Validators.required, Validators.minLength(4)
      ]),
      description: new FormControl(this.notification.description, []),
      response: new FormControl(this.notification.response, [
        Validators.maxLength(511)
      ]),
      text_color: new FormControl(this.notification.text_color, [
        Validators.required
      ]),
      background_color: new FormControl(this.notification.background_color, [
        Validators.required
      ]),
      action_color: new FormControl(this.notification.action_color, [
        Validators.required
      ]),
      priority: new FormControl(this.notification.priority, [
        Validators.required
      ]),
      redirect_url: new FormControl(this.notification.redirect_url, [
        Validators.maxLength(511)
      ]),
      sticky: new FormControl(this.notification.sticky, []),
      user_id: new FormControl(this.notification.user_id, []),
      image: new FormControl(this.notification.image, [])
    }
  );

  protected componentName(): string {
    return "NotificationUpsertComponent";
  }

  constructor(eventBus: EventBusService,
              router: Router,
              subContainer: SubscriptionContainerService,
              private notificationService: NotificationService,
              private notifier: NotifierService,
              private userService: UserService,
              private route: ActivatedRoute
  ) {
    super(eventBus, router, subContainer)
  }

  ngOnInit(): void {
    this.loading = false
    this.route.paramMap.subscribe(paramMap => {
      let exId = paramMap.get('id');
      if (exId != null) {
        this.exId = parseInt(exId)
        this.loadExNotification(this.exId)
      }
    })
  }

  onSubmit() {
    if (!this.notificationForm.valid) {
      return
    }
    this.uploadComponent.uploadImages()
  }

  onImgUploaded(images: UploadedImages) {
    if (images.thumbs != null && images.thumbs.length > 0) {
      this.notificationForm.controls.image.setValue(images.thumbs[0])
    }
    this.upsertNotification(this.exId)
  }

  upsertNotification(exId: number | null) {
    this.loading = true
    let key = this.componentKey(EventKey.NOTIFICATIONS.UPSERT(exId))
    console.log("formValue")
    console.log(this.notificationForm.value)
    // @ts-ignore
    this.notificationService.upsert(key, this.notificationForm.value, exId)
    this.subscribe(key, null, (value, meta) => {
      this.loading = false;
      this.notifier.success("Success", "Poll created")
      this.notificationForm.reset();
      this.navigate("/dash/notifications", value => {
        this.notifier.success("Success!", "Successfully created/updated notification.")
      })
    })
    this.subscribe(this.getErrorKey(key), null, (value, meta) => {
      let error: HttpErrorResponse = meta.data
      this.loading = false;
      this.notifier.warning('Failed!', error.error.message)
    })
  }

  searchUsersForEvent(event: any) {
    let query = TextUtility.removeSpecialCharacters(event.target?.value)
    if (query.length > 2) {
      this.searchUsers(query)
    }
  }

  searchUsers(query: string) {
    let key = this.componentKey(EventKey.USERS.SEARCH_USERS())
    this.userService.searchUsers(key, {
      page: 0, size: 10,
      q: query, slice: true
    })
    this.subscribe(key, null, (value, meta) => {
      this.users = meta.data.content
    })
  }

  private loadExNotification(exId: number) {
    this.loading = true
    let key = this.componentKey(EventKey.NOTIFICATIONS.GET(exId))
    this.notificationService.fetchSingle(key, exId)
    this.subscribe(key, null, (value, meta) => {
      this.loading = false;
      this.updateForm(meta.data)
    })
    this.subscribe(this.getErrorKey(key), null, (value, meta) => {
      let error: HttpErrorResponse = meta.data
      this.loading = false;
    })
  }

  private updateForm(n: INotification) {
    this.users = [{
      id: n.user_id,
      name: n.user_full_name,
      username: '',
      avatar: '',
      label: n.user_full_name
    }]
    this.notificationForm.controls.user_id.setValue(n.user_id)
    this.notificationForm.controls.title.setValue(n.title)
    this.notificationForm.controls.description.setValue(n.description)
    this.notificationForm.controls.response.setValue(n.response)
    this.notificationForm.controls.text_color.setValue(n.text_color)
    this.notificationForm.controls.background_color.setValue(n.background_color)
    this.notificationForm.controls.action_color.setValue(n.action_color)
    this.notificationForm.controls.priority.setValue(n.priority)
    this.notificationForm.controls.redirect_url.setValue(n.redirect_url)
    this.notificationForm.controls.image.setValue(n.image == null ? null : n.image)
    this.notification.image = n.image
    this.notificationForm.controls.sticky.setValue(n.sticky)
  }

}
