import {Injectable} from "@angular/core";
import {BaseService} from "../../../../../../base/services/base.service";
import {AuthService} from "../../../../../common/modules/auth/services/auth.service";
import {NotifierService} from "../../../../../../services/notifier/notifier.service";
import {HttpClient, HttpErrorResponse} from "@angular/common/http";
import {EventBusService} from "../../../../../../services/eventbus/event-bus.service";
import {IParams} from "../../../../../../base/endpoints/iparams";
import {DashEndpoint} from "../../../../dash.endpoint";
import {IPage} from "../../../../../../base/models/IPage";
import {IAuth} from "../../../../../common/modules/auth/models/auth.model";
import {ILocation, ILocationReq} from "../models/location.model";
import {ILabel} from "../../../labels/models/label.model";
import {InventoryEndpoints} from "../../../inventory/inventory.endpoints";

@Injectable({
  providedIn: 'root'
})
export class LocationService extends BaseService {

  constructor(
    authService: AuthService,
    notifier: NotifierService,
    private http: HttpClient,
    private eventBusService: EventBusService
  ) {
    super(authService, notifier)
  }

  searchChildren(key: string, endpoint: string, params: IParams) {
    let url = DashEndpoint.LOCATIONS.SEARCH(endpoint, params)
    this.http.get<IPage<ILocation>>(url).subscribe((location: IPage<ILocation>) => {
        this.eventBusService.publish(key, location)
      }, error => {
        this.handleError(error, (auth: IAuth) => {
          this.searchChildren(key, endpoint, params)
        }, (err: HttpErrorResponse) => {
          this.eventBusService.publish(this.getErrorKey(key), err)
        })
      })
  }

  getLocationsForType(key: string, endpoint: string, typeId: number) {
    let url = DashEndpoint.LOCATIONS.GET_LOCATIONS_FOR_TYPE(endpoint, typeId)
    this.http.get<IPage<ILocation>>(url)
      .subscribe((location: IPage<ILocation>) => {
        this.eventBusService.publish(key, location)
      }, error => {
        this.handleError(error, (auth: IAuth) => {
          this.getLocationsForType(key, endpoint, typeId)
        }, (err: HttpErrorResponse) => {
          this.eventBusService.publish(this.getErrorKey(key), err)
        })
      })
  }

  fetchSingle(key: string, id: number) {
    this.http.get<ILocation>(DashEndpoint.LOCATIONS.GET(id))
      .subscribe((location: ILocation) => {
        this.eventBusService.publish(key, location)
      }, (error: HttpErrorResponse) => {
        this.handleError(error, (auth: IAuth) => {
          this.fetchSingle(key, id)
        }, (err: HttpErrorResponse) => {
          this.eventBusService.publish(this.getErrorKey(key), err)
        })
      })
  }

  fetchMultiple(key: string, ids: number[]) {
    this.http.get<ILabel[]>(DashEndpoint.LOCATIONS.GET_MULTIPLE(ids))
      .subscribe((label: ILabel[]) => {
        this.eventBusService.publish(key, label)
      }, (error: HttpErrorResponse) => {
        this.handleError(error, (auth: IAuth) => {
          this.fetchMultiple(key, ids)
        }, (err: HttpErrorResponse) => {
          this.eventBusService.publish(this.getErrorKey(key), err)
        })
      })
  }

  upsert(key: string, locationReq: ILocationReq, exId: number | null) {
    let endpoint = exId == null ? DashEndpoint.LOCATIONS.CREATE() : DashEndpoint.LOCATIONS.UPDATE(exId)
    let observable = exId == null ? this.http.post<number>(endpoint, locationReq)
      : this.http.patch(endpoint, locationReq)
    observable.subscribe(res => {
      this.eventBusService.publish(key, res)
    }, (err: HttpErrorResponse) => {
      this.handleError(err, (auth: IAuth) => {
        this.upsert(key, locationReq, exId)
      }, (err: HttpErrorResponse) => {
        this.eventBusService.publish(this.getErrorKey(key), err)
      })
    })
  }

}
