import {Component, EventEmitter, Inject, Input, OnInit, Output} from '@angular/core';
import {BaseComponent} from "../../../../../../base/components/base.component";
import {EventBusService} from "../../../../../../services/eventbus/event-bus.service";
import {Router} from "@angular/router";
import {SubscriptionContainerService} from "../../../../../../base/services/subscription-container.service";
import {FileUploadService} from "../../../../../../services/files/file-upload.service";
import {MAT_DIALOG_DATA, MatDialog, MatDialogRef} from "@angular/material/dialog";
import {LabelService} from "../../services/label.service";
import {NotifierService} from "../../../../../../services/notifier/notifier.service";
import {PollService} from "../../../poll/services/poll.service";
import {FormGroup} from "@angular/forms";
import {ILabel} from "../../models/ilabel";
import {IParams} from "../../../../../../base/endpoints/iparams";
import {EventKey} from "../../../../../../services/eventbus/event.key";
import {MetaData} from "ng-event-bus/lib/meta-data";
import {IPage} from "../../../../../../base/models/IPage";
import {HttpErrorResponse} from "@angular/common/http";
import {WebAppEndpoint} from "../../../../../webapp/web-app.endpoint";

@Component({
  selector: 'app-label-update',
  templateUrl: './label-update.component.html',
  styleUrls: ['./label-update.component.scss']
})
export class LabelUpdateComponent extends BaseComponent implements OnInit {
  labelForm: FormGroup

  @Output() formSubmitEvent: EventEmitter<boolean> = new EventEmitter<boolean>()
  labelList: ILabel[] = []
  labels: IPage<ILabel>

  protected componentName(): string {
    return "LabelUpdateComponent";
  }

  constructor(
    eventBus: EventBusService,
    router: Router,
    subContainer: SubscriptionContainerService,
    private labelService: LabelService,
    private notifier: NotifierService,
    @Inject(MAT_DIALOG_DATA) public data: any,
  ) {
    super(eventBus, router, subContainer)
  }

  ngOnInit(): void {
    this.loading = false;
    this.labelForm = this.labelService.populateLabelForm(this.data)
    this.getLabels({size: 20})
  }

  onSearchLabel(event: any) {
    this.getLabels({
      size: 20,
      q: event?.target?.value
    })
  }

  private getLabels(params: IParams) {
    this.loading = true
    const key = this.componentKey(EventKey.LABELS_LEGACY.SEARCH())
    this.labelService.searchLabels(key, WebAppEndpoint.LABELS.SEARCH_ENDPOINT_ADMIN, params)
    this.subscribe(
      key,
      null,
      (value: Boolean, meta: MetaData) => {
        this.labels = meta.data
        this.labelList = this.labels.content
        this.loading = false
      })
    this.subscribe(this.getErrorKey(key), null, (value, meta) => {
      this.notifier.warning('Error', meta.data.error.message)
      this.loading = false

    })
  }

  onScrollLabelToEnd() {
    if (!this.labels.last) {
      this.getLabels({
        size: this.labels.numberOfElements + 20,
      })
    }
  }

  updateLabel() {
    this.loading = true;
    let key = this.componentKey(EventKey.LABELS_LEGACY.UPDATE(this.labelForm.value.id))
    this.labelService.updateLabel(key, this.labelForm.value)

    this.subscribe(key, null, (value, meta) => {
      this.notifier.success("Success", "Label updated")
      this.formSubmitEvent.emit(true)
      this.loading = false;

    })
    this.subscribe(this.getErrorKey(key), null, (value, meta) => {
      let error: HttpErrorResponse = meta.data
      this.notifier.warning('Failed!', error.error.message)
      this.loading = false;

    })
  }
}
