import {Component} from '@angular/core';
import {defaultPage, IPage} from "../../../../../../base/models/IPage";
import {ILabel} from "../../models/label.model";
import {defaultLabelConfig, LabelConfig, selfRoutes} from "../../models/config.model";
import {BreadcrumbItem} from "../../../../../common/modules/generic/breadcrumb/breadcrumb.model";
import {AuthService} from "../../../../../common/modules/auth/services/auth.service";
import {EventBusService} from "../../../../../../services/eventbus/event-bus.service";
import {ActivatedRoute, Router} from "@angular/router";
import {SubscriptionContainerService} from "../../../../../../base/services/subscription-container.service";
import {DashEndpoint} from "../../../../dash.endpoint";
import {parseFilterData} from "../../../../../common/modules/utility/components/basic-filter/basic-filter.model";
import {IParams} from "../../../../../../base/endpoints/iparams";
import {EventKey} from "../../../../../../services/eventbus/event.key";
import {MetaData} from "ng-event-bus/lib/meta-data";
import {LabelService} from "../../services/label.service";
import {NotifierService} from "../../../../../../services/notifier/notifier.service";
import {BaseListComponent} from "../../../../../../base/components/base-list.component";
import {MatDialog} from "@angular/material/dialog";
import {ComponentType} from "@angular/cdk/overlay";
import {BaseUpsertComponent} from "../../../../../../base/components/base-upsert.component";
import {LabelUpsertComponent} from "../label-upsert/label-upsert.component";
import {BaseConfig} from "../../../../../../base/models/baseConfig";
import {BaseListComponentV2} from "../../../../../../base/components/base-list-v2.component";
import {BaseUpsertComponentV2} from "../../../../../../base/components/base-upsert-v2.component";
import {DialogService} from "../../../../../common/modules/utility/components/popup/dialog.service";

@Component({
  selector: 'app-label-list',
  templateUrl: './label-list.component.html',
  styleUrls: ['./label-list.component.scss']
})
export class LabelListComponent extends BaseListComponentV2<ILabel> {
  protected readonly selfRoutes = selfRoutes;

  config: LabelConfig = defaultLabelConfig

  labels: IPage<ILabel> = defaultPage

  protected componentName(): string {
    return "LabelListComponent";
  }

  constructor(eventBus: EventBusService,
              router: Router,
              subContainer: SubscriptionContainerService,
              authService: AuthService,
              private activatedRoute: ActivatedRoute,
              private labelService: LabelService,
              private notifier: NotifierService,
              popup: DialogService<ILabel>) {
    super(eventBus, router, subContainer, popup)
    if (authService.isAdmin()) {
      this.config.showUserFilter = true
      this.config.showDeleteButton = true
      this.config.searchEndpoint = DashEndpoint.LABELS.LABEL_ENDPOINT_ADMIN
    }

  }

  protected getBreadcrumbItems(): BreadcrumbItem[] {
    return this.breadcrumbItems;
  }

  getUpsertComponent(): ComponentType<BaseUpsertComponentV2<ILabel>> {
    return LabelUpsertComponent;
  }

  getConfig(): BaseConfig {
    return this.config;
  }

  override getRoutes(): { create: string, update: (id: number) => string, params: IParams } | null {
    return {
      create: selfRoutes.create,
      update: (id: number) => selfRoutes.update(id),
      params: {}
    }
  }

  protected initialize(): void {
    this.activatedRoute.queryParams.subscribe(params => {
      let filterData = parseFilterData(params)
      let parentId: number | null = params['parent_id'] ? parseInt(params['parent_id']) : null
      this.fetchLabels({
        q: filterData.query,
        username: filterData.username,
        from_date: filterData.fromDate,
        to_date: filterData.toDate,
        parent_id: parentId,
        page: filterData.page,
        size: filterData.size
      })

      // // get ancestors for label for breadcrumb
      // this.getMultipleByIds()
    });
  }

  private fetchLabels(params: IParams) {
    this.loading = true
    const key = this.componentKey(EventKey.LABELS.SEARCH(params))
    this.labelService.search(key, this.config.searchEndpoint, params)
    this.subscribe(
      key,
      null,
      (value: Boolean, meta: MetaData) => {
        this.labels = meta.data
        this.loading = false
      })
    this.subscribe(this.getErrorKey(key), null, (value, meta) => {
      this.notifier.error('Error', meta.data.error.message)
    })
  }

  private getMultipleByIds(ids: number[]) {
    this.loading = true
    const key = this.componentKey(EventKey.LABELS.GET_MULTIPLE(ids))
    this.labelService.fetchMultiple(key, ids)
    this.subscribe(
      key,
      null,
      (value: Boolean, meta: MetaData) => {
        let labels = meta.data as ILabel[]
        let breadcrumbItems: BreadcrumbItem[] = [{
          text: "Labels",
          link: selfRoutes.list,
          active: false
        }]
        labels.map(label => {
          // map label to breadcrumb item
          breadcrumbItems.push({
            text: label.name,
            link: selfRoutes.getChildren(label.id),
            active: false
          })
        })
        this.breadcrumbItems = breadcrumbItems
        this.loading = false
      })
    this.subscribe(this.getErrorKey(key), null, (value, meta) => {
      this.notifier.error('Error', meta.data.error.message)
    })
  }

  onDeleted(deleted: Boolean) {
    this.ngOnInit()
  }

  onLabelClick(item: ILabel) {
    this.navigateByUrl(selfRoutes.getChildren(item.id), then => {
      this.getMultipleByIds(item.absolute_path.split(':').map(id => parseInt(id)))
    })
  }

}
