import {NgModule} from '@angular/core';
import {RouterModule, Routes} from '@angular/router';
import {HomePageComponent} from "./pages/home-page/home-page.component";
import {PollEditFormComponent} from "./domains/poll/components/poll-edit-form/poll-edit-form.component";
import {
  QuestionEditFormComponent
} from "./domains/questionnaires/components/question-edit-form/question-edit-form.component";
import {NotificationListComponent} from "./domains/notifications/notification-list/notification-list.component";
import {NotificationUpsertComponent} from "./domains/notifications/notification-upsert/notification-upsert.component";
import {VoteListComponent} from "./domains/poll/components/vote-list/vote-list.component";
import {
  QuestionnairePageComponent
} from "./domains/questionnaires/page/questionnaire-page/questionnaire-page.component";
import {PollPageComponent} from "./domains/poll/pages/poll-page/poll-page.component";
import {AuthGuardService} from "../common/modules/auth/services/auth-guard.service";
import {ParticipantsComponent} from "./domains/exam/participants/participants.component";
import {ResultSheetComponent} from "./domains/exam/result-sheet/result-sheet.component";
import {AnswerSheetComponent} from "./domains/exam/answer-sheet/answer-sheet.component";
import {CrudExampleComponent} from "./domains/crudexample/crudexample.component";
import {
  CrudExampleUpsertComponent
} from "./domains/crudexample/components/crudexample-upsert/crudexample-upsert.component";
import {LabelUpsertComponent} from "./domains/labels/components/label-upsert/label-upsert.component";
import {LabelListComponent} from "./domains/labels/components/label-list/label-list.component";
import {FeedbackComponent} from "./domains/feedbacks/feedback.component";
import {FeedbackUpsertComponent} from "./domains/feedbacks/components/feedback-upsert/feedback-upsert.component";
import {POptionComponent} from "./domains/poll/poll-options/poption.component";
import {LocationTypeComponent} from "./domains/geo/locationtypes/locationtype.component";
import {
  LocationTypeUpsertComponent
} from "./domains/geo/locationtypes/components/locationtype-upsert/locationtype-upsert.component";
import {LocationComponent} from "./domains/geo/locations/location.component";
import {LocationUpsertComponent} from "./domains/geo/locations/components/location-upsert/location-upsert.component";
import {AddressComponent} from "./domains/geo/addresss/address.component";
import {AddressUpsertComponent} from "./domains/geo/addresss/components/address-upsert/address-upsert.component";
import {HomeComponent} from "../webapp/pages/home/home.component";
import {OverviewStatsComponent} from "./domains/poll/components/overview-stats/overview-stats.component";
import {SalesDashboardComponent} from "./pages/dashboards/sales/sales-dashboard/sales-dashboard.component";
import {OrganizationComponent} from "./domains/organizations/organization.component";
import {
  OrganizationUpsertComponent
} from "./domains/organizations/components/organization-upsert/organization-upsert.component";

const routes: Routes = [
  {path: '', component: HomeComponent},
  {path: 'dashboard', component: HomePageComponent},
  {path: 'dash/teacher-dashboard', component: OverviewStatsComponent},
  {path: 'dash/sales-dashboard', component: SalesDashboardComponent},
  {path: 'questionnaires', component: QuestionnairePageComponent},
  {path: 'polls', component: PollPageComponent},
  {path: 'poll-form', component: PollEditFormComponent},
  {path: 'poll-form/:id', component: PollEditFormComponent},
  {path: 'question-form', component: QuestionEditFormComponent},
  {path: 'questions/question-form', component: QuestionEditFormComponent},
  {path: 'question-form/:id', component: QuestionEditFormComponent},
  {path: 'poll-options', component: POptionComponent},
  {path: 'notifications', component: NotificationListComponent},
  {path: 'notifications/create', component: NotificationUpsertComponent},
  {path: 'notifications/:id/update', component: NotificationUpsertComponent},
  {path: 'votes', component: VoteListComponent},
  {
    path: 'users',
    loadChildren: () => import('./domains/users/user.module').then(m => m.UserModule),
    canActivate: [AuthGuardService]
  },
  {path: 'exams/questionnaires/:id/participants', component: ParticipantsComponent, canActivate: [AuthGuardService]},
  {path: 'exams/questionnaires/:id/result-sheet', component: ResultSheetComponent, canActivate: [AuthGuardService]},
  {
    path: 'exams/questionnaires/:id/participants/:participantId/mark-sheet',
    component: AnswerSheetComponent,
    canActivate: [AuthGuardService]
  },
  {path: 'crudexamples', component: CrudExampleComponent, canActivate: [AuthGuardService]},
  {path: 'crudexamples/:id/update', component: CrudExampleUpsertComponent, canActivate: [AuthGuardService]},
  {path: 'crudexamples/create', component: CrudExampleUpsertComponent, canActivate: [AuthGuardService]},
  {path: 'labels', component: LabelListComponent, canActivate: [AuthGuardService]},
  {path: 'labels/create', component: LabelUpsertComponent, canActivate: [AuthGuardService]},
  {path: 'labels/:id/update', component: LabelUpsertComponent, canActivate: [AuthGuardService]},
  {path: 'feedbacks', component: FeedbackComponent, canActivate: [AuthGuardService]},
  {path: 'feedbacks/create', component: FeedbackUpsertComponent, canActivate: [AuthGuardService]},
  {path: 'feedbacks/:id/update', component: FeedbackUpsertComponent, canActivate: [AuthGuardService]},
  {
    path: 'inventory',
    loadChildren: () => import('./domains/inventory/inventory.module').then(m => m.InventoryModule),
    canActivate: [AuthGuardService]
  },
  {
    path: 'sales',
    loadChildren: () => import('./domains/sales/sales.module').then(m => m.SalesModule),
    canActivate: [AuthGuardService]
  },
  {
    path: 'accounts',
    loadChildren: () => import('../common/modules/accounting/accounting.module').then(m => m.AccountingModule),
    canActivate: [AuthGuardService]
  },
  {path: 'location-types', component: LocationTypeComponent, canActivate: [AuthGuardService]},
  {path: 'location-types/create', component: LocationTypeUpsertComponent, canActivate: [AuthGuardService]},
  {path: 'location-types/:id/update', component: LocationTypeUpsertComponent, canActivate: [AuthGuardService]},
  {path: 'locations', component: LocationComponent, canActivate: [AuthGuardService]},
  {path: 'locations/create', component: LocationUpsertComponent, canActivate: [AuthGuardService]},
  {path: 'locations/:id/update', component: LocationUpsertComponent, canActivate: [AuthGuardService]},
  {path: 'addresses', component: AddressComponent, canActivate: [AuthGuardService]},
  {path: 'addresses/create', component: AddressUpsertComponent, canActivate: [AuthGuardService]},
  {path: 'addresses/:id/update', component: AddressUpsertComponent, canActivate: [AuthGuardService]},
  {path: 'organizations', component: OrganizationComponent, canActivate: [AuthGuardService]},
  {path: 'organizations/create', component: OrganizationUpsertComponent, canActivate: [AuthGuardService]},
  {path: 'organizations/:id/update', component: OrganizationUpsertComponent, canActivate: [AuthGuardService]},
  {
    path: 'hr',
    loadChildren: () => import('./domains/hr/hr.module').then(m => m.HrModule),
    canActivate: [AuthGuardService]
  },
];

@NgModule({
  imports: [RouterModule.forChild(routes)],
  exports: [RouterModule]
})
export class DashRoute {
}
