import {BaseComponent} from "./base.component";
import {BreadcrumbItem, Layout} from "../../modules/common/modules/generic/breadcrumb/breadcrumb.model";
import {EventBusService} from "../../services/eventbus/event-bus.service";
import {Router} from "@angular/router";
import {SubscriptionContainerService} from "../services/subscription-container.service";
import {MatDialog} from "@angular/material/dialog";
import {ComponentType} from "@angular/cdk/overlay";
import {BaseUpsertComponent} from "./base-upsert.component";
import {BaseConfig} from "../models/baseConfig";
import {Component} from "@angular/core";
import {StringUtil} from "../../utils/string.util";
import {slideIn} from "../animations/component.animation";

@Component({
  template: '',
  animations: [
    slideIn('onGrid', 'left'),
    slideIn('onColumn', 'right')
  ]
})
export abstract class BaseListComponent extends BaseComponent {
  protected readonly Layout = Layout;
  protected readonly StringUtil = StringUtil;

  protected breadcrumbItems: BreadcrumbItem[] = []
  protected layout: Layout = Layout.GRID

  protected constructor(
    eventBus: EventBusService,
    router: Router,
    subContainer: SubscriptionContainerService,
    private dialog: MatDialog,
  ) {
    super(eventBus, router, subContainer);
    this.breadcrumbItems = this.getBreadcrumbItems();
  }

  override ngOnInit() {
    this.layout = this.getConfig().defaultLayout
    this.initialize()
  }

  protected abstract initialize(): void

  protected abstract getBreadcrumbItems(): BreadcrumbItem[];

  abstract getUpsertComponent(): ComponentType<BaseUpsertComponent> | null;

  abstract getConfig(): BaseConfig

  abstract getRoutes(): { create: string, update: (id: number) => string } | null

  onLayoutSelected(layout: Layout) {
    this.layout = layout
  }

  openUpsertComponent(id: number | null) {
    let upsertComponent = this.getUpsertComponent()
    if (upsertComponent == null) {
      return
    }
    const dialogRef = this.dialog.open(upsertComponent, {
      disableClose: true,
      data: {
        id: id
      },
    })
    let component = dialogRef.componentInstance
    component.onCanceled.subscribe(() => {
      dialogRef.close()
    })
    component.onSuccess.subscribe(() => {
      this.ngOnInit()
      console.log("onSuccess. closing dialog")
      dialogRef.close()
    })
  }

  onUpsertClick(id: number | null) {
    if (this.getConfig().upsertOnDialog) {
      this.openUpsertComponent(id)
    } else {
      let routes = this.getRoutes()
      if (routes == null) {
        return
      }
      let route = id == null ? routes.create : routes.update(id)
      this.navigate(route, value => {
      })
    }
  }

}
