import {Injectable} from '@angular/core';
import {BaseService} from "../../../../../base/services/base.service";
import {AuthService} from "../../../../common/modules/auth/services/auth.service";
import {NotifierService} from "../../../../../services/notifier/notifier.service";
import {HttpClient} from "@angular/common/http";
import {FormBuilder, FormControl} from "@angular/forms";
import {EventBusService} from "../../../../../services/eventbus/event-bus.service";
import {IPost} from "../../questionnaires/models/IPost";

@Injectable({
  providedIn: 'root'
})
export class PostService extends BaseService {

  constructor(authService: AuthService,
              notifier: NotifierService,
              private http: HttpClient,
              private formBuilder: FormBuilder,
              private eventBusService: EventBusService) {
    super(authService, notifier);
  }

  getPostForm(type: String) {
    return this.formBuilder.group({
      content: new FormControl(""),
      hashtags: new FormControl([]),
      images: new FormControl(),
      is_public: new FormControl(),
      thumbs: new FormControl([]),
      type: new FormControl(type)
    })
  }

  populatePostForm(post: IPost) {
    return this.formBuilder.group({
      id: new FormControl(post.id),
      content: new FormControl(post.content),
      hashtags: new FormControl(post.hashtags),
      images: new FormControl(post.images),
      is_public: new FormControl(post.is_public),
      thumbs: new FormControl(post.thumbs),
      type: new FormControl(post.type)
    })
  }
}
