import {Component, Inject} from '@angular/core';
import {FormControl, FormGroup, Validators} from "@angular/forms";
import {BaseUpsertComponentV2} from "../../../../../../../base/components/base-upsert-v2.component";
import {defaultUnitConfig, selfRoutes, UnitConfig} from "../../models/config.model";
import {EventBusService} from "../../../../../../../services/eventbus/event-bus.service";
import {ActivatedRoute, Router} from "@angular/router";
import {SubscriptionContainerService} from "../../../../../../../base/services/subscription-container.service";
import {UnitService} from "../../services/unit.service";
import {NotifierService} from "../../../../../../../services/notifier/notifier.service";
import {MAT_DIALOG_DATA} from "@angular/material/dialog";
import {BreadcrumbItem} from "../../../../../../common/modules/generic/breadcrumb/breadcrumb.model";
import {EventKey} from "../../../../../../../services/eventbus/event.key";
import {HttpErrorResponse} from "@angular/common/http";
import {BaseConfig} from "../../../../../../../base/models/baseConfig";
import {IUnit, IUnitDetail, IUnitReq} from "../../models/unit.model";

@Component({
  selector: 'app-unit-upsert',
  templateUrl: './unit-upsert.component.html',
  styleUrls: ['./unit-upsert.component.scss']
})
export class UnitUpsertComponent extends BaseUpsertComponentV2<IUnitDetail> {

  config: UnitConfig = defaultUnitConfig

  unitForm = new FormGroup({
    title: new FormControl('', [Validators.required, Validators.minLength(3)]),
    short_code: new FormControl('', [Validators.required, Validators.minLength(1), Validators.maxLength(10)]),
    symbol: new FormControl('', [Validators.maxLength(5)]),
    description: new FormControl('', [Validators.required, Validators.minLength(10)]),
  })

  constructor(eventBus: EventBusService,
              router: Router,
              subContainer: SubscriptionContainerService,
              route: ActivatedRoute,
              private unitService: UnitService,
              private notifier: NotifierService,
              @Inject(MAT_DIALOG_DATA) dialogData: any) {
    super(eventBus, router, subContainer, route, dialogData)

    if (dialogData.title != null) {
      this.unitForm.controls.title.setValue(dialogData.title)
    }

  }

  protected override componentName(): string {
    return "UnitUpsertComponent"
  }

  protected override getBreadcrumbItems(): BreadcrumbItem[] {
    return [
      {
        text: this.translations["Dashboard"],
        link: null,
        active: false
      },
      {
        text: "Units",
        link: "/dash/units",
        active: false
      },
      {
        text: "Upsert",
        link: null,
        active: true
      }
    ]
  }

  override initialize(exId: number): void {
    this.loading = true
    let key = this.componentKey(EventKey.UNITS.GET(exId))
    this.unitService.fetchSingle(key, exId)
    this.subscribe(key, null, (value, meta) => {
      this.loading = false;
      this.populateForm(meta.data)
    })
    this.subscribe(this.getErrorKey(key), null, (value, meta) => {
      let error: HttpErrorResponse = meta.data
      console.log(error)
      this.loading = false;
    })
  }

  onSubmit() {
    if (!this.unitForm.valid) {
      return
    }
    this.upsert(this.exId)
  }

  getConfig(): BaseConfig {
    return this.config;
  }

  getListPageRoute(): string {
    return selfRoutes.list;
  }

  upsert(exId: number | null) {
    this.loading = true
    let key = this.componentKey(EventKey.UNITS.UPSERT(exId))
    let req = this.unitForm.value as IUnitReq
    this.unitService.upsert(key, req, exId)
    this.subscribe(key, null, (value, meta) => {
      this.notifier.success("Success", "Unit has been " + (exId ? "updated" : "created"))
      this.unitForm.reset();
      this.onSuccess(meta.data)
    })
    this.subscribe(this.getErrorKey(key), null, (value, meta) => {
      let error: HttpErrorResponse = meta.data
      this.loading = false;
      this.notifier.warning('Failed!', error.error.message)
    })
  }

  private populateForm(n: IUnit) {
    this.unitForm.controls.title.setValue(n.title)
    this.unitForm.controls.description.setValue(n.description)
    this.unitForm.controls.short_code.setValue(n.short_code)
    this.unitForm.controls.symbol.setValue(n.symbol? n.symbol : null)
  }

}
