import {Component, EventEmitter, Input, Output} from '@angular/core';
import {BreadcrumbItem, Layout} from "./breadcrumb.model";
import {Router} from "@angular/router";

@Component({
  selector: 'app-breadcrumb',
  templateUrl: './breadcrumb.component.html',
  styleUrls: ['./breadcrumb.component.scss']
})
export class BreadcrumbComponent {
  @Input() title: string = ""
  @Input() breadcrumbItems: BreadcrumbItem[] = []
  @Input() layout: Layout = Layout.COLUMN

  @Output() layoutSelected: EventEmitter<Layout> = new EventEmitter<Layout>()

  onLayoutSelected(layout: Layout) {
    this.layout = layout
    this.layoutSelected.emit(layout)
  }

  protected readonly Layout = Layout;

  constructor(private router: Router) {
  }

  onItemClick(item: BreadcrumbItem) {
    if (item.link) {
      this.router.navigateByUrl(item.link, {state: {breadcrumb: item}})
    }
  }
}
