import {EventBusService} from "../../../../../services/eventbus/event-bus.service";
import {Router} from "@angular/router";
import {SubscriptionContainerService} from "../../../../../base/services/subscription-container.service";
import {OrganizationService} from "../services/organization.service";
import {EventKey} from "../../../../../services/eventbus/event.key";
import {IOrganization} from "../models/organization.model";
import {BaseComponent} from "../../../../../base/components/base.component";

export abstract class BaseComponentForOrg extends BaseComponent {
  protected orgId: number | null = null

  protected constructor(eventBus: EventBusService,
                        router: Router,
                        subContainer: SubscriptionContainerService,
                        private orgService: OrganizationService
  ) {
    super(eventBus, router, subContainer)
  }

  protected onOrgLoaded(block: (orgId: number) => void) {
    this.loading = true
    // fetch My org key
    let key = this.componentKey(EventKey.ORGANIZATIONS.GET_MY_ORG())
    this.orgService.fetchMyOrgs(key)
    this.subscribe(key, null, (value, meta) => {
      this.loading = false
      let orgs = meta.data as IOrganization[]
      if (orgs.length > 0) {
        this.orgId = orgs[0].id
        block(this.orgId)
      }
    })
  }

}
