import {Component} from '@angular/core';
import {BaseListComponentV2} from "../../../../../../../base/components/base-list-v2.component";
import {defaultLocationTypeConfig, LocationTypeConfig, selfRoutes} from "../../models/config.model";
import {
  BasicFilterData,
  defaultFilterData,
  parseFilterData,
  toParams
} from "../../../../../../common/modules/utility/components/basic-filter/basic-filter.model";
import {defaultPage, IPage} from "../../../../../../../base/models/IPage";
import {EventBusService} from "../../../../../../../services/eventbus/event-bus.service";
import {ActivatedRoute, Router} from "@angular/router";
import {SubscriptionContainerService} from "../../../../../../../base/services/subscription-container.service";
import {AuthService} from "../../../../../../common/modules/auth/services/auth.service";
import {LocationTypeService} from "../../services/locationtype.service";
import {NotifierService} from "../../../../../../../services/notifier/notifier.service";
import {DashEndpoint} from "../../../../../dash.endpoint";
import {BreadcrumbItem} from "../../../../../../common/modules/generic/breadcrumb/breadcrumb.model";
import {IParams} from "../../../../../../../base/endpoints/iparams";
import {EventKey} from "../../../../../../../services/eventbus/event.key";
import {MetaData} from "ng-event-bus/lib/meta-data";
import {ComponentType} from "@angular/cdk/overlay";
import {LocationTypeUpsertComponent} from "../locationtype-upsert/locationtype-upsert.component";
import {ILocationType} from "../../models/locationtype.model";
import {BaseUpsertComponentV2} from "../../../../../../../base/components/base-upsert-v2.component";
import {DialogService} from "../../../../../../common/modules/utility/components/popup/dialog.service";
import {BaseConfig} from "../../../../../../../base/models/baseConfig";

@Component({
  selector: 'app-locationtype-list',
  templateUrl: './locationtype-list.component.html',
  styleUrls: ['./locationtype-list.component.scss']
})
export class LocationTypeListComponent extends BaseListComponentV2<ILocationType> {

  config: LocationTypeConfig = defaultLocationTypeConfig
  filterData: BasicFilterData = defaultFilterData

  locationTypes: IPage<ILocationType> = defaultPage

  protected componentName(): string {
    return "LocationTypeListComponent";
  }

  constructor(eventBus: EventBusService,
              router: Router,
              subContainer: SubscriptionContainerService,
              authService: AuthService,
              dialog: DialogService<ILocationType>,
              private activatedRoute: ActivatedRoute,
              private locationTypeService: LocationTypeService,
              private notifier: NotifierService,
  ) {
    super(eventBus, router, subContainer, dialog)
    if (authService.isAdmin()) {
      this.config.searchEndpoint = DashEndpoint.LOCATIONTYPES.LOCATIONTYPE_ENDPOINT_ADMIN
    }
  }

  initialize(): void {
    this.activatedRoute.queryParams.subscribe(params => {
      this.filterData = parseFilterData(params)
      this.fetchLocationTypes({
        q: this.filterData.query,
        username: this.filterData.username,
        from_date: this.filterData.fromDate,
        to_date: this.filterData.toDate,
        page: this.filterData.page,
        size: this.filterData.size
      })
    });
  }

  protected override getBreadcrumbItems(): BreadcrumbItem[] {
    return [
      {
         text: this.translations["Dashboard"],
        link: null,
        active: false
      },
      {
        text: "LocationTypes",
        link: null,
        active: true
      }
    ]
  }

  private fetchLocationTypes(params: IParams) {
    this.loading = true
    const key = this.componentKey(EventKey.LOCATIONTYPES.SEARCH(params))
    this.locationTypeService.search(key, this.config.searchEndpoint, params)
    this.subscribe(
      key,
      null,
      (value: Boolean, meta: MetaData) => {
        this.locationTypes = meta.data
        this.loading = false
      })
    this.subscribe(this.getErrorKey(key), null, (value, meta) => {
      this.notifier.error('Error', meta.data.error.message)
    })
  }

  onDeleted(deleted: Boolean) {
    if (deleted) {
      this.notifier.success("Success", "LocationType is deleted")
      this.ngOnInit()
    }
  }

  override getConfig(): BaseConfig {
    return this.config
  }

  override getRoutes(): { create: string, update: (id: number) => string, params: IParams } | null {
    return {
      create: selfRoutes.create,
      update: (id: number) => selfRoutes.update(id),
      params: toParams(this.filterData)
    }
  }

  getUpsertComponent(): ComponentType<BaseUpsertComponentV2<ILocationType>> | null {
    return LocationTypeUpsertComponent
  }

  onTitleClick(item: ILocationType) {
    this.navigateByUrl(selfRoutes.getChildren(item.id), then => {
      this.getMultipleByIds(item.absolute_path.split(':').map(id => parseInt(id)))
    })
  }

  private getMultipleByIds(ids: number[]) {
    this.loading = true
    const key = this.componentKey(EventKey.LOCATIONS.GET_MULTIPLE(ids))
    this.locationTypeService.fetchMultiple(key, ids)
    this.subscribe(
      key,
      null,
      (value: Boolean, meta: MetaData) => {
        let types = meta.data as ILocationType[]
        let breadcrumbItems: BreadcrumbItem[] = [{
          text: "Locations",
          link: selfRoutes.list,
          active: false
        }]
        types.map(type => {
          // map label to breadcrumb item
          breadcrumbItems.push({
            text: type.label,
            link: selfRoutes.getChildren(type.id),
            active: false
          })
        })
        this.breadcrumbItems = breadcrumbItems
        this.loading = false
      })
    this.subscribe(this.getErrorKey(key), null, (value, meta) => {
      this.notifier.error('Error', meta.data.error.message)
    })
  }
}
