import {Component} from '@angular/core';
import {BaseComponent} from "../../../../../../base/components/base.component";
import {PollStats} from "../../../../models/poll-stats.model";
import {AuthService} from "../../../../../common/modules/auth/services/auth.service";
import {NotifierService} from "../../../../../../services/notifier/notifier.service";
import {EventBusService} from "../../../../../../services/eventbus/event-bus.service";
import {PollStatsService} from "../../../../services/poll-stats.service";
import {ActivatedRoute, Router} from "@angular/router";
import {SubscriptionContainerService} from "../../../../../../base/services/subscription-container.service";
import {EventKey} from "../../../../../../services/eventbus/event.key";
import {MetaData} from "ng-event-bus/lib/meta-data";
import {TimeUtility} from "../../../../../common/modules/utility/time.utility";
import * as moment from 'moment';
import {OverviewStatsConfig} from "./config";
import {WebAppEndpoint} from "../../../../../webapp/web-app.endpoint";
import {EarningChartConfig} from "../../../../components/earning-chart/config";
import {
  BasicFilterData,
  defaultFilterData,
  parseFilterData, toParams
} from "../../../../../common/modules/utility/components/basic-filter/basic-filter.model";
import {IParams} from "../../../../../../base/endpoints/iparams";

@Component({
  selector: 'app-overview-stats',
  templateUrl: './overview-stats.component.html',
  styleUrls: ['./overview-stats.component.scss']
})
export class OverviewStatsComponent extends BaseComponent {
  config: OverviewStatsConfig = {
    showUserFilter: false,
    endpoint: WebAppEndpoint.POLL_STATISTICS.OVERVIEW_STATS_ENDPOINT
  }

  earningsChartConfig: EarningChartConfig = {
    endpoint: WebAppEndpoint.POLL_STATISTICS.DATE_WISE_POLL_COUNTS_ENDPOINT
  }

  pollStats: PollStats = new PollStats()
  filterData: BasicFilterData = defaultFilterData

  constructor(private authService: AuthService,
              private notifier: NotifierService,
              eventBus: EventBusService,
              router: Router,
              subContainer: SubscriptionContainerService,
              private pollStatsService: PollStatsService,
              private activatedRoute: ActivatedRoute) {
    super(eventBus, router, subContainer)
    if (this.authService.isAdmin()) {
      this.config = {
        showUserFilter: true,
        endpoint: WebAppEndpoint.POLL_STATISTICS.OVERVIEW_STATS_ENDPOINT_ADMIN
      }
      this.earningsChartConfig = {
        endpoint: WebAppEndpoint.POLL_STATISTICS.DATE_WISE_POLL_COUNTS_ENDPOINT_ADMIN
      }
    }
  }

  getQueryParams() {
    return toParams(this.filterData)
  }

  protected componentName(): string {
    return "OverviewStatsComponent";
  }

  ngOnInit(): void {
    this.activatedRoute.queryParams.subscribe(params => {
      this.filterData = parseFilterData(params)
      this.loadPollStats({
        q: this.filterData.query,
        username: this.filterData.username,
        from_date: this.filterData.fromDate,
        to_date: this.filterData.toDate,
        page: this.filterData.page,
        size: this.filterData.size
      })
    });
  }

  private loadPollStats(params: IParams) {
    this.loading = true
    const key = this.componentKey(EventKey.POLL_STATS.OVERVIEW())
    this.pollStatsService.fetchOverviewStats(key, this.config.endpoint, params)
    this.subscribe(
      key,
      null,
      (value: Boolean, meta: MetaData) => {
        this.pollStats = meta.data
        this.loading = false
      })
    this.subscribe(this.getErrorKey(key), null, (value, meta) => {
      this.notifier.log(meta.data)
    })
  }

  getPayableEntityCount(): number {
    if (this.pollStats == null) return 0
    return this.pollStats.approved_poll_count + this.pollStats.approved_questionnaire_count
  }


}
