import {Component, EventEmitter, Input, Output} from '@angular/core';
import {BaseComponent} from "../../../../../../../base/components/base.component";
import {DashEndpoint} from "../../../../../dash.endpoint";
import {ILocationType} from "../../models/locationtype.model";
import {EventBusService} from "../../../../../../../services/eventbus/event-bus.service";
import {Router} from "@angular/router";
import {SubscriptionContainerService} from "../../../../../../../base/services/subscription-container.service";
import {AuthService} from "../../../../../../common/modules/auth/services/auth.service";
import {LocationTypeService} from "../../services/locationtype.service";
import {TextUtility} from "../../../../../../common/modules/utility/text.utility";
import {LocationTypeUpsertComponent} from "../locationtype-upsert/locationtype-upsert.component";
import {IParams} from "../../../../../../../base/endpoints/iparams";
import {EventKey} from "../../../../../../../services/eventbus/event.key";
import {DialogService} from "../../../../../../common/modules/utility/components/popup/dialog.service";
@Component({
  selector: 'app-locationtype-select',
  templateUrl: './locationtype-select.component.html',
  styleUrls: ['./locationtype-select.component.scss']
})
export class LocationTypeSelectComponent extends BaseComponent {
  @Input() enableAdd: boolean = true

  @Input() searchEndpoint: string = DashEndpoint.LOCATIONTYPES.LOCATIONTYPE_ENDPOINT

  items: ILocationType[] = []
  @Input() selectedItem: ILocationType | null = null
  @Output() selected: EventEmitter<ILocationType | null> = new EventEmitter<ILocationType | null>()

  protected override componentName(): string {
    return "LocationTypeSelectComponent";
  }

  constructor(eventBus: EventBusService,
              router: Router,
              subContainer: SubscriptionContainerService,
              authService: AuthService,
              private locationTypeService: LocationTypeService,
              private popupService: DialogService<ILocationType>) {
    super(eventBus, router, subContainer)

    if (authService.isAdmin()) {
      this.searchEndpoint = DashEndpoint.LOCATIONTYPES.LOCATIONTYPE_ENDPOINT_ADMIN
    }
  }

  override ngOnInit(): void {
    this.loading = false
    this.search({
      page: 0, size: 10, q: null
    })
  }

  searchForEvent(event: any) {
    let query = TextUtility.removeSpecialCharacters(event.target?.value)
    if (query.length > 2) {
      this.search({
        page: 0, size: 10, q: query
      })
    }
  }

  onSelected(event: any) {
    if (this.items.find(c => c.label == event.label)) {
      this.selected.emit(this.selectedItem)
    } else {
      let popup = this.popupService.open(
        LocationTypeUpsertComponent, {id: null, title: event.title}
      )
      popup.componentInstance.onCompleted.subscribe((locationtype: ILocationType | null) => {
          this.selected.emit(locationtype)
        }
      )
    }
  }

  search(params: IParams) {
    this.loading = true
    let key = this.componentKey(EventKey.LOCATIONTYPES.SEARCH(params))
    this.locationTypeService.search(key, this.searchEndpoint, params)
    this.subscribe(key, null, (value, meta) => {
      this.items = meta.data.content
      this.loading = false
    })
  }
}
