import {Injectable} from '@angular/core';
import {BaseService} from "../../../../../base/services/base.service";
import {AuthService} from "../../../../common/modules/auth/services/auth.service";
import {NotifierService} from "../../../../../services/notifier/notifier.service";
import {HttpClient, HttpErrorResponse} from "@angular/common/http";
import {EventBusService} from "../../../../../services/eventbus/event-bus.service";
import {IParams} from "../../../../../base/endpoints/iparams";
import {DashEndpoint} from "../../../dash.endpoint";
import {IPage} from "../../../../../base/models/IPage";
import {IFeedback, IFeedbackActionReq, IFeedbackReq} from "../models/feedback.model";
import {IAuth} from "../../../../common/modules/auth/models/auth.model";

@Injectable({
  providedIn: 'root'
})
export class FeedbackService extends BaseService {

  constructor(
    authService: AuthService,
    notifier: NotifierService,
    private http: HttpClient,
    private eventBusService: EventBusService
  ) {
    super(authService, notifier)
  }

  search(key: string, endpoint: string, params: IParams) {
    let url = DashEndpoint.FEEDBACKS.SEARCH(endpoint, params)
    this.http.get<IPage<IFeedback>>(url)
      .subscribe((feedback: IPage<IFeedback>) => {
        this.eventBusService.publish(key, feedback)
      }, error => {
         this.handleError(error, (auth: IAuth) => {
          this.search(key, endpoint, params)
        }, (err: HttpErrorResponse) => {
          this.eventBusService.publish(this.getErrorKey(key), err)
        })
      })
  }

  fetchSingle(key: string, id: number) {
    this.http.get<IFeedback>(DashEndpoint.FEEDBACKS.GET(id))
      .subscribe((feedback: IFeedback) => {
        this.eventBusService.publish(key, feedback)
      }, (error: HttpErrorResponse) => {
         this.handleError(error, (auth: IAuth) => {
          this.fetchSingle(key, id)
        }, (err: HttpErrorResponse) => {
          this.eventBusService.publish(this.getErrorKey(key), err)
        })
      })
  }

  upsert(key: string, feedbackReq: IFeedbackReq, exId: number | null) {
    let endpoint = exId == null ? DashEndpoint.FEEDBACKS.CREATE() : DashEndpoint.FEEDBACKS.UPDATE(exId)
    let observable = exId == null ? this.http.post<number>(endpoint, feedbackReq)
      : this.http.patch(endpoint, feedbackReq)
    observable.subscribe(res => {
      this.eventBusService.publish(key, res)
    }, (err: HttpErrorResponse) => {
      this.handleError(err, (auth: IAuth) => {
        this.upsert(key, feedbackReq, exId)
      }, (err: HttpErrorResponse) => {
        this.eventBusService.publish(this.getErrorKey(key), err)
      })
    })
  }

  takeAction(key: string, feedbackId: number, actionReq: IFeedbackActionReq) {
    let endpoint = DashEndpoint.FEEDBACKS.TAKE_ACTION(feedbackId)
    let observable = this.http.patch(endpoint, actionReq)
    observable.subscribe(res => {
      this.eventBusService.publish(key, res)
    }, (err: HttpErrorResponse) => {
      this.handleError(err, (auth: IAuth) => {
        this.takeAction(key,feedbackId,actionReq)
      }, (err: HttpErrorResponse) => {
        this.eventBusService.publish(this.getErrorKey(key), err)
      })
    })
  }

}
