import {Injectable} from '@angular/core';
import {IParams} from "../../../../../base/endpoints/iparams";
import {IPage} from "../../../../../base/models/IPage";
import {ILabel} from "../models/ilabel";
import {WebAppEndpoint} from "../../../../webapp/web-app.endpoint";
import {AuthService} from "../../../../common/modules/auth/services/auth.service";
import {NotifierService} from "../../../../../services/notifier/notifier.service";
import {HttpClient, HttpErrorResponse} from "@angular/common/http";
import {FormBuilder} from "@angular/forms";
import {EventBusService} from "../../../../../services/eventbus/event-bus.service";
import {BaseService} from "../../../../../base/services/base.service";
import {BehaviorSubject} from "rxjs";
import {IAuth} from "../../../../common/modules/auth/models/auth.model";

@Injectable({
	providedIn: 'root'
})
export class LabelTreeService extends BaseService {
	expandedNodesSubject = new BehaviorSubject<number[]>([]);
	expandedNodes$ = this.expandedNodesSubject.asObservable();

	constructor(authService: AuthService,
				notifier: NotifierService,
				private http: HttpClient,
				private formBuilder: FormBuilder,
				private eventBusService: EventBusService) {
		super(authService, notifier);
	}

	getLabelTree(key: string, params: IParams) {
		this.http.get<IPage<ILabel>>(WebAppEndpoint.LABELS.SEARCH(WebAppEndpoint.LABELS.SEARCH_ENDPOINT_ADMIN, params))
			.subscribe((label: IPage<ILabel>) => {
				this.eventBusService.publish(
					key,
					label
				)
			}, error => {
				 this.handleError(error, (auth: IAuth) => {
          this.getLabelTree(key, params)
        }, (err: HttpErrorResponse) => {
          this.eventBusService.publish(this.getErrorKey(key), err)
        })
			})
	}

	setExpandedNodes(nodeIds: number[]) {
		this.expandedNodesSubject.next(nodeIds);
	}

}
