import {Component, Input, OnChanges, SimpleChanges, ViewChild} from '@angular/core';
import {
  ApexChart,
  ApexDataLabels,
  ApexNonAxisChartSeries,
  ApexPlotOptions,
  ApexResponsive,
  ChartComponent
} from "ng-apexcharts";

export type ChartOptions = {
  series: ApexNonAxisChartSeries;
  chart: ApexChart;
  responsive: ApexResponsive[];
  dataLabels: ApexDataLabels;
  plotOptions: ApexPlotOptions;
  labels: any;
};

@Component({
  selector: 'app-pi-chart',
  templateUrl: './pi-chart.component.html',
  styleUrls: ['./pi-chart.component.scss']
})
export class PiChartComponent implements OnChanges {
  @ViewChild("chart") chart: ChartComponent;
  public chartOptions: Partial<ChartOptions> = defaultOptions;

  @Input() data: IPiChartData = {
    items: [],
    labelFormatter: (val: string) => val
  }

  constructor() {
    this.chartOptions = defaultOptions
  }

  ngOnChanges(changes: SimpleChanges): void {
    if (this.data) {
      this.chartOptions = this.updateData(this.data)
    }
  }

  updateData(data: IPiChartData): ChartOptions {
    return {
      series: data.items.map(item => item.value),
      chart: {
        type: "donut"
      },
      labels: data.items.map(item => data.labelFormatter(item.label)),
      plotOptions: {
        pie: {
          donut: {
            labels: {
              show: true,
              name: {
                show: true,
                fontSize: '22px',
                fontFamily: 'Helvetica, Arial, sans-serif',
                fontWeight: 600,
                color: "#263238",
                offsetY: -10,
              },
              value: {
                show: true,
                fontSize: '16px',
                fontFamily: 'Helvetica, Arial, sans-serif',
                fontWeight: 400,
                color: "#969696",
                offsetY: 16,
                formatter: data.labelFormatter
              },
              total: {
                show: true,
                showAlways: true,
                fontFamily: 'Helvetica, Arial, sans-serif',
                fontSize: '22px',
                fontWeight: 600,
                label: 'Total',
                color: "#263238",
                formatter: function (w) {
                  return w
                }
              }
            }
          }
        }
      },
      dataLabels: {
        enabled: true,
        dropShadow: {
          enabled: true,
        }
      },
      responsive: [
        {
          breakpoint: 480,
          options: {
            // chart: {
            //   width: 200
            // },
            legend: {
              position: "bottom"
            }
          }
        }
      ]
    };
  }

}

export interface IPiChartData {
  items: IPiChartItem[],
  labelFormatter: (val: string) => string
}

export interface IPiChartItem {
  label: string
  value: number
}

const defaultOptions: Partial<ChartOptions> = {
  series: [44, 55, 13, 43, 22],
  chart: {
    type: "donut"
  },
  labels: ["Team A", "Team B", "Team C", "Team D", "Team E"],
  plotOptions: {
    pie: {
      donut: {
        labels: {
          show: true,
          name: {
            show: true,
            fontSize: '22px',
            fontFamily: 'Helvetica, Arial, sans-serif',
            fontWeight: 600,
            color: "#263238",
            offsetY: -10,
          },
          value: {
            show: true,
            fontSize: '16px',
            fontFamily: 'Helvetica, Arial, sans-serif',
            fontWeight: 400,
            color: "#969696",
            offsetY: 16,
            formatter: function (val) {
              return val
            }
          },
          total: {
            show: true,
            showAlways: true,
            fontFamily: 'Helvetica, Arial, sans-serif',
            fontSize: '22px',
            fontWeight: 600,
            label: 'Total',
            color: "#263238",
            formatter: function (w) {
              return w
            }
          }
        }
      }
    }
  },
  dataLabels: {
    enabled: true,
    dropShadow: {
      enabled: true,
    }
  },
  // responsive: [
  //   {
  //     breakpoint: 480,
  //     options: {
  //       chart: {
  //         width: 200
  //       },
  //       legend: {
  //         position: "bottom"
  //       }
  //     }
  //   }
  // ]
};

