import {DashEndpoint} from "../../../dash.endpoint";
import {BaseConfig, BaseConfigV2} from "../../../../../base/models/baseConfig";
import {Layout} from "../../../../common/modules/generic/breadcrumb/breadcrumb.model";

export let selfEndpoints = {
  endpoint: DashEndpoint.CRUDEXAMPLES.CRUDEXAMPLE_ENDPOINT,
  adminEndpoint: DashEndpoint.CRUDEXAMPLES.CRUDEXAMPLE_ENDPOINT_ADMIN,
}

export interface CrudExampleConfig extends BaseConfigV2 {
}

export let defaultCrudExampleConfig: CrudExampleConfig = {
  defaultLayout: Layout.GRID,
  upsertOnDialog: true,
  showUserFilter: false,
  showDeleteButton: true,
  endpoint: selfEndpoints.endpoint,
  delEndpoint: selfEndpoints.endpoint
}

export let selfRoutes = {
  list: "/dash/crudexamples",
  create: "/dash/crudexamples/create",
  update(id: number | null) {
    return id == null ? this.create : "/dash/crudexamples/" + id + '/update'
  }
}


