import {Component, EventEmitter, Output} from '@angular/core';
import {BaseComponent} from "../../../../base/components/base.component";
import {EventBusService} from "../../../../services/eventbus/event-bus.service";
import {Router} from "@angular/router";
import {SubscriptionContainerService} from "../../../../base/services/subscription-container.service";

@Component({
  selector: 'app-approving-filter',
  templateUrl: './approving-filter.component.html',
  styleUrls: ['./approving-filter.component.scss']
})
export class ApprovingFilterComponent extends BaseComponent {

  protected componentName(): string {
    return "ApprovingFilterComponent";
  }

  items = [{value: true, label: 'Approved'},
    {value: false, label: 'Unapproved'}]
  isApproved: boolean
  @Output() selected: EventEmitter<Boolean | null> = new EventEmitter<Boolean | null>()

  constructor(eventBus: EventBusService,
              router: Router,
              subContainer: SubscriptionContainerService) {
    super(eventBus, router, subContainer)
  }

  ngOnInit(): void {
    this.loading = false
  }

  onUserSelected(event: any) {
    this.selected.emit(this.isApproved)
  }
}
