import {Layout} from "../../../../../common/modules/generic/breadcrumb/breadcrumb.model";
import {DashEndpoint} from "../../../../dash.endpoint";
import {BaseConfig} from "../../../../../../base/models/baseConfig";

export interface LocationTypeConfig extends BaseConfig {
}

export let defaultLocationTypeConfig: LocationTypeConfig = {
  defaultLayout: Layout.GRID,
  upsertOnDialog: false,
  showUserFilter: false,
  showDeleteButton: true,
  searchEndpoint: DashEndpoint.LOCATIONTYPES.LOCATIONTYPE_ENDPOINT,
  delEndpoint: DashEndpoint.LOCATIONTYPES.LOCATIONTYPE_ENDPOINT
}


export let selfRoutes = {
  list: "/dash/location-types",
  getChildren(id: number) {
    return "/dash/location-types?parent_id=" + id
  },
  create: "/dash/location-types/create",
  update(id: number | null) {
    return id == null ? this.create : "/dash/location-types/" + id + '/update'
  }
}
